import { Component, Input, Output, EventEmitter } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatIconModule } from '@angular/material/icon';
import { MatButtonModule } from '@angular/material/button';
import { ICourse } from '../../interfaces/models/school.interface';
import { getImageOrFallback, createFallbackImageFromName } from '../../utils/image-fallback.util';

@Component({
  selector: 'app-course-card',
  standalone: true,
  imports: [CommonModule, MatIconModule, MatButtonModule],
  templateUrl: './course-card.component.html',
  styleUrls: ['./course-card.component.scss']
})
export class CourseCardComponent {
  @Input() product!: ICourse;
  @Input() showDiscount = true;
  @Input() showTeacher = true;
  @Output() productClick = new EventEmitter<ICourse>();
  @Output() favoriteClick = new EventEmitter<ICourse>();
  @Output() cartClick = new EventEmitter<ICourse>();

  isFavorite = false;
  Math = Math;

  get discountRate(): number {
    if (this.product.discountType === 'percent') {
      return this.product.discount;
    } else if (this.product.discountType === 'won' && this.product.price > 0) {
      return Math.round((this.product.discount / this.product.price) * 100);
    }
    return 0;
  }

  get finalPrice(): number {
    if (this.product.discountType === 'won') {
      return this.product.price - this.product.discount;
    } else if (this.product.discountType === 'percent') {
      return this.product.price - (this.product.price * this.product.discount / 100);
    }
    return this.product.price;
  }

  get imageUrl(): string {
    return getImageOrFallback(
      this.product.thumbnailMeta,
      this.product.name,
      undefined, // placeholder ëŒ€ì‹  fallback ìƒì„±
      300
    );
  }

  get averageRating(): number {
    if (!this.product.reviews || this.product.reviews.length === 0) {
      return 0;
    }
    const sum = this.product.reviews.reduce((acc, review) => acc + review.rating, 0);
    return Math.round((sum / this.product.reviews.length) * 10) / 10;
  }

  get reviewCount(): number {
    return this.product.reviews?.length || 0;
  }

  onProductClick(): void {
    this.productClick.emit(this.product);
  }

  onFavoriteClick(event: Event): void {
    event.stopPropagation();
    this.isFavorite = !this.isFavorite;
    this.favoriteClick.emit(this.product);
  }

  onCartClick(event: Event): void {
    event.stopPropagation();
    this.cartClick.emit(this.product);
  }

  onImageError(event: Event): void {
    const img = event.target as HTMLImageElement;
    // ì´ë¯¸ì§€ ë¡œë“œ ì‹¤íŒ¨ ì‹œ fallback ìƒì„±
    img.src = createFallbackImageFromName(this.product.name, 300);
  }
}
