  /**
   * í˜„ì¬ ëª¨ë“œì— ë”°ë¥¸ í”„ë¡œí•„/ëŒ€ì‹œë³´ë“œ ë§í¬ ë°˜í™˜
   */
  getProfileLink(): string {
    const mode = this.authModeService.getCurrentMode();
    switch (mode) {
      case 'TEACHER':
        return '/education/teacher/dashboard';
      case 'ADMIN':
        return '/education/admin/dashboard';
      case 'STUDENT':
      default:
        return '/education/student/profile';
    }
  }

  getSchoolImageUrl(school: ISchool): string {
    const logoMeta = school.logoImageMeta;
    if (logoMeta?.url && logoMeta.url.startsWith('/')) {
      const absoluteUrl = environment.baseSyncUrl + logoMeta.url;
      return getImageOrFallback({ ...logoMeta, url: absoluteUrl }, school.name, undefined, 200);
    }
    return getImageOrFallback(logoMeta, school.name, undefined, 200);
  }

  getDefaultSchoolImage(schoolName: string): string {
    const hash = schoolName.split('').reduce((acc, char) => char.charCodeAt(0) + ((acc << 5) - acc), 0);
    const hue = Math.abs(hash) % 360;
    const svg = `<svg width="200" height="200" xmlns="http://www.w3.org/2000/svg"><defs><linearGradient id="grad${hash}" x1="0%" y1="0%" x2="100%" y2="100%"><stop offset="0%" style="stop-color:hsl(${hue}, 70%, 60%);stop-opacity:1" /><stop offset="100%" style="stop-color:hsl(${hue + 30}, 70%, 40%);stop-opacity:1" /></linearGradient></defs><rect width="200" height="200" fill="url(#grad${hash})" /><text x="100" y="100" font-family="Arial, sans-serif" font-size="24" font-weight="bold" text-anchor="middle" dominant-baseline="middle" fill="white">${schoolName.substring(0, 2).toUpperCase()}</text></svg>`;
    return `data:image/svg+xml;base64,${btoa(unescape(encodeURIComponent(svg)))}`;
  }

  onImageError(event: any): void {
    const target = event.target;
    target.src = this.getDefaultSchoolImage(target.alt || 'School');
  }

  get popularSchools(): ISchool[] {
    return [...this.schools].sort((a, b) => (b.productCount || 0) - (a.productCount || 0)).slice(0, 6);
  }

  canShowSpeedDial(): boolean {
    const currentMode = this.authModeService.getCurrentMode();
    const isTeacherAuthenticated = this.teacherAuthService.isAuthenticated();
    return currentMode === 'TEACHER' && isTeacherAuthenticated;
  }

