import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl, ValidationErrors } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Subject, takeUntil } from 'rxjs';
import { StudentAuthService } from '../../services/student-auth.service';
import { TeacherAuthService } from '../../services/teacher-auth.service';
import { ThemeService } from '../../../../services/theme.service';

@Component({
  selector: 'app-student-register',
  standalone: false,
  templateUrl: './student-register.html',
  styleUrl: './student-register.scss'
})
export class StudentRegister implements OnInit, OnDestroy {
  registerForm!: FormGroup;
  isLoading = false;
  errorMessage = '';
  hidePassword = true;
  hideConfirmPassword = true;
  isDarkMode = false;
  isTeacher = false; // êµì‚¬ íšŒì›ê°€ì… ì—¬ë¶€

  private destroy$ = new Subject<void>();

  constructor(
    private fb: FormBuilder,
    private studentAuthService: StudentAuthService,
    private teacherAuthService: TeacherAuthService,
    private router: Router,
    private route: ActivatedRoute,
    private snackBar: MatSnackBar,
    private themeService: ThemeService
  ) {}

  ngOnInit(): void {
    // ì¿¼ë¦¬ íŒŒë¼ë¯¸í„°ë¡œ êµì‚¬/í•™ìƒ êµ¬ë¶„
    this.route.queryParams
      .pipe(takeUntil(this.destroy$))
      .subscribe(params => {
        this.isTeacher = params['type'] === 'teacher';
      });

    this.initForm();
    
    // ë‹¤í¬ëª¨ë“œ ìƒíƒœ êµ¬ë…
    this.themeService.currentTheme$
      .pipe(takeUntil(this.destroy$))
      .subscribe(theme => {
        this.isDarkMode = theme === 'dark';
      });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  initForm(): void {
    this.registerForm = this.fb.group({
      id: ['', [Validators.required, Validators.minLength(4)]],
      password: ['', [Validators.required, this.passwordValidator()]],
      confirmPassword: ['', [Validators.required]],
      name: ['', [Validators.required, Validators.minLength(2)]],
      email: ['', [Validators.required, Validators.email]],
      phone: ['', [Validators.required, Validators.pattern(/^01[0-9]-?[0-9]{3,4}-?[0-9]{4}$/)]]
    }, {
      validators: this.passwordMatchValidator
    });
  }

  /**
   * ë¹„ë°€ë²ˆí˜¸ ìœ íš¨ì„± ê²€ì‚¬
   * - ìµœì†Œ 6ì ì´ìƒ
   * - ì˜ë¬¸, ìˆ«ì, íŠ¹ìˆ˜ë¬¸ì ì¤‘ 2ê°€ì§€ ì´ìƒ ì¡°í•©
   */
  passwordValidator() {
    return (control: AbstractControl): ValidationErrors | null => {
      const value = control.value;
      if (!value) {
        return null;
      }

      // ìµœì†Œ 6ì
      if (value.length < 6) {
        return {
          passwordInvalid: true,
          message: 'ë¹„ë°€ë²ˆí˜¸ëŠ” ìµœì†Œ 6ì ì´ìƒì´ì–´ì•¼ í•©ë‹ˆë‹¤.'
        };
      }

      // ì˜ë¬¸, ìˆ«ì, íŠ¹ìˆ˜ë¬¸ì ì¤‘ 2ê°€ì§€ ì´ìƒ ì¡°í•© ì²´í¬
      const hasLetter = /[a-zA-Z]/.test(value);
      const hasNumber = /[0-9]/.test(value);
      const hasSpecial = /[!@#$%^&*(),.?":{}|<>]/.test(value);

      const combinationCount = [hasLetter, hasNumber, hasSpecial].filter(Boolean).length;

      if (combinationCount < 2) {
        return {
          passwordInvalid: true,
          message: 'ë¹„ë°€ë²ˆí˜¸ëŠ” ì˜ë¬¸, ìˆ«ì, íŠ¹ìˆ˜ë¬¸ì ì¤‘ 2ê°€ì§€ ì´ìƒì„ ì¡°í•©í•´ì•¼ í•©ë‹ˆë‹¤.'
        };
      }

      return null;
    };
  }

  /**
   * ë¹„ë°€ë²ˆí˜¸ í™•ì¸ ì¼ì¹˜ ê²€ì‚¬
   */
  passwordMatchValidator(group: AbstractControl): ValidationErrors | null {
    const password = group.get('password')?.value;
    const confirmPassword = group.get('confirmPassword')?.value;

    if (!password || !confirmPassword) {
      return null;
    }

    if (password !== confirmPassword) {
      group.get('confirmPassword')?.setErrors({
        passwordMismatch: true,
        message: 'ë¹„ë°€ë²ˆí˜¸ê°€ ì¼ì¹˜í•˜ì§€ ì•ŠìŠµë‹ˆë‹¤.'
      });
      return { passwordMismatch: true };
    } else {
      // ë¹„ë°€ë²ˆí˜¸ê°€ ì¼ì¹˜í•˜ë©´ confirmPasswordì˜ ì—ëŸ¬ë¥¼ ì œê±° (ë‹¤ë¥¸ ì—ëŸ¬ëŠ” ìœ ì§€)
      const confirmPasswordControl = group.get('confirmPassword');
      if (confirmPasswordControl?.hasError('passwordMismatch')) {
        confirmPasswordControl.setErrors(null);
      }
    }

    return null;
  }

  /**
   * í¼ ì œì¶œ
   */
  onSubmit(): void {
    if (this.registerForm.invalid) {
      this.registerForm.markAllAsTouched();
      return;
    }

    this.isLoading = true;
    this.errorMessage = '';

    const { id, password, name, email, phone } = this.registerForm.value;

    // êµì‚¬ íšŒì›ê°€ì…
    if (this.isTeacher) {
      this.teacherAuthService.register({ id, password, name, email, phone })
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (user: any) => {
            this.snackBar.open('êµì‚¬ íšŒì›ê°€ì…ì´ ì™„ë£Œë˜ì—ˆìŠµë‹ˆë‹¤!', 'í™•ì¸', {
              duration: 3000,
              horizontalPosition: 'center',
              verticalPosition: 'top'
            });
            
            // íšŒì›ê°€ì… ì„±ê³µ ì‹œ ìë™ ë¡œê·¸ì¸ ì²˜ë¦¬ë¨
            this.teacherAuthService.setUser(user);
            
            // êµìœ¡ í™ˆìœ¼ë¡œ ì´ë™
            setTimeout(() => {
              this.router.navigate(['/education/home']);
            }, 500);
          },
          error: (error: any) => {
            this.isLoading = false;
            this.handleRegistrationError(error);
          }
        });
    } 
    // ê³ ê° íšŒì›ê°€ì…
    else {
      this.studentAuthService.register({ id, password, name, email, phone })
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (user: any) => {
            this.snackBar.open('ê³ ê° íšŒì›ê°€ì…ì´ ì™„ë£Œë˜ì—ˆìŠµë‹ˆë‹¤!', 'í™•ì¸', {
              duration: 3000,
              horizontalPosition: 'center',
              verticalPosition: 'top'
            });
            
            // íšŒì›ê°€ì… ì„±ê³µ ì‹œ ìë™ ë¡œê·¸ì¸ ì²˜ë¦¬ë¨
            this.studentAuthService.setUser(user);
            
            // êµìœ¡ í™ˆìœ¼ë¡œ ì´ë™
            setTimeout(() => {
              this.router.navigate(['/education/home']);
            }, 500);
          },
          error: (error: any) => {
            this.isLoading = false;
            this.handleRegistrationError(error);
          }
        });
    }
  }

  /**
   * íšŒì›ê°€ì… ì—ëŸ¬ ì²˜ë¦¬
   */
  private handleRegistrationError(error: any): void {
    // ì—ëŸ¬ ë©”ì‹œì§€ ì²˜ë¦¬
    if (error.error?.message) {
      // messageê°€ ê°ì²´ì¸ ê²½ìš° (NestJS ê¸°ë³¸ ì—ëŸ¬ ì‘ë‹µ ë“±)
      if (typeof error.error.message === 'object' && error.error.message.message) {
        this.errorMessage = error.error.message.message;
      } else {
        this.errorMessage = error.error.message;
      }
    } else if (error.status === 404) {
      this.errorMessage = 'íšŒì›ê°€ì… APIë¥¼ ì°¾ì„ ìˆ˜ ì—†ìŠµë‹ˆë‹¤. (404)';
    } else if (error.status === 409) {
      this.errorMessage = 'ì´ë¯¸ ì‚¬ìš© ì¤‘ì¸ ì•„ì´ë””ì…ë‹ˆë‹¤.';
    } else {
      this.errorMessage = 'íšŒì›ê°€ì…ì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤. ë‹¤ì‹œ ì‹œë„í•´ì£¼ì„¸ìš”.';
    }

    this.snackBar.open(this.errorMessage, 'ë‹«ê¸°', {
      duration: 5000,
      horizontalPosition: 'center',
      verticalPosition: 'top',
      panelClass: ['error-snackbar']
    });
  }

  /**
   * í•„ë“œ ìœ íš¨ì„± ê²€ì‚¬
   */
  isFieldInvalid(fieldName: string): boolean {
    const field = this.registerForm.get(fieldName);
    return !!(field && field.invalid && (field.dirty || field.touched));
  }

  /**
   * í•„ë“œ ì—ëŸ¬ ë©”ì‹œì§€ ë°˜í™˜
   */
  getFieldError(fieldName: string): string {
    const field = this.registerForm.get(fieldName);
    if (!field || !field.errors) {
      return '';
    }

    if (field.errors['required']) {
      return this.getRequiredMessage(fieldName);
    }
    if (field.errors['minlength']) {
      const minLength = field.errors['minlength'].requiredLength;
      return `ìµœì†Œ ${minLength}ì ì´ìƒ ì…ë ¥í•´ì£¼ì„¸ìš”.`;
    }
    if (field.errors['email']) {
      return 'ì˜¬ë°”ë¥¸ ì´ë©”ì¼ ì£¼ì†Œë¥¼ ì…ë ¥í•´ì£¼ì„¸ìš”.';
    }
    if (field.errors['pattern']) {
      if (fieldName === 'phone') {
        return 'ì˜¬ë°”ë¥¸ íœ´ëŒ€í° ë²ˆí˜¸ë¥¼ ì…ë ¥í•´ì£¼ì„¸ìš”. (ì˜ˆ: 010-1234-5678)';
      }
    }
    if (field.errors['passwordInvalid']) {
      return field.errors['message'] || 'ë¹„ë°€ë²ˆí˜¸ í˜•ì‹ì´ ì˜¬ë°”ë¥´ì§€ ì•ŠìŠµë‹ˆë‹¤.';
    }
    if (field.errors['passwordMismatch']) {
      return field.errors['message'] || 'ë¹„ë°€ë²ˆí˜¸ê°€ ì¼ì¹˜í•˜ì§€ ì•ŠìŠµë‹ˆë‹¤.';
    }

    return 'ì…ë ¥ê°’ì´ ì˜¬ë°”ë¥´ì§€ ì•ŠìŠµë‹ˆë‹¤.';
  }

  private getRequiredMessage(fieldName: string): string {
    const messages: { [key: string]: string } = {
      id: 'ì•„ì´ë””ë¥¼ ì…ë ¥í•´ì£¼ì„¸ìš”.',
      password: 'ë¹„ë°€ë²ˆí˜¸ë¥¼ ì…ë ¥í•´ì£¼ì„¸ìš”.',
      confirmPassword: 'ë¹„ë°€ë²ˆí˜¸ í™•ì¸ì„ ì…ë ¥í•´ì£¼ì„¸ìš”.',
      name: 'ì´ë¦„ì„ ì…ë ¥í•´ì£¼ì„¸ìš”.',
      email: 'ì´ë©”ì¼ì„ ì…ë ¥í•´ì£¼ì„¸ìš”.',
      phone: 'íœ´ëŒ€í° ë²ˆí˜¸ë¥¼ ì…ë ¥í•´ì£¼ì„¸ìš”.'
    };
    return messages[fieldName] || 'í•„ìˆ˜ ì…ë ¥ í•­ëª©ì…ë‹ˆë‹¤.';
  }

  /**
   * ë¡œê·¸ì¸ í˜ì´ì§€ë¡œ ì´ë™
   */
  onLoginClick(): void {
    if (this.isTeacher) {
      this.router.navigate(['/store/teacher/login']);
    } else {
      this.router.navigate(['/store/login']);
    }
  }
}


