import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { MissionApiService, IMissionSurvey } from '../../services/mission-api.service';
import { Subject, takeUntil } from 'rxjs';

@Component({
  selector: 'app-survey-response-list',
  templateUrl: './survey-response-list.component.html',
  styleUrls: ['./survey-response-list.component.scss'],
  standalone: false
})
export class SurveyResponseListComponent implements OnInit {
  private destroy$ = new Subject<void>();
  
  survey: IMissionSurvey;
  responses: any[] = [];
  loading = true;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: { survey: IMissionSurvey },
    private dialogRef: MatDialogRef<SurveyResponseListComponent>,
    private missionApiService: MissionApiService
  ) {
    this.survey = data.survey;
  }

  ngOnInit(): void {
    this.loadResponses();
  }

  loadResponses(): void {
    this.loading = true;
    this.missionApiService.getSurveyResponses$(this.survey.id)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          this.responses = res;
          this.loading = false;
        },
        error: (err) => {
          console.error('Failed to load responses', err);
          this.loading = false;
        }
      });
  }

  getAnswer(response: any, questionId: string): string {
    const answer = response.answers?.find((a: any) => a.questionId === questionId);
    if (!answer) return '-';
    if (Array.isArray(answer.value)) return answer.value.join(', ');
    return answer.value;
  }

  exportToExcel(): void {
    // TODO: Implement excel export
    alert('ì—‘ì…€ ë‹¤ìš´ë¡œë“œ ê¸°ëŠ¥ì€ ì¤€ë¹„ ì¤‘ì…ë‹ˆë‹¤.');
  }

  close(): void {
    this.dialogRef.close();
  }
}
