import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-widget-image',
  templateUrl: './widget-image.component.html',
  styleUrls: ['./widget-image.component.scss'],
  standalone: false
})
export class WidgetImageComponent implements OnInit {
  @Input() properties!: any;

  style: any = {};

  ngOnInit(): void {
    this.style = { ...this.getStyle() };
  }

  /**
   * properties.style ê°ì²´ë¥¼ ì‹¤ì œ CSS ìŠ¤íƒ€ì¼ë¡œ ë³€í™˜
   * enabledê°€ trueì¸ í•­ëª©ë§Œ ì ìš©
   */
  getStyle() {
    const styles: { [key: string]: string | number } = {};

    if (!this.properties?.style) return styles;

    for (const key in this.properties.style) {
      const item = this.properties.style[key];
      if (!item.enabled) continue; // í™œì„±í™”ëœ í•­ëª©ë§Œ ì¶”ê°€

      styles[item.cssProperty] = item.unit
        ? `${item.value}${item.unit}`
        : item.value;
    }

    return styles;
  }

  /**
   * ì´ë¯¸ì§€ í´ë¦­ ì‹œ action ì²˜ë¦¬ (ë§í¬, ëª¨ë‹¬ ë“±)
   */
  onClick() {
    if (!this.properties?.action) return;
    
    if (this.properties.action.action === 'link' && this.properties.action.value) {
      window.open(this.properties.action.value, '_blank');
    }
  }
}
