  private checkAndAdjustAllIframes(): void {
    // í˜„ì¬ í˜ì´ì§€ì˜ ëª¨ë“  iframe ì°¾ì•„ì„œ ì¦‰ì‹œ ì²˜ë¦¬
    const iframes = document.querySelectorAll('iframe[id^="embed-"]') as NodeListOf<HTMLIFrameElement>;
    iframes.forEach(iframe => {
      this.performImmediateHeightAdjustment(iframe);
    });
  }

  private observeIframeChanges(iframe: HTMLIFrameElement): void {
    try {
      if (iframe.contentDocument) {
        const observer = new MutationObserver(() => {
          this.performHeightAdjustment(iframe);
        });
        
        observer.observe(iframe.contentDocument.body, {
          childList: true,
          subtree: true,
          attributes: true
        });
        
        // ì§€ì—°ëœ ì •ë¦¬ë¥¼ ìœ„í•´ ì €ì¥
        setTimeout(() => {
          observer.disconnect();
        }, 5000); // 5ì´ˆ í›„ ê´€ì°° ì¤‘ë‹¨
      }
    } catch (e) {
      // í¬ë¡œìŠ¤ ì˜¤ë¦¬ì§„ ì—ëŸ¬ ë¬´ì‹œ
    }
  }

