  // ì¸ë„¤ì¼ ë„¤ë¹„ê²Œì´ì…˜
  get visibleThumbnails(): { image: string; index: number }[] {
    const start = this.currentThumbnailPage * this.thumbnailsPerPage;
    const end = start + this.thumbnailsPerPage;
    return this.images
      .slice(start, end)
      .map((image, idx) => ({ image, index: start + idx }));
  }

  get canGoPrevThumbnails(): boolean {
    return this.currentThumbnailPage > 0;
  }

  get canGoNextThumbnails(): boolean {
    return (this.currentThumbnailPage + 1) * this.thumbnailsPerPage < this.images.length;
  }

  prevThumbnails(): void {
    if (this.canGoPrevThumbnails) {
      this.currentThumbnailPage--;
    }
  }

  nextThumbnails(): void {
    if (this.canGoNextThumbnails) {
      this.currentThumbnailPage++;
    }
  }

  increaseQuantity(): void {
    this.quantity++;
  }

  decreaseQuantity(): void {
    if (this.quantity > 1) {
      this.quantity--;
    }
  }

  setActiveTab(tab: 'detail' | 'mission' | 'review' | 'qna' | 'shipping'): void {
    this.activeTab = tab;
    
    // SyncAna íŒ¨í„´: íƒ­ ì „í™˜ ì‹œ ë™ì  ì»´í¬ë„ŒíŠ¸ ë Œë”ë§
    if (tab === 'detail' && this.detailContainer) {
      this.renderDetailContent();
    }
  }

  /**
   * ìƒì„¸ì •ë³´ íƒ­ ì½˜í…ì¸  ë Œë”ë§ (SyncAna íŒ¨í„´)
   * detailì„ JSON íŒŒì‹±í•˜ì—¬ sections â†’ rows â†’ columns â†’ widget êµ¬ì¡°ë¡œ ë™ì  ì»´í¬ë„ŒíŠ¸ ìƒì„±
   */
  private renderDetailContent(): void {
    if (!this.product?.detail || !this.detailContainer) return;
    
    this.detailContainer.clear();
    
    try {
      const wysiwyg = JSON.parse(this.product.detail);
      
      if (!wysiwyg.sections || !Array.isArray(wysiwyg.sections)) {
        return;
      }
      
      // sections ìˆœíšŒ
      wysiwyg.sections.forEach((section: any) => {
        if (!section.rows || !Array.isArray(section.rows)) return;
        
        // rows ìˆœíšŒ
        section.rows.forEach((row: any) => {
          if (!row.columns || !Array.isArray(row.columns)) return;
          
          // columns ìˆœíšŒ
          row.columns.forEach((column: any) => {
            if (!column.widget) return;
            
            const widget = column.widget;
            const widgetType = widget.widgetType;
            
            // widgetTypeì— ë§ëŠ” ì»´í¬ë„ŒíŠ¸ ì°¾ê¸°
            const componentType = widgetItemMap[widgetType];
            
            if (componentType) {
              // ë™ì  ì»´í¬ë„ŒíŠ¸ ìƒì„±
              const componentRef = this.detailContainer.createComponent(componentType);
              
              // ì»´í¬ë„ŒíŠ¸ì— ë°ì´í„° ì „ë‹¬
              if ('widget' in componentRef.instance) {
                (componentRef.instance as any).widget = widget;
              }
              
            } else {
            }
          });
        });
      });
      
    } catch (e) {
    }
  }

