  /**
   * ì´ë¯¸ì§€ URLì—ì„œ íŒŒì¼ëª… ì¶”ì¶œ
   */
  getImageFileName(imageUrl: string): string {
    if (!imageUrl) {
      return 'ì´ë¯¸ì§€ ì—†ìŒ';
    }
    try {
      const urlParts = imageUrl.split('/');
      const fileName = urlParts[urlParts.length - 1];
      return fileName.split('?')[0] || 'ì´ë¯¸ì§€';
    } catch (error) {
      return 'ì´ë¯¸ì§€';
    }
  }

  /**
   * Check if current user is enrolled in this course
   */
  private checkEnrollmentStatus(): void {
    if (!this.productId) {
      console.log('âŒ productIdê°€ ì—†ìŠµë‹ˆë‹¤');
      return;
    }

    this.checkingEnrollment = true;
    console.log(`ğŸ” ì…êµ ìƒíƒœ í™•ì¸ ì¤‘: courseId=${this.productId}`);
    
    // checkout í˜ì´ì§€ì™€ ë™ì¼í•˜ê²Œ getMyEnrollments$() ì‚¬ìš©
    this.enrollmentApiService
      .getMyEnrollments$()
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (enrollments) => {
          console.log('âœ… ìˆ˜ê°•ì‹ ì²­ ëª©ë¡ ì¡°íšŒ ì™„ë£Œ:', enrollments);
          
          // í˜„ì¬ courseIdê°€ ìˆ˜ê°•ì‹ ì²­ ëª©ë¡ì— ìˆëŠ”ì§€ í™•ì¸
          const isEnrolledForCourse = enrollments?.some((enrollment: any) => 
            enrollment.courseId === this.productId || enrollment.course?.id === this.productId
          ) ?? false;
          
          this.isEnrolled = isEnrolledForCourse;
          console.log(`ğŸ¯ ê³¼ëª© ${this.productId} ìˆ˜ê°•ì‹ ì²­ ìƒíƒœ: ${this.isEnrolled}`);
          this.checkingEnrollment = false;
          this.cdr.markForCheck();
        },
        error: (error) => {
          console.error('âŒ ìˆ˜ê°•ì‹ ì²­ ìƒíƒœ í™•ì¸ ì‹¤íŒ¨:', error);
          this.isEnrolled = false;
          this.checkingEnrollment = false;
          this.cdr.markForCheck();
        }
      });
  }

  /**
   * Open introduction survey dialog for Dream Design course
   */
  openIntroductionSurvey(): void {
    if (!this.productId) {
      this.notification.show('ê³¼ëª© ì •ë³´ë¥¼ ë¶ˆëŸ¬ì˜¬ ìˆ˜ ì—†ìŠµë‹ˆë‹¤.');
      return;
    }

    // ì…êµí•˜ì§€ ì•Šì€ ê²½ìš° ê²½ê³ 
    if (!this.isEnrolled) {
      this.notification.show('ì…êµí•œ í•™ìƒë§Œ ì„¤ë¬¸ì— ì‘ë‹µí•  ìˆ˜ ìˆìŠµë‹ˆë‹¤.');
      return;
    }

    const dialogRef = this.dialog.open(IntroductionSurveyComponent, {
      width: '600px',
      maxWidth: '90vw',
      maxHeight: '90vh',
      disableClose: false,
      panelClass: ['introduction-survey-dialog'],
      data: {
        courseId: this.productId
      }
    });

    dialogRef.afterClosed()
      .pipe(takeUntil(this.destroy$))
      .subscribe(result => {
        if (result && result.status === 'COMPLETED') {
          this.debugLogger.success('ì„¤ë¬¸ì œì¶œ', 'âœ… ì„¤ë¬¸ ì œì¶œ ì™„ë£Œ');
          this.notification.show('ì„¤ë¬¸ ì œì¶œì„ ì™„ë£Œí–ˆìŠµë‹ˆë‹¤! ë¯¸ì…˜ í˜ì´ì§€ë¡œ ì´ë™í•©ë‹ˆë‹¤.');
          // ì œì¶œ ìƒíƒœ ì—…ë°ì´íŠ¸
          this.introductionSurveyStatus = {
            submitted: true,
            submittedAt: new Date().toLocaleString('ko-KR')
          };
          this.cdr.markForCheck();
          
          // 3ì´ˆ í›„ ë¯¸ì…˜ í˜ì´ì§€ë¡œ ì´ë™
          setTimeout(() => {
            this.router.navigate(['/education/course', this.productId, 'mission']);
          }, 1500);
        }
      });
  }
  
  /**
   * ì„¤ë¬¸ ì œì¶œ ì—¬ë¶€ í™•ì¸
   */
  private checkIntroductionSurveyStatus(): void {
    if (!this.productId) return;
    
    // ë¡œê·¸ì¸ ìƒíƒœ í™•ì¸
    const isLoggedIn = this.studentAuthService.isAuthenticated();
    const currentUser = this.studentAuthService.getCurrentUser();
    
    if (!isLoggedIn) {
      // ë¡œê·¸ì¸í•˜ì§€ ì•Šì€ ì‚¬ìš©ìëŠ” ì„¤ë¬¸ ì™„ë£Œ ìƒíƒœë¥¼ í‘œì‹œí•˜ì§€ ì•ŠìŒ
      this.introductionSurveyStatus = { submitted: false };
      this.cdr.markForCheck();
      return;
    }
    
    this.introductionSurveyService
      .checkSurveyStatus$(this.productId, currentUser?.id)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (response: any) => {
          if (response && response.status === 'COMPLETED' && response.submittedAt) {
            this.introductionSurveyStatus = {
              submitted: true,
              submittedAt: new Date(response.submittedAt).toLocaleString('ko-KR')
            };
          } else {
            this.introductionSurveyStatus = { submitted: false };
          }
          this.cdr.markForCheck();
        },
        error: () => {
          this.introductionSurveyStatus = { submitted: false };
        }
      });
  }
