import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable, map, catchError } from 'rxjs';
import { filter } from 'rxjs/operators';
import { JwtHelperService } from '@auth0/angular-jwt';
import { Router } from '@angular/router';
import { IUser, LoginDto, AuthResponse } from '../interfaces/auth.interface';
import { environment } from '../config/environment';

export abstract class BaseAuthService<T extends IUser> {
  protected user$ = new BehaviorSubject<T | null>(null);
  protected initialized = false;
  protected abstract storageTokenKey: string;
  protected abstract loginEndpoint: string;
  protected abstract loginRedirectPath: string;
  
  // ì‚¬ìš©ì ì •ë³´ ì €ì¥ í‚¤ ì¶”ê°€
  protected get storageUserKey(): string {
    return `${this.storageTokenKey}_user`;
  }

  constructor(
    protected http: HttpClient,
    protected jwtHelper: JwtHelperService,
    protected router: Router
  ) {
    // initUser()ëŠ” ìì‹ í´ë˜ìŠ¤ ìƒì„±ìì—ì„œ í˜¸ì¶œë˜ë„ë¡ ì œê±°
  }

  getUser$(): Observable<T> {
    return this.user$.asObservable().pipe(
      filter((user): user is T => user !== null)
    );
  }

  getNullableUser$(): Observable<T | null> {
    return this.user$.asObservable();
  }

  getCurrentUser(): T | null {
    return this.user$.getValue();
  }

  setUser(user: T): void {
    this.user$.next(user);
  }

  initUser(): void {
    // ì´ë¯¸ ì´ˆê¸°í™”ë˜ì—ˆìœ¼ë©´ skip
    if (this.initialized) return;
    this.initialized = true;

    const accessToken = localStorage.getItem(this.storageTokenKey);
    const userJson = localStorage.getItem(this.storageUserKey);
    
    console.log('[BaseAuthService] initUser() - storageKey:', this.storageTokenKey);
    console.log('[BaseAuthService] initUser() - token:', accessToken);
    console.log('[BaseAuthService] initUser() - userJson:', userJson);

    // JWT í† í° ìœ íš¨ì„± ê²€ì‚¬
    if (!this.isValidToken(accessToken)) {
      console.log('[BaseAuthService] initUser() - í† í°ì´ ìœ íš¨í•˜ì§€ ì•ŠìŒ');
      if (accessToken) localStorage.removeItem(this.storageTokenKey);
      if (userJson) localStorage.removeItem(this.storageUserKey);
      return;
    }

    // í† í°ì´ ë§Œë£Œëœ ê²½ìš°
    if (this.jwtHelper.isTokenExpired(accessToken)) {
      console.log('[BaseAuthService] initUser() - í† í°ì´ ë§Œë£Œë¨');
      localStorage.removeItem(this.storageTokenKey);
      localStorage.removeItem(this.storageUserKey);
      return;
    }

    // ì €ì¥ëœ ì‚¬ìš©ì ì •ë³´ê°€ ìˆìœ¼ë©´ ì‚¬ìš©
    if (userJson) {
      try {
        const user = JSON.parse(userJson) as T;
        console.log('[BaseAuthService] initUser() - localStorageì—ì„œ ì‚¬ìš©ì ì •ë³´ ë¡œë“œ:', user);
        this.user$.next(user);
        console.log('[BaseAuthService] initUser() - user$ ì—…ë°ì´íŠ¸ ì™„ë£Œ');
        return;
      } catch (error) {
        console.warn('[BaseAuthService] initUser() - ì‚¬ìš©ì ì •ë³´ íŒŒì‹± ì‹¤íŒ¨:', error);
      }
    }

    // ì €ì¥ëœ ì •ë³´ê°€ ì—†ìœ¼ë©´ JWTì—ì„œ ë””ì½”ë”© ì‹œë„
    try {
      const decodedToken = this.jwtHelper.decodeToken(accessToken);
      console.log('[BaseAuthService] initUser() - ë””ì½”ë”©ëœ í† í°:', decodedToken);
      
      // JWT í† í°ì˜ payloadì—ì„œ ì‚¬ìš©ì ì •ë³´ ì¶”ì¶œ
      const user = (decodedToken.payload || decodedToken) as T;
      console.log('[BaseAuthService] initUser() - JWTì—ì„œ ì¶”ì¶œí•œ ì‚¬ìš©ì ì •ë³´:', user);
      
      this.user$.next(user);
      // localStorageì— ì €ì¥
      localStorage.setItem(this.storageUserKey, JSON.stringify(user));
      console.log('[BaseAuthService] initUser() - user$ ì—…ë°ì´íŠ¸ ë° localStorage ì €ì¥ ì™„ë£Œ');
    } catch (error) {
      console.warn('[BaseAuthService] initUser() - í† í° ë””ì½”ë”© ì‹¤íŒ¨:', error);
      localStorage.removeItem(this.storageTokenKey);
      localStorage.removeItem(this.storageUserKey);
    }
  }

  login(loginDto: LoginDto): Observable<T> {
    return this.http.post<any>(this.loginEndpoint, loginDto).pipe(
      map((response: any) => {
        console.log('[BaseAuthService] ğŸ” RAW ì‘ë‹µ:', JSON.stringify(response, null, 2));
        
        let user: T | null = null;
        let accessToken: string = '';
        
        // íŒ¨í„´ 1: { isSuccess, status, data: { accessToken } } - JWT í† í°ì— user ì •ë³´ í¬í•¨
        if (response.data?.accessToken && !response.data.user) {
          accessToken = response.data.accessToken;
          console.log('[BaseAuthService] âœ… íŒ¨í„´ 1: JWT í† í° ë°©ì‹ (user ì •ë³´ëŠ” í† í°ì— í¬í•¨)');
          
          // JWT í† í° ë””ì½”ë”©í•˜ì—¬ user ì •ë³´ ì¶”ì¶œ
          try {
            const decodedToken = this.jwtHelper.decodeToken(accessToken);
            
            if (decodedToken && decodedToken.payload) {
              user = decodedToken.payload as T;
            } else {
              throw new Error('JWT payload not found');
            }
          } catch (error) {
            throw new Error('Failed to decode JWT token');
          }
        }
        // íŒ¨í„´ 2: { user: {...}, accessToken: '...' }
        else if (response.user && response.accessToken) {
          user = response.user;
          accessToken = response.accessToken;
        }
        // íŒ¨í„´ 3: { data: { user: {...}, accessToken: '...' } }
        else if (response.data?.user && response.data?.accessToken) {
          user = response.data.user;
          accessToken = response.data.accessToken;
        }
        // íŒ¨í„´ 4: { accessToken: '...', ...user_fields }
        else if (response.accessToken) {
          accessToken = response.accessToken;
          const { accessToken: _, ...userData } = response;
          user = userData as T;
        }
        
        if (!user) {
          throw new Error('Invalid response structure: user not found');
        }
        
        if (!accessToken) {
          throw new Error('Invalid response structure: accessToken not found');
        }
        
        // í† í° ì €ì¥
        this.setToken(accessToken);
        
        // User ê°ì²´ ë§¤í•‘ ë° ì €ì¥
        const mappedUser = this.mapUser(user);
        this.user$.next(mappedUser);
        
        // localStorageì— ì‚¬ìš©ì ì •ë³´ ì €ì¥
        localStorage.setItem(this.storageUserKey, JSON.stringify(mappedUser));
        
        return mappedUser;
      }),
      catchError(error => {
        throw error;
      })
    );
  }

  logout(): void {
    this.clearSession();
    this.router.navigate([this.loginRedirectPath]);
  }

  /**
   * ì„¸ì…˜ ì •ë³´ë§Œ ì‚­ì œ (ë¦¬ë‹¤ì´ë ‰íŠ¸ ì—†ìŒ)
   */
  clearSession(): void {
    console.log('[BaseAuthService] ì„¸ì…˜ ì •ë¦¬ ì‹œì‘...');
    
    // í† í°ê³¼ ì‚¬ìš©ì ì •ë³´ ì‚­ì œ
    localStorage.removeItem(this.storageTokenKey);
    localStorage.removeItem(this.storageUserKey);
    this.user$.next(null);
    
    // education ëª¨ë“ˆì˜ ëª¨ë“  ë¡œì»¬ìŠ¤í† ë¦¬ì§€ ë°ì´í„° ì •ë¦¬
    const keysToRemove = [
      'introduction_survey_submitted',
      'introduction_survey_submittedAt',
      'weekly_surveys_completed',
      'cart_items',
      'favorites',
      'visited_courses',
      'user_preferences'
    ];
    
    keysToRemove.forEach(key => {
      if (localStorage.getItem(key)) {
        console.log(`[BaseAuthService] localStorage ì œê±°: ${key}`);
        localStorage.removeItem(key);
      }
    });
    
    console.log('[BaseAuthService] ì„¸ì…˜ ì •ë¦¬ ì™„ë£Œ');
  }

  isAuthenticated(): boolean {
    const token = localStorage.getItem(this.storageTokenKey);
    return this.isValidToken(token) && !this.jwtHelper.isTokenExpired(token);
  }

  protected setToken(token: string): void {
    localStorage.setItem(this.storageTokenKey, token);
  }

  getToken(): string | null {
    return localStorage.getItem(this.storageTokenKey);
  }

  private isValidToken(token: string | null): token is string {
    if (!token) return false;

    const lowerToken = token.toLowerCase().trim();
    if (['undefined', 'null', 'false', ''].includes(lowerToken)) return false;

    // JWT í˜•ì‹ ì²´í¬ (3ê°œ ë¶€ë¶„ìœ¼ë¡œ êµ¬ì„±ëœ ì ìœ¼ë¡œ êµ¬ë¶„ëœ ë¬¸ìì—´)
    return /^[A-Za-z0-9_-]+\.[A-Za-z0-9_-]+\.[A-Za-z0-9_-]+$/.test(token);
  }

  protected mapUser(user: T): T {
    console.log('[BaseAuthService] mapUser() - ì…ë ¥ user:', user);
    
    if (!user) {
      console.warn('[BaseAuthService] mapUser() - userê°€ undefined/nullì…ë‹ˆë‹¤!');
      throw new Error('User object is undefined or null');
    }
    
    return {
      ...user,
      createdAt: user.createdAt ? new Date(user.createdAt) : new Date(),
      updatedAt: user.updatedAt ? new Date(user.updatedAt) : undefined,
    } as T;
  }
}