import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { IMissionSurvey, ISurveyQuestion } from '../../services/mission-api.service';
import { FormBuilder, FormGroup, FormArray, Validators } from '@angular/forms';
import { v4 as uuidv4 } from 'uuid';

@Component({
  selector: 'app-survey-editor-dialog',
  templateUrl: './survey-editor-dialog.component.html',
  styleUrls: ['./survey-editor-dialog.component.scss'],
  standalone: false
})
export class SurveyEditorDialogComponent implements OnInit {
  surveyForm: FormGroup;
  questionTypes = [
    { value: 'TEXT', label: 'ë‹¨ë‹µí˜•', icon: 'short_text' },
    { value: 'TEXTAREA', label: 'ì¥ë¬¸í˜•', icon: 'notes' },
    { value: 'SINGLE_CHOICE', label: 'ê°ê´€ì‹ (ë‹¨ì¼)', icon: 'radio_button_checked' },
    { value: 'MULTIPLE_CHOICE', label: 'ê°ê´€ì‹ (ë‹¤ì¤‘)', icon: 'check_box' },
    { value: 'RATING', label: 'í‰ì ', icon: 'star' },
    { value: 'DATE', label: 'ë‚ ì§œ', icon: 'event' }
  ];

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: { missionId: string, survey?: IMissionSurvey },
    private dialogRef: MatDialogRef<SurveyEditorDialogComponent>,
    private fb: FormBuilder
  ) {
    this.surveyForm = this.fb.group({
      title: [data.survey?.title || 'ìƒˆë¡œìš´ ì…ë ¥í¼', [Validators.required]],
      description: [data.survey?.description || ''],
      isAnonymous: [data.survey?.isAnonymous || false],
      isRequired: [data.survey?.isRequired || true],
      questions: this.fb.array([])
    });
  }

  ngOnInit(): void {
    if (this.data.survey?.questions) {
      this.data.survey.questions.forEach(q => this.addQuestion(q));
    } else {
      this.addQuestion();
    }
  }

  get questions(): FormArray {
    return this.surveyForm.get('questions') as FormArray;
  }

  addQuestion(q?: ISurveyQuestion): void {
    const questionGroup = this.fb.group({
      id: [q?.id || uuidv4()],
      type: [q?.type || 'TEXT', Validators.required],
      question: [q?.question || '', Validators.required],
      required: [q?.required ?? true],
      options: [q?.options || []],
      order: [q?.order || this.questions.length]
    });
    this.questions.push(questionGroup);
  }

  removeQuestion(index: number): void {
    this.questions.removeAt(index);
  }

  addOption(questionIndex: number): void {
    const question = this.questions.at(questionIndex);
    const options = question.get('options')?.value || [];
    question.get('options')?.setValue([...options, `ì˜µì…˜ ${options.length + 1}`]);
  }

  removeOption(questionIndex: number, optionIndex: number): void {
    const question = this.questions.at(questionIndex);
    const options = [...(question.get('options')?.value || [])];
    options.splice(optionIndex, 1);
    question.get('options')?.setValue(options);
  }

  trackByFn(index: number, item: any): any {
    return index;
  }

  save(): void {
    if (this.surveyForm.valid) {
      this.dialogRef.close(this.surveyForm.value);
    }
  }

  close(): void {
    this.dialogRef.close();
  }
}
