import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { JwtHelperService } from '@auth0/angular-jwt';
import { Router } from '@angular/router';
import { Observable, map } from 'rxjs';
import { BaseAuthService } from './base-auth.service';
import { ITeacher, AuthResponse } from '../interfaces/auth.interface';
import { environment } from '../config/environment';

export interface UpdateTeacherDto {
  name: string;
  email: string;
  phone: string;
}

export interface TeacherSignupDto {
  id: string;
  password: string;
  name: string;
  email: string;
  phone: string;
  businessName?: string;
  businessNumber?: string;
}

@Injectable({
  providedIn: 'root'
})
export class TeacherAuthService extends BaseAuthService<ITeacher> {
  protected storageTokenKey = 'ez_edu_teacher_token';
  protected loginEndpoint = environment.baseSyncUrl + '/education/teachers/login';
  protected loginRedirectPath = '/education/teacher/login';

  constructor(
    protected override http: HttpClient,
    protected override jwtHelper: JwtHelperService,
    protected override router: Router
  ) {
    super(http, jwtHelper, router);
    this.initUser();
  }

  register(signupData: TeacherSignupDto): Observable<ITeacher> {
    const signupEndpoint = environment.baseSyncUrl + '/education/teachers/register';
    return this.http.post<any>(signupEndpoint, signupData).pipe(
      map((response) => {
        console.log('[TeacherAuthService] ğŸ” íšŒì›ê°€ì… ì‘ë‹µ:', response);
        
        let user: any;
        let accessToken: string = '';

        // íŒ¨í„´ 1: { user: {...}, accessToken: '...' }
        if (response.user && response.accessToken) {
          user = response.user;
          accessToken = response.accessToken;
        } 
        // íŒ¨í„´ 2: { accessToken: '...', ...userData }
        else if (response.accessToken) {
          accessToken = response.accessToken;
          const { accessToken: _, ...userData } = response;
          user = userData;
        }
        // íŒ¨í„´ 3: ì‘ë‹µ ìì²´ê°€ user ê°ì²´ì¸ ê²½ìš°
        else {
          user = response;
          console.warn('[TeacherAuthService] íšŒì›ê°€ì… ì‘ë‹µì— í† í°ì´ ì—†ìŠµë‹ˆë‹¤. ìë™ ë¡œê·¸ì¸ì´ ë˜ì§€ ì•Šì„ ìˆ˜ ìˆìŠµë‹ˆë‹¤.');
        }

        if (accessToken) {
          this.setToken(accessToken);
        }
        
        return this.mapUser(user);
      })
    );
  }

  updateProfile(updateData: UpdateTeacherDto): Observable<ITeacher> {
    const teacherId = this.user$.value?.userIdx;
    if (!teacherId) {
      throw new Error('êµì‚¬ ì •ë³´ê°€ ì—†ìŠµë‹ˆë‹¤. ë‹¤ì‹œ ë¡œê·¸ì¸ í•´ì£¼ì„¸ìš”.');
    }

    const updateEndpoint = environment.baseSyncUrl + `/teacher/${teacherId}/profile`;
    return this.http.put<{ user: ITeacher }>(updateEndpoint, updateData).pipe(
      map(({ user }) => {
        const mappedUser = this.mapUser(user);
        this.setUser(mappedUser);
        return mappedUser;
      })
    );
  }

  updateStore(storeData: any): Observable<ITeacher> {
    const teacherId = this.user$.value?.userIdx;
    if (!teacherId) {
      throw new Error('íŒë§¤ì ì •ë³´ê°€ ì—†ìŠµë‹ˆë‹¤. ë‹¤ì‹œ ë¡œê·¸ì¸ í•´ì£¼ì„¸ìš”.');
    }

    const storeEndpoint = environment.baseSyncUrl + `/teacher/${teacherId}/store`;
    return this.http.put<{ user: ITeacher }>(storeEndpoint, storeData).pipe(
      map(({ user }) => {
        const mappedUser = this.mapUser(user);
        this.setUser(mappedUser);
        return mappedUser;
      })
    );
  }
}