import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { switchMap, map } from 'rxjs/operators';
import { Router } from '@angular/router';
import { StudentAuthService } from './student-auth.service';
import { TeacherAuthService } from './teacher-auth.service';
import { AdminAuthService } from './admin-auth.service';
import { UserRole } from '../interfaces/auth.interface';

@Injectable({
  providedIn: 'root'
})
export class AuthModeService {
  public currentMode$ = new BehaviorSubject<UserRole>('STUDENT');
  private readonly STORAGE_KEY = 'ez_edu_auth_mode';

  constructor(
    private router: Router,
    private studentAuth: StudentAuthService,
    private teacherAuth: TeacherAuthService,
    private adminAuth: AdminAuthService
  ) {
    this.initializeMode();
  }

  getCurrentMode$(): Observable<UserRole> {
    return this.currentMode$.asObservable();
  }

  getCurrentMode(): UserRole {
    return this.currentMode$.value;
  }

  switchMode(mode: UserRole): Observable<boolean> {
    // í˜„ì¬ ëª¨ë“  ì„œë¹„ìŠ¤ì—ì„œ ë¡œê·¸ì•„ì›ƒ
    this.logoutAll();
    
    // ìƒˆë¡œìš´ ëª¨ë“œ ì„¤ì •
    this.setMode(mode);
    
    return of(true);
  }

  logout(): Observable<boolean> {
    this.logoutAll();
    return of(true);
  }

  private setMode(mode: UserRole): void {
    this.currentMode$.next(mode);
    localStorage.setItem(this.STORAGE_KEY, mode);
  }

  private initializeMode(): void {
    const savedMode = localStorage.getItem(this.STORAGE_KEY) as UserRole;
    if (savedMode && ['STUDENT', 'TEACHER', 'ADMIN'].includes(savedMode)) {
      this.currentMode$.next(savedMode);
    }
  }

  private logoutAll(): void {
    // ëª¨ë“  ì¸ì¦ ì„œë¹„ìŠ¤ì—ì„œ ë¡œê·¸ì•„ì›ƒ (ë¦¬ë‹¤ì´ë ‰íŠ¸ ë°©ì§€)
    this.studentAuth.clearSession();
    this.teacherAuth.clearSession();
    this.adminAuth.clearSession();
  }

  private navigateToLogin(mode: UserRole): void {
    switch (mode) {
      case 'STUDENT':
        this.router.navigate(['/education/login']);
        break;
      case 'TEACHER':
        this.router.navigate(['/education/teacher/login']);
        break;
      case 'ADMIN':
        this.router.navigate(['/education/admin/login']);
        break;
    }
  }

  // í˜„ì¬ í™œì„± ì‚¬ìš©ì ì •ë³´ ê°€ì ¸ì˜¤ê¸° (Observable) - ëª¨ë“œ ë³€ê²½ì— ë°˜ì‘
  getCurrentUser(): Observable<any> {
    console.log('[AuthModeService] getCurrentUser() í˜¸ì¶œë¨');
    return this.currentMode$.pipe(
      switchMap(mode => {
        switch (mode) {
          case 'STUDENT':
            const studentUser = this.studentAuth.getCurrentUser();
            return this.studentAuth.getNullableUser$();
          case 'TEACHER':
            const teacherUser = this.teacherAuth.getCurrentUser();
            return this.teacherAuth.getNullableUser$();
          case 'ADMIN':
            const adminUser = this.adminAuth.getCurrentUser();
            return this.adminAuth.getNullableUser$();
          default:
            return of(null);
        }
      })
    );
  }

  // í˜„ì¬ í™œì„± ì‚¬ìš©ì ì •ë³´ ê°€ì ¸ì˜¤ê¸° (ë™ê¸°)
  getCurrentUserSync(): any {
    const mode = this.getCurrentMode();
    switch (mode) {
      case 'STUDENT':
        return this.studentAuth.getCurrentUser();
      case 'TEACHER':
        return this.teacherAuth.getCurrentUser();
      case 'ADMIN':
        return this.adminAuth.getCurrentUser();
      default:
        return null;
    }
  }

  // í˜„ì¬ ëª¨ë“œê°€ ì¸ì¦ë˜ì—ˆëŠ”ì§€ í™•ì¸ - ëª¨ë“œ ë³€ê²½ì— ë°˜ì‘
  isAuthenticated(): Observable<boolean> {
    return this.currentMode$.pipe(
      switchMap(mode => {
        switch (mode) {
          case 'STUDENT':
            return this.studentAuth.getNullableUser$().pipe(map((user: any) => !!user));
          case 'TEACHER':
            return this.teacherAuth.getNullableUser$().pipe(map((user: any) => !!user));
          case 'ADMIN':
            return this.adminAuth.getNullableUser$().pipe(map((user: any) => !!user));
          default:
            return of(false);
        }
      })
    ) as Observable<boolean>;
  }

  // ëª¨ë“œë³„ í™ˆ ê²½ë¡œ ê°€ì ¸ì˜¤ê¸°
  getHomeRoute(): string {
    const mode = this.getCurrentMode();
    switch (mode) {
      case 'STUDENT':
        return '/education/home';
      case 'TEACHER':
        return '/education/home';
      case 'ADMIN':
        return '/education/home';
      default:
        return '/education/home';
    }
  }
}