import { Component, OnInit, OnDestroy, Inject } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Subject, takeUntil } from 'rxjs';
import { WeeklySurveyApiService, WeeklySurvey, SurveyQuestion, SurveyAnswer, SurveySubmitDto } from '../../services/weekly-survey-api.service';
import { StudentAuthService } from '../../services/student-auth.service';

export interface WeeklySurveyDialogData {
  courseCode: string;
  courseId?: string;
  courseName: string;
  schoolId: string;
  weekNumber: number;
}

@Component({
  selector: 'app-weekly-survey',
  templateUrl: './weekly-survey.component.html',
  styleUrls: ['./weekly-survey.component.scss'],
  standalone: false
})
export class WeeklySurveyComponent implements OnInit, OnDestroy {
  survey: WeeklySurvey | null = null;
  surveyForm!: FormGroup;
  
  isLoading = true;
  isSubmitting = false;
  hasAlreadyResponded = false;
  errorMessage = '';
  
  private destroy$ = new Subject<void>();

  constructor(
    private fb: FormBuilder,
    private surveyApiService: WeeklySurveyApiService,
    private studentAuthService: StudentAuthService,
    private snackBar: MatSnackBar,
    public dialogRef: MatDialogRef<WeeklySurveyComponent>,
    @Inject(MAT_DIALOG_DATA) public data: WeeklySurveyDialogData
  ) {
    this.surveyForm = this.fb.group({});
  }

  ngOnInit(): void {
    this.loadSurvey();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  private loadSurvey(): void {
    const studentId = this.studentAuthService.getCurrentUser()?.id;
    
    // ë¨¼ì € ì´ë¯¸ ì‘ë‹µí–ˆëŠ”ì§€ í™•ì¸
    if (studentId) {
      this.surveyApiService.checkIfResponded(
        this.data.courseCode, 
        this.data.weekNumber, 
        studentId
      ).pipe(takeUntil(this.destroy$)).subscribe({
        next: (result) => {
          if (result.hasResponded) {
            this.hasAlreadyResponded = true;
            this.isLoading = false;
            return;
          }
          this.fetchSurveyData();
        },
        error: () => {
          this.fetchSurveyData();
        }
      });
    } else {
      this.fetchSurveyData();
    }
  }

  private fetchSurveyData(): void {
    this.surveyApiService.getWeeklySurvey(this.data.courseCode, this.data.weekNumber)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (survey) => {
          this.survey = survey;
          this.buildForm(survey.questions);
          this.isLoading = false;
        },
        error: (error) => {
          this.errorMessage = 'ì„¤ë¬¸ì„ ë¶ˆëŸ¬ì˜¤ëŠ”ë° ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.';
          this.isLoading = false;
        }
      });
  }

  private buildForm(questions: SurveyQuestion[]): void {
    const formControls: { [key: string]: FormControl } = {};
    
    questions.forEach(q => {
      let validators = [];
      if (q.required) {
        validators.push(Validators.required);
      }
      
      // ì§ˆë¬¸ ìœ í˜•ë³„ ê¸°ë³¸ê°’ ì„¤ì •
      let defaultValue: any = '';
      
      switch (q.type) {
        case 'MULTIPLE_CHOICE':
          defaultValue = [];
          break;
        case 'RATING':
        case 'SCALE':
          defaultValue = q.min || 1;
          break;
        case 'DATE':
          defaultValue = null;
          break;
        default:
          defaultValue = '';
      }
      
      formControls[q.id] = new FormControl(defaultValue, validators);
    });
    
    this.surveyForm = this.fb.group(formControls);
  }

  getFormControl(questionId: string): FormControl {
    return this.surveyForm.get(questionId) as FormControl;
  }

  onMultipleChoiceChange(questionId: string, value: string, checked: boolean): void {
    const control = this.getFormControl(questionId);
    const currentValues: string[] = control.value || [];
    
    if (checked) {
      control.setValue([...currentValues, value]);
    } else {
      control.setValue(currentValues.filter((v: string) => v !== value));
    }
  }

  isOptionChecked(questionId: string, value: string): boolean {
    const control = this.getFormControl(questionId);
    return (control.value || []).includes(value);
  }

  getRatingArray(question: SurveyQuestion): number[] {
    const min = question.min || 1;
    const max = question.max || 5;
    return Array.from({ length: max - min + 1 }, (_, i) => min + i);
  }

  onSubmit(): void {
    if (this.surveyForm.invalid || !this.survey) {
      this.markFormAsTouched();
      this.snackBar.open('í•„ìˆ˜ í•­ëª©ì„ ëª¨ë‘ ì…ë ¥í•´ì£¼ì„¸ìš”.', 'í™•ì¸', { duration: 3000 });
      return;
    }

    const studentId = this.studentAuthService.getCurrentUser()?.id;
    if (!studentId) {
      this.snackBar.open('ë¡œê·¸ì¸ì´ í•„ìš”í•©ë‹ˆë‹¤.', 'í™•ì¸', { duration: 3000 });
      return;
    }

    this.isSubmitting = true;

    // ë‹µë³€ ë°ì´í„° êµ¬ì„±
    const answers: SurveyAnswer[] = this.survey.questions.map(q => {
      const value = this.surveyForm.get(q.id)?.value;
      return {
        questionId: q.id,
        questionText: q.question,
        value: value,
        displayValue: this.formatDisplayValue(q, value)
      };
    });

    const submitData: SurveySubmitDto = {
      courseCode: this.data.courseCode,
      courseId: this.data.courseId,
      schoolId: this.data.schoolId,
      studentId: studentId,
      weekNumber: this.data.weekNumber,
      answers: answers
    };

    this.surveyApiService.submitSurvey(submitData)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: () => {
          this.snackBar.open('ì„¤ë¬¸ì´ ì„±ê³µì ìœ¼ë¡œ ì œì¶œë˜ì—ˆìŠµë‹ˆë‹¤! ğŸ“§ ì´ë©”ì¼ë¡œ í™•ì¸ì„œê°€ ë°œì†¡ë©ë‹ˆë‹¤.', 'í™•ì¸', { duration: 5000 });
          this.dialogRef.close({ success: true });
        },
        error: (error) => {
          this.isSubmitting = false;
          const message = error.error?.message || 'ì„¤ë¬¸ ì œì¶œì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.';
          this.snackBar.open(message, 'í™•ì¸', { duration: 3000 });
        }
      });
  }

  private formatDisplayValue(question: SurveyQuestion, value: any): string {
    if (value === null || value === undefined || value === '') return '';
    
    switch (question.type) {
      case 'SINGLE_CHOICE':
        const singleOption = question.options?.find(o => o.value === value);
        return singleOption?.label || String(value);
      
      case 'MULTIPLE_CHOICE':
        if (Array.isArray(value)) {
          return value.map(v => {
            const opt = question.options?.find(o => o.value === v);
            return opt?.label || v;
          }).join(', ');
        }
        return String(value);
      
      case 'RATING':
        return `${value}ì `;
      
      case 'SCALE':
        return `${value} / ${question.max || 10}`;
      
      default:
        return String(value);
    }
  }

  private markFormAsTouched(): void {
    Object.keys(this.surveyForm.controls).forEach(key => {
      this.surveyForm.get(key)?.markAsTouched();
    });
  }

  onCancel(): void {
    this.dialogRef.close({ success: false });
  }
}
