/**
 * Course Introduction Survey Interface
 * Used to collect student information before course starts (e.g., Dream Design class)
 */

export enum ExperienceLevel {
  BEGINNER = 'BEGINNER',
  INTERMEDIATE = 'INTERMEDIATE',
  ADVANCED = 'ADVANCED',
}

export enum IntroductionSurveyStatus {
  PENDING = 'PENDING',
  COMPLETED = 'COMPLETED',
  SUBMITTED = 'SUBMITTED',
}

export interface IIntroductionSurvey {
  id: string;
  courseId: string;
  studentId: string;
  dreamDescription: string;
  wantToMake: string;
  selfIntroduction: string;
  motivationScore: number; // 1-5 scale
  experienceLevel: ExperienceLevel;
  expectation: string;
  specialRequest: string;
  status: IntroductionSurveyStatus;
  submittedAt?: Date;
  createdAt: Date;
  updatedAt: Date;
}

export interface ICreateIntroductionSurvey {
  dreamDescription?: string;
  wantToMake?: string;
  selfIntroduction?: string;
  motivationScore?: number;
  experienceLevel?: ExperienceLevel;
  expectation?: string;
  specialRequest?: string;
}

export interface IIntroductionSurveyResponse {
  id: string;
  courseId: string;
  studentId: string;
  studentName?: string;
  dreamDescription: string;
  wantToMake: string;
  selfIntroduction: string;
  motivationScore: number;
  experienceLevel: ExperienceLevel;
  expectation: string;
  specialRequest: string;
  status: IntroductionSurveyStatus;
  submittedAt?: Date;
  createdAt: Date;
  updatedAt: Date;
}

export interface ISurveyCompletionRate {
  total: number;
  completed: number;
  rate: number; // 0-100
}
