import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { BehaviorSubject, Observable, EMPTY, Subject } from 'rxjs';
import { catchError, take, debounceTime, switchMap } from 'rxjs/operators';
import { environment } from '../config/environment';
import { ICart, ICartItem } from '../interfaces/models/school.interface';

interface AddItemDto {
  productId: string;
  quantity: number;
  ownerId: string;
}

interface UpdateItemDto {
  productId: string;
  quantity: number;
  ownerId: string;
}

@Injectable({
  providedIn: 'root'
})
export class CartService {
  private readonly apiUrl = `${environment.apiUrl}/cart`;
  
  // ì¥ë°”êµ¬ë‹ˆ ìƒíƒœ ê´€ë¦¬
  public cart$ = new BehaviorSubject<ICart | undefined>(undefined);
  
  // ìˆ˜ëŸ‰ ë³€ê²½ debounceë¥¼ ìœ„í•œ ë²„í¼
  private quantityBuffer$ = new Subject<{ productId: string; quantity: number; snapshot?: ICart }>();
  private debouncing = false;
  private previousCart?: ICart;

  constructor(private http: HttpClient) {
    this.setupQuantityDebounce();
  }

  /**
   * ìˆ˜ëŸ‰ ë³€ê²½ ìš”ì²­ì„ ë””ë°”ìš´ìŠ¤ ì²˜ë¦¬
   */
  private setupQuantityDebounce(): void {
    this.quantityBuffer$
      .pipe(
        debounceTime(500),
        switchMap((dto) => {
          const userId = dto.snapshot?.ownerId;
          if (!userId) return EMPTY;
          
          return this.updateItemQuantity$({ 
            productId: dto.productId, 
            quantity: dto.quantity 
          }, userId).pipe(
            catchError((error) => {
              // ì‹¤íŒ¨ ì‹œ ì´ì „ ìƒíƒœë¡œ ë³µì›
              if (this.previousCart) {
                this.cart$.next(this.previousCart);
              }
              return EMPTY;
            })
          );
        })
      )
      .subscribe((updatedCart) => {
        if (updatedCart) {
          this.cart$.next(updatedCart);
        }
        this.debouncing = false;
        this.previousCart = undefined;
      });
  }

  /**
   * [êµ¬ë§¤ì] ì¥ë°”êµ¬ë‹ˆ ì¡°íšŒ
   */
  fetchCart(userId: string): void {
    const params = new HttpParams().set('ownerId', userId);
    this.http
      .get<ICart>(this.apiUrl, { params })
      .pipe(
        take(1),
        catchError((error) => {
          this.cart$.next(undefined);
          return EMPTY;
        })
      )
      .subscribe((cart) => {
        if (cart) {
          this.cart$.next(cart);
        }
      });
  }

  /**
   * [êµ¬ë§¤ì] ì¥ë°”êµ¬ë‹ˆì— ìƒí’ˆ ì¶”ê°€
   */
  addItem$(param: { productId: string; quantity: number }, userId: string): Observable<ICart> {
    const dto: AddItemDto = {
      ...param,
      ownerId: userId,
    };
    
    return this.http.post<ICart>(this.apiUrl, dto).pipe(
      take(1),
      catchError((error) => {
        throw error;
      })
    );
  }

  /**
   * [êµ¬ë§¤ì] ì¥ë°”êµ¬ë‹ˆ ìˆ˜ëŸ‰ ë³€ê²½ (ì„œë²„ ìš”ì²­)
   */
  private updateItemQuantity$(
    param: { productId: string; quantity: number },
    userId: string
  ): Observable<ICart | null> {
    const dto: UpdateItemDto = {
      ...param,
      ownerId: userId,
    };
    
    return this.http
      .patch<ICart>(`${this.apiUrl}/items/${param.productId}`, dto)
      .pipe(
        take(1),
        catchError((error) => {
          throw error;
        })
      );
  }

  /**
   * [êµ¬ë§¤ì] ì¥ë°”êµ¬ë‹ˆ ìˆ˜ëŸ‰ ë³€ê²½ ìš”ì²­ íì‰ (ë‚™ê´€ì  ì—…ë°ì´íŠ¸)
   */
  queueUpdateItemQuantity(dto: { productId: string; quantity: number }): void {
    const current = this.cart$.getValue();
    
    // ì²« ë²ˆì§¸ ë³€ê²½ì´ë©´ ì´ì „ ìƒíƒœ ì €ì¥
    if (!this.debouncing) {
      this.previousCart = structuredClone(current);
    }
    
    // ë‚™ê´€ì  ì—…ë°ì´íŠ¸: UIì— ì¦‰ì‹œ ë°˜ì˜
    const clone = structuredClone(current);
    const target = clone?.items.find((item) => item.product.id === dto.productId);
    if (target) {
      target.quantity = dto.quantity;
    }
    this.cart$.next(clone);
    
    // ì„œë²„ ìš”ì²­ì€ debounce ì²˜ë¦¬
    this.quantityBuffer$.next({ ...dto, snapshot: current });
    this.debouncing = true;
  }

  /**
   * [êµ¬ë§¤ì] ì¥ë°”êµ¬ë‹ˆ ì•„ì´í…œ ì‚­ì œ
   */
  deleteItem(itemId: string, userId: string): Observable<ICart | null> {
    const params = new HttpParams().set('ownerId', userId);
    
    return this.http
      .delete<ICart>(`${this.apiUrl}/items/${itemId}`, { params })
      .pipe(
        take(1),
        catchError((error) => {
          throw error;
        })
      );
  }

  /**
   * ì¥ë°”êµ¬ë‹ˆ ë¹„ìš°ê¸°
   */
  clearCart(): void {
    this.cart$.next(undefined);
  }

  /**
   * í˜„ì¬ ì¥ë°”êµ¬ë‹ˆ ê°€ì ¸ì˜¤ê¸°
   */
  getCart(): ICart | undefined {
    return this.cart$.getValue();
  }
}
