import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable, map, catchError, of } from 'rxjs';
import { environment } from '../config/environment';
import { ICourse, ISchool, ISearchProductDTO, ProductSearchResponseDto } from '../interfaces/models/school.interface';

@Injectable({ providedIn: 'root' })
export class CourseApiService {
  private readonly baseUrl = environment.baseSyncUrl + '/product';
  private readonly educationUrl = environment.baseSyncUrl + '/education/courses';

  constructor(private readonly http: HttpClient) {}

  // ========== êµìœ¡ ëª¨ë“ˆ ì „ìš© API (edu_ í…Œì´ë¸” ì—°ë™) ==========

  /**
   * ì¸ê¸° ê³¼ëª© ì¡°íšŒ
   * ë°±ì—”ë“œ: GET /education/courses/popular
   */
  getPopularCourses$(limit: number = 10): Observable<ICourse[]> {
    const params = new HttpParams().set('limit', limit.toString());
    return this.http.get<any[]>(`${this.educationUrl}/popular`, { params }).pipe(
      map(courses => courses.map(c => this.mapEducationCourseResponse(c))),
      catchError(() => of([]))
    );
  }

  /**
   * ì¹´í…Œê³ ë¦¬ë³„ ê³¼ëª© ì¡°íšŒ
   * ë°±ì—”ë“œ: GET /education/courses/category/:category
   */
  getCoursesByCategory$(category: string): Observable<ICourse[]> {
    return this.http.get<any[]>(`${this.educationUrl}/category/${category}`).pipe(
      map(courses => courses.map(c => this.mapEducationCourseResponse(c))),
      catchError(() => of([]))
    );
  }

  /**
   * í•™êµë³„ ê³¼ëª© ì¡°íšŒ
   * ë°±ì—”ë“œ: GET /education/courses/school/:schoolId
   */
  getCoursesBySchool$(schoolId: string): Observable<ICourse[]> {
    return this.http.get<any[]>(`${this.educationUrl}/school/${schoolId}`).pipe(
      map(courses => courses.map(c => this.mapEducationCourseResponse(c))),
      catchError(() => of([]))
    );
  }

  /**
   * ê³¼ëª© ìƒì„¸ ì¡°íšŒ
   * ë°±ì—”ë“œ: GET /education/courses/:id
   */
  getEducationCourse$(courseId: string): Observable<ICourse | null> {
    return this.http.get<any>(`${this.educationUrl}/${courseId}`).pipe(
      map(course => this.mapEducationCourseResponse(course)),
      catchError(() => of(null))
    );
  }

  /**
   * êµìœ¡ ëª¨ë“ˆ ê³¼ëª© ì‘ë‹µ ë§¤í•‘
   */
  private mapEducationCourseResponse(course: any): ICourse {
    const school = course.school ? this.mapSchoolResponse(course.school) : {
      id: course.schoolId || 'unknown',
      name: 'ì•Œ ìˆ˜ ì—†ëŠ” í•™êµ',
      ownerId: '',
      businessRegistrationNumber: '',
      zipcode: '',
      address: '',
      addressDetail: '',
      studentServiceNumber: '',
      eCommerceRegistrationNumber: '',
      email: '',
      createdAt: new Date()
    };

    return {
      id: course.id,
      schoolId: course.schoolId,
      storeId: course.schoolId,
      name: course.name || course.title || 'ì´ë¦„ ì—†ìŒ',
      shortDescription: course.shortDescription || course.description,
      description: course.detail || course.description,
      price: course.price || 0,
      discount: 0,
      discountType: 'won',
      type: 'purchase',
      detail: course.description || '',
      isVisible: true,
      shippingFee: 0,
      stockGrantPolicyId: 0,
      school: school,
      store: school,
      originalPrice: course.originalPrice || 0,
      quantity: 999,
      status: course.status,
      thumbnailMeta: course.thumbnailId ? {
        id: course.thumbnailId,
        url: `${environment.baseSyncUrl}/uploads/${course.thumbnailId}`
      } : undefined,
      imageMetas: [],
      category: course.category,
      subCategory: course.subCategory,
      tags: course.tags || [],
      createdAt: new Date(course.createdAt),
      // êµìœ¡ ëª¨ë“ˆ ì „ìš© í•„ë“œ
      teacherId: course.teacherId,
      difficulty: course.difficulty,
      totalWeeks: course.totalWeeks,
      estimatedHours: course.estimatedHours,
      enrollmentCount: course.enrollmentCount,
      rating: course.rating,
      reviewCount: course.reviewCount,
      isFree: course.isFree,
      prerequisites: course.prerequisites,
      learningObjectives: course.learningObjectives,
    };
  }

  /**
   * í•™êµ ì‘ë‹µ ë§¤í•‘ (í•„ìš”ì‹œ)
   */
  private mapSchoolResponse(school: any): ISchool {
    return {
      id: school.id,
      name: school.name,
      description: school.description,
      ownerId: school.ownerId,
      logoImageId: school.logoImageId,
      businessRegistrationNumber: school.businessRegistrationNumber || '',
      zipcode: school.zipcode || '',
      address: school.address || '',
      addressDetail: school.addressDetail || '',
      studentServiceNumber: school.studentServiceNumber || '',
      eCommerceRegistrationNumber: school.eCommerceRegistrationNumber || '',
      email: school.email || '',
      productCount: school.courseCount,
      status: school.status,
      createdAt: new Date(school.createdAt)
    };
  }

  // ========== ê¸°ì¡´ Store Product API (í˜¸í™˜ì„± ìœ ì§€) ==========

  /**
   * íŠ¹ì • ìŠ¤í† ì–´ì˜ ëª¨ë“  ìƒí’ˆ ì¡°íšŒ
   * SyncAna íŒ¨í„´: /store/:storeId/products (withReviews ì˜µì…˜)
   */
  getProductsByStoreId$(storeId: string): Observable<ICourse[]> {
    // SyncAna íŒ¨í„´: /store/:storeId/products
    const url = environment.baseSyncUrl + `/store/${storeId}/products`;
    const params = new HttpParams().set('withReviews', 'true');
    
    return this.http.get<ICourse[]>(url, { params }).pipe(
      map(products => products.map(p => this.mapProductResponse(p))),
      catchError((error) => {
        return of([]);
      })
    );
  }

  /**
   * ë‹¨ì¼ ìƒí’ˆ ì¡°íšŒ
   * SyncAna íŒ¨í„´: /store/products/:productId
   */
  getProductById$(productId: string): Observable<ICourse | null> {
    // SyncAna íŒ¨í„´: /store/products/:productId
    const url = environment.baseSyncUrl + `/store/products/${productId}`;
    
    return this.http.get<ICourse>(url).pipe(
      map(product => this.mapProductResponse(product)),
      catchError((error) => {
        return of(null);
      })
    );
  }

  /**
   * ëª¨ë“  ìƒí’ˆ ì¡°íšŒ (ê´€ë¦¬ììš©)
   * SyncAna íŒ¨í„´: /store/products/all
   */
  getAllProducts$(): Observable<ICourse[]> {
    const url = environment.baseSyncUrl + '/store/products/all';
    
    return this.http.get<ICourse[]>(url).pipe(
      map(products => products.map(p => this.mapProductResponse(p))),
      catchError((error) => {
        return of([]);
      })
    );
  }

  /**
   * ìƒí’ˆ ê²€ìƒ‰
   * ì‹¤ì œ API ê²½ë¡œ: GET /store/products/search/visible?q={query}&sort={sort}&page={page}&limit={limit}
   */
  searchProducts$(searchDto: ISearchProductDTO): Observable<ProductSearchResponseDto> {
    const params = new HttpParams({ 
      fromObject: { 
        q: searchDto.q,
        sort: searchDto.sort,
        page: searchDto.page.toString(),
        limit: searchDto.limit.toString()
      } 
    });
    
    const url = environment.baseSyncUrl + '/store/products/search/visible';
    return this.http.get<ProductSearchResponseDto>(url, { params }).pipe(
      map(response => ({
        products: response.products.map(p => this.mapProductResponse(p)),
        hasNext: response.hasNext
      })),
      catchError((error) => {
        return of({ products: [], hasNext: false });
      })
    );
  }

  /**
   * ë°±ì—”ë“œ ì‘ë‹µì„ UI ëª¨ë¸ë¡œ ë§¤í•‘
   * SyncAna íŒ¨í„´: ìƒëŒ€ ê²½ë¡œì— baseSyncUrl ë¶™ì´ê¸°
   */
  private mapProductResponse(product: any): ICourse {
    const school = product.school || product.store;
    const schoolId = product.schoolId || product.storeId;

    return {
      ...product,
      schoolId: schoolId,
      storeId: schoolId,
      school: school,
      store: school,
      createdAt: product.createdAt ? new Date(product.createdAt) : new Date(),
      thumbnailMeta: product.thumbnailMeta ? {
        ...product.thumbnailMeta,
        // SyncAna íŒ¨í„´: ìƒëŒ€ ê²½ë¡œì— baseSyncUrlì„ prefixë¡œ ì¶”ê°€
        url: product.thumbnailMeta.url.startsWith('http') 
          ? product.thumbnailMeta.url 
          : `${environment.baseSyncUrl}${product.thumbnailMeta.url}`
      } : undefined,
      imageMetas: product.imageMetas?.map((img: any) => ({
        ...img,
        url: img.url.startsWith('http') 
          ? img.url 
          : `${environment.baseSyncUrl}${img.url}`
      })) || []
    } as ICourse;
  }
}
