import { CanActivateFn, Router, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { inject } from '@angular/core';
import { AdminAuthService } from '../services/admin-auth.service';

export const adminAuthGuard: CanActivateFn = (route: ActivatedRouteSnapshot, state: RouterStateSnapshot) => {
  const adminAuth = inject(AdminAuthService);
  const router = inject(Router);

  // ë§¤ë²ˆ í† í° ìœ íš¨ì„±ì„ ëª…ì‹œì ìœ¼ë¡œ í™•ì¸
  const isAuthenticated = adminAuth.isAuthenticated();
  
  if (!isAuthenticated) {
    console.warn('[adminAuthGuard] ì¸ì¦ ì‹¤íŒ¨ - ë¡œê·¸ì¸ í˜ì´ì§€ë¡œ ì´ë™');
    router.navigate(['/education/admin/login']);
    return false;
  }

  const user = adminAuth.getCurrentUser();
  
  // ê´€ë¦¬ì ê¶Œí•œ ì²´í¬ (roleId >= 300)
  let roleId = 0;
  if (typeof user?.role === 'object' && user?.role !== null) {
    roleId = (user.role as any).roleId || 0;
  } else if (typeof user?.role === 'number') {
    roleId = user.role;
  }
  
  if (roleId >= 300) {
    console.log('[adminAuthGuard] ê´€ë¦¬ì ê¶Œí•œ í™•ì¸ (roleId:', roleId, ')');
    return true;
  }
  
  console.log('[adminAuthGuard] ê´€ë¦¬ì ê¶Œí•œ ì—†ìŒ (roleId:', roleId, ')');
  router.navigate(['/education']);
  return false;
};

