import { Component, Input, OnChanges, SimpleChanges, OnInit } from '@angular/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { IWysiwyg } from '../../interfaces/models/widget.interface';

@Component({
  selector: 'app-course-detail-viewer',
  templateUrl: './course-detail-viewer.component.html',
  styleUrls: ['./course-detail-viewer.component.scss'],
  standalone: false
})
export class CourseDetailViewerComponent implements OnInit, OnChanges {
  @Input() detailJson?: string; // JSON ë¬¸ìì—´
  @Input() wysiwyg?: IWysiwyg; // ë˜ëŠ” ì§ì ‘ ê°ì²´

  parsedWysiwyg: IWysiwyg | null = null;

  constructor(private sanitizer: DomSanitizer) {}

  ngOnInit(): void {
    this.parseData();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['detailJson'] || changes['wysiwyg']) {
      this.parseData();
    }
  }

  private parseData(): void {
    // detailJsonì´ ì—†ìœ¼ë©´ wysiwyg ì‚¬ìš©
    if (!this.detailJson && this.wysiwyg) {
      this.parsedWysiwyg = this.wysiwyg;
      return;
    }

    if (!this.detailJson) {
      this.parsedWysiwyg = null;
      return;
    }

    // ì´ë¯¸ ê°ì²´ì¸ ê²½ìš°
    if (typeof this.detailJson === 'object') {
      this.parsedWysiwyg = this.detailJson as any;
      return;
    }

    // ë¬¸ìì—´ì¸ ê²½ìš° íŒŒì‹± ì‹œë„
    try {
      let parsed = JSON.parse(this.detailJson);
      
      // ì´ì¤‘ stringify ì²´í¬: íŒŒì‹± ê²°ê³¼ê°€ ë˜ ë¬¸ìì—´ì´ë©´ í•œ ë²ˆ ë” íŒŒì‹±
      if (typeof parsed === 'string') {
        parsed = JSON.parse(parsed);
      }
      
      // ìµœì¢… ê²€ì¦
      if (parsed && typeof parsed === 'object' && parsed.sections) {
        this.parsedWysiwyg = parsed;
      } else {
        this.parsedWysiwyg = null;
      }
      
    } catch (e) {
      this.parsedWysiwyg = null;
    }
  }

  /**
   * ìœ„ì ¯ ìŠ¤íƒ€ì¼ ê°€ì ¸ì˜¤ê¸°
   */
  getWidgetStyle(widget: any): any {
    const styles: any = {};
    const styleConfig = widget.properties?.style || {};

    for (const key in styleConfig) {
      const styleItem = styleConfig[key];
      if (styleItem.enabled) {
        const cssProperty = styleItem.cssProperty;
        const value = styleItem.value;
        const unit = styleItem.unit || '';
        styles[cssProperty] = value + unit;
      }
    }

    return styles;
  }

  /**
   * í…ìŠ¤íŠ¸ ìœ„ì ¯ì˜ HTML ì½˜í…ì¸ ë¥¼ ì•ˆì „í•˜ê²Œ ë°˜í™˜
   */
  getTextContent(widget: any): string {
    return widget.properties?.content || '';
  }

  /**
   * ì´ë¯¸ì§€ ìœ„ì ¯ì˜ ì†ŒìŠ¤ URL
   */
  getImageSrc(widget: any): string {
    const src = widget.properties?.source?.src || '';
    return this.normalizeImageUrl(src);
  }

  /**
   * ì´ë¯¸ì§€ ìœ„ì ¯ì˜ alt í…ìŠ¤íŠ¸
   */
  getImageAlt(widget: any): string {
    return widget.properties?.source?.alt || 'ìƒí’ˆ ì´ë¯¸ì§€';
  }

  /**
   * ì´ë¯¸ì§€ URL ì •ê·œí™” (3000 í¬íŠ¸ ì œê±°)
   */
  private normalizeImageUrl(url: string): string {
    if (!url) return url;
    
    // https://api.loworldsoft.com:3000 -> https://api.loworldsoft.com ë³€í™˜
    return url.replace(/https:\/\/api\.loworldsoft\.com:3000/g, 'https://api.loworldsoft.com');
  }

  /**
   * ì´ë¯¸ì§€ ìœ„ì ¯ì˜ ì„¤ëª…
   */
  getImageDescription(widget: any): string {
    return widget.properties?.description || '';
  }

  /**
   * Embed ìœ„ì ¯ì˜ ì†ŒìŠ¤ URL (ì•ˆì „í•˜ê²Œ ì²˜ë¦¬)
   */
  getEmbedSrc(widget: any): SafeResourceUrl {
    const src = widget.properties?.source?.src || '';
    return this.sanitizer.bypassSecurityTrustResourceUrl(src);
  }

  /**
   * Embed ìœ„ì ¯ì˜ ì œëª©
   */
  getEmbedTitle(widget: any): string {
    return widget.properties?.source?.title || 'ì„ë² ë””ë“œ ì½˜í…ì¸ ';
  }

  /**
   * ì´ë¯¸ì§€ ë¡œë”© ì‹¤íŒ¨ ì²˜ë¦¬ (ì¬ì‹œë„ ë°©ì§€)
   */
  onImageError(event: Event): void {
    const imgElement = event.target as HTMLImageElement;
    // srcë¥¼ ë¹„ì›Œì„œ ì¬ì‹œë„ ë°©ì§€
    imgElement.src = '';
    // ë˜ëŠ” ê¸°ë³¸ ì´ë¯¸ì§€ë¡œ ëŒ€ì²´
    // imgElement.src = 'assets/images/no-image.png';
  }

  /**
   * ë””ë²„ê¹…ìš©: ë°ì´í„° ê¸¸ì´ ë°˜í™˜
   */
  getDataLength(): number {
    if (typeof this.detailJson === 'string') {
      return this.detailJson.length;
    }
    return 0;
  }
}
