import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';
import { StudentAuthService } from '../../services/student-auth.service';
import { AuthModeService } from '../../services/auth-mode.service';
import { ThemeService } from '../../../../services/theme.service';

@Component({
  selector: 'app-student-login',
  templateUrl: './student-login.component.html',
  styleUrls: ['./student-login.component.scss'],
  standalone: false
})
export class StudentLoginComponent implements OnInit, OnDestroy {
  loginForm: FormGroup;
  isLoading = false;
  errorMessage = '';
  hidePassword = true;
  isDarkMode = false;
  private subscriptions = new Subscription();

  constructor(
    private fb: FormBuilder,
    private studentAuth: StudentAuthService,
    private authModeService: AuthModeService,
    private router: Router,
    private route: ActivatedRoute,
    public themeService: ThemeService
  ) {
    this.loginForm = this.fb.group({
      id: ['', [Validators.required, Validators.minLength(3)]],
      password: ['', [Validators.required, Validators.minLength(6)]]
    });
  }

  ngOnInit(): void {
    // ëª¨ë“œë¥¼ í•™ìƒìœ¼ë¡œ ì„¤ì •
    this.authModeService.switchMode('STUDENT');

    // í…Œë§ˆ êµ¬ë…
    this.subscriptions.add(
      this.themeService.currentTheme$.subscribe(theme => {
        this.isDarkMode = theme === 'dark';
      })
    );

    // ì´ë¯¸ ë¡œê·¸ì¸ëœ ê²½ìš° ë¦¬ë‹¤ì´ë ‰íŠ¸
    if (this.studentAuth.isAuthenticated()) {
      this.redirectToReturnUrl();
    }
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  onSubmit(): void {
    if (this.loginForm.valid && !this.isLoading) {
      this.isLoading = true;
      this.errorMessage = '';

      const { id, password } = this.loginForm.value;

      this.subscriptions.add(
        this.studentAuth.login({ id, password }).subscribe({
          next: (user) => {
            this.redirectToReturnUrl();
          },
          error: (error) => {
            this.errorMessage = this.getErrorMessage(error);
            this.isLoading = false;
          }
        })
      );
    } else {
      this.markFormGroupTouched();
    }
  }

  onRegisterClick(): void {
    this.router.navigate(['/education/register']).then(
      success => console.log('ë¼ìš°íŒ… ì„±ê³µ:', success),
      error => console.warn('ë¼ìš°íŒ… ì‹¤íŒ¨:', error)
    );
  }

  onModeSwitch(mode: 'TEACHER' | 'ADMIN'): void {
    this.authModeService.switchMode(mode);
    if (mode === 'TEACHER') {
      this.router.navigate(['/education/teacher/login']);
    } else {
      this.router.navigate(['/education/admin/login']);
    }
  }

  private redirectToReturnUrl(): void {
    const returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/education/home';
    this.router.navigate([returnUrl]);
  }

  private markFormGroupTouched(): void {
    Object.keys(this.loginForm.controls).forEach(key => {
      const control = this.loginForm.get(key);
      if (control) {
        control.markAsTouched();
      }
    });
  }

  getFieldError(fieldName: string): string {
    const field = this.loginForm.get(fieldName);
    if (field && field.errors && field.touched) {
      if (field.errors['required']) {
        return `${this.getFieldDisplayName(fieldName)}ì„(ë¥¼) ì…ë ¥í•´ì£¼ì„¸ìš”.`;
      }
      if (field.errors['minlength']) {
        const minLength = field.errors['minlength'].requiredLength;
        return `${this.getFieldDisplayName(fieldName)}ì€(ëŠ”) ìµœì†Œ ${minLength}ì ì´ìƒì´ì–´ì•¼ í•©ë‹ˆë‹¤.`;
      }
    }
    return '';
  }

  private getFieldDisplayName(fieldName: string): string {
    const displayNames: { [key: string]: string } = {
      'id': 'ì•„ì´ë””',
      'password': 'ë¹„ë°€ë²ˆí˜¸'
    };
    return displayNames[fieldName] || fieldName;
  }

  isFieldInvalid(fieldName: string): boolean {
    const field = this.loginForm.get(fieldName);
    return !!(field && field.errors && field.touched);
  }

  private getErrorMessage(error: any): string {
    if (error.error?.message) {
      if (typeof error.error.message === 'object' && error.error.message.message) {
        return error.error.message.message;
      }
      return error.error.message;
    }
    
    if (error.status === 401) {
      return 'ì•„ì´ë”” ë˜ëŠ” ë¹„ë°€ë²ˆí˜¸ê°€ ì˜ëª»ë˜ì—ˆìŠµë‹ˆë‹¤.';
    }
    
    if (error.status === 0) {
      return 'ì„œë²„ì— ì—°ê²°í•  ìˆ˜ ì—†ìŠµë‹ˆë‹¤. ë„¤íŠ¸ì›Œí¬ ì—°ê²°ì„ í™•ì¸í•´ì£¼ì„¸ìš”.';
    }
    
    return 'ë¡œê·¸ì¸ì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤. ì ì‹œ í›„ ë‹¤ì‹œ ì‹œë„í•´ì£¼ì„¸ìš”.';
  }
}