import { Component, Input, OnInit } from '@angular/core';
import { ITextWidgetProperties } from '../../../interfaces/models/widget.interface';

@Component({
  selector: 'app-widget-text',
  templateUrl: './widget-text.component.html',
  styleUrls: ['./widget-text.component.scss'],
  standalone: false
})
export class WidgetTextComponent implements OnInit {
  @Input() properties!: ITextWidgetProperties;

  style: any = {};

  ngOnInit(): void {
    this.style = this.getStyle();
  }

  /**
   * properties.style ê°ì²´ë¥¼ ì‹¤ì œ CSS ìŠ¤íƒ€ì¼ë¡œ ë³€í™˜
   * enabledê°€ trueì¸ í•­ëª©ë§Œ ì ìš©
   */
  getStyle() {
    const styles: { [key: string]: string | number } = {};

    if (!this.properties?.style) return styles;

    const styleObj = this.properties.style as any;
    for (const key in styleObj) {
      const item = styleObj[key];
      if (!item || !item.enabled) continue;

      styles[item.cssProperty] = item.unit
        ? `${item.value}${item.unit}`
        : item.value;
    }

    return styles;
  }
}
