import { Component, ElementRef, OnDestroy, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Subject, takeUntil } from 'rxjs';
import { ICourse, SortType } from '../../interfaces/models/school.interface';
import { CourseApiService } from '../../services/course-api.service';
import { environment } from '../../config/environment';

@Component({
  selector: 'app-course-search',
  templateUrl: './course-search.component.html',
  styleUrls: ['./course-search.component.scss'],
  standalone: false
})
export class CourseSearchComponent implements OnInit, OnDestroy, AfterViewInit {
  @ViewChild('observerTarget', { static: false }) observerTarget!: ElementRef;

  // ë ˆì´ì•„ì›ƒ ê´€ë ¨
  showLeftPanel = false;
  isProcessing = false;

  // ê²€ìƒ‰ ê´€ë ¨
  searchQuery = '';
  products: ICourse[] = [];
  currentSort: SortType = 'latest';
  isLoading = false;
  hasMore = true;
  currentPage = 1;
  limit = 20;

  private destroy$ = new Subject<void>();
  private observer?: IntersectionObserver;

  // ì±„íŒ… ìœ„ì ¯ìš© organizationId
  chatOrganizationId = environment.chatOrganizationId;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private productApiService: CourseApiService
  ) {}

  ngOnInit(): void {
    // URL ì¿¼ë¦¬ íŒŒë¼ë¯¸í„°ì—ì„œ ê²€ìƒ‰ì–´ ê°€ì ¸ì˜¤ê¸°
    this.route.queryParams
      .pipe(takeUntil(this.destroy$))
      .subscribe(params => {
        const newQuery = params['q'] || '';
        const newSort = (params['sort'] || 'latest') as SortType;
        
        // ê²€ìƒ‰ì–´ë‚˜ ì •ë ¬ì´ ë³€ê²½ë˜ì—ˆì„ ë•Œë§Œ ë¦¬ì…‹
        if (this.searchQuery !== newQuery || this.currentSort !== newSort) {
          this.searchQuery = newQuery;
          this.currentSort = newSort;
          this.resetSearch();
        }
      });
  }

  ngAfterViewInit(): void {
    this.setupInfiniteScroll();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
    this.disconnectObserver();
  }

  /**
   * ë¬´í•œ ìŠ¤í¬ë¡¤ ì„¤ì •
   */
  private setupInfiniteScroll(): void {
    if (this.observerTarget) {
      this.observer = new IntersectionObserver(
        (entries) => {
          if (entries[0].isIntersecting && !this.isLoading && this.hasMore) {
            this.loadMoreProducts();
          }
        },
        { threshold: 0.1 }
      );
      this.observer.observe(this.observerTarget.nativeElement);
    }
  }

  private disconnectObserver(): void {
    if (this.observer) {
      this.observer.disconnect();
    }
  }

  /**
   * ì •ë ¬ ë³€ê²½
   */
  onSortChange(sortType: SortType): void {
    if (this.currentSort !== sortType) {
      this.router.navigate([], {
        relativeTo: this.route,
        queryParams: { q: this.searchQuery, sort: sortType },
        queryParamsHandling: 'merge'
      });
    }
  }

  /**
   * ê²€ìƒ‰ ë¦¬ì…‹
   */
  private resetSearch(): void {
    this.products = [];
    this.currentPage = 1;
    this.hasMore = true;
    this.loadMoreProducts();
  }

  /**
   * ìƒí’ˆ ë” ë¶ˆëŸ¬ì˜¤ê¸°
   */
  private loadMoreProducts(): void {
    if (this.isLoading || !this.searchQuery.trim()) return;

    this.isLoading = true;
    this.isProcessing = true;

    const dto = {
      q: this.searchQuery.trim(),
      sort: this.currentSort,
      page: this.currentPage,
      limit: this.limit
    };


    this.productApiService.searchProducts$(dto)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (response) => {
          this.products = [...this.products, ...response.products];
          this.hasMore = response.hasNext;
          this.currentPage++;
          this.isLoading = false;
          this.isProcessing = false;
        },
        error: (error) => {
          this.isLoading = false;
          this.isProcessing = false;
        }
      });
  }

  /**
   * ìƒí’ˆ í´ë¦­
   */
  onProductClick(productId: string): void {
    this.router.navigate(['/store/product/detail', productId]);
  }

  /**
   * í•™êµ í´ë¦­
   */
  onSchoolClick(event: Event, schoolId: string): void {
    event.stopPropagation();
    this.router.navigate(['/education/school', schoolId]);
  }

  /**
   * ìµœì¢… ê°€ê²© ê³„ì‚°
   */
  getFinalPrice(product: ICourse): number {
    if (product.discount === 0) return product.price;

    if (product.discountType === 'won') {
      return Math.max(0, product.price - product.discount);
    } else {
      return Math.max(0, product.price * (1 - product.discount / 100));
    }
  }

  /**
   * ê°€ê²© í¬ë§·
   */
  formatPrice(price: number): string {
    return new Intl.NumberFormat('ko-KR').format(price);
  }

  /**
   * ì‹ ìƒí’ˆ ì—¬ë¶€
   */
  isNewProduct(product: ICourse): boolean {
    const daysDiff = Math.floor((Date.now() - new Date(product.createdAt).getTime()) / (1000 * 60 * 60 * 24));
    return daysDiff <= 7; // 7ì¼ ë‚´ ë“±ë¡ëœ ìƒí’ˆ
  }

  /**
   * í• ì¸ ì—¬ë¶€
   */
  hasDiscount(product: ICourse): boolean {
    return product.discount > 0;
  }

  /**
   * í• ì¸ìœ¨ ê³„ì‚°
   */
  getDiscountRate(product: ICourse): number {
    if (product.discountType === 'percent') {
      return product.discount;
    } else {
      return Math.floor((product.discount / product.price) * 100);
    }
  }

  /**
   * ë ˆì´ì•„ì›ƒ ë©”ì„œë“œë“¤
   */
  toggleLeftPanel(): void {
    this.showLeftPanel = !this.showLeftPanel;
  }

  closeLeftPanel(): void {
    this.showLeftPanel = false;
  }

  onStoreLeftAction(action: any): void {
    if (action?.action) {
      switch (action.action) {
        case 'home':
          this.router.navigate(['/store/home']);
          break;
        case 'cart':
          this.router.navigate(['/store/cart']);
          break;
      }
    }
    this.closeLeftPanel();
  }

  onStoreMenuChange(menu: string): void {
    switch (menu) {
      case 'í™ˆ':
        this.router.navigate(['/store/home']);
        break;
      case 'ì¥ë°”êµ¬ë‹ˆ':
        this.router.navigate(['/store/cart']);
        break;
    }
  }

  onStoreToolbarAction(action: any): void {
    // í•„ìš”ì‹œ êµ¬í˜„
  }

  onStoreSearch(query: string): void {
    if (query.trim()) {
      this.router.navigate(['/store/search'], { queryParams: { q: query } });
    }
  }
}
