import { Component, Inject, OnInit, OnDestroy } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { MatDialogModule, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatButtonModule } from '@angular/material/button';
import { MatIconModule } from '@angular/material/icon';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatInputModule } from '@angular/material/input';
import { Subject, takeUntil, finalize } from 'rxjs';
import { SubmissionReplyApiService } from '../../services/submission-reply-api.service';
import { ISubmissionDetail, ISubmissionReply } from '../../interfaces/submission-reply.interface';
import { NotificationService } from '../../../../services/notification.service';

export interface SubmissionReplyModalData {
  submissionId: string;
  studentId: string;
  studentName: string;
}

@Component({
  selector: 'app-submission-reply-modal',
  templateUrl: './submission-reply-modal.component.html',
  styleUrls: ['./submission-reply-modal.component.scss'],
  standalone: true,
  imports: [
    CommonModule,
    FormsModule,
    MatDialogModule,
    MatButtonModule,
    MatIconModule,
    MatProgressSpinnerModule,
    MatCheckboxModule,
    MatFormFieldModule,
    MatInputModule,
  ],
})
export class SubmissionReplyModalComponent implements OnInit, OnDestroy {
  private destroy$ = new Subject<void>();

  submission: ISubmissionDetail | null = null;
  loading = false;
  saving = false;
  isComposing = false;
  replyContent = '';
  isRequiringAction = false;
  requiredScore: number | null = null;
  
  // ì±„ì  ê´€ë ¨
  gradeScore: number | null = null;

  constructor(
    public dialogRef: MatDialogRef<SubmissionReplyModalComponent>,
    @Inject(MAT_DIALOG_DATA) public data: SubmissionReplyModalData,
    private submissionReplyService: SubmissionReplyApiService,
    private notificationService: NotificationService,
  ) {}

  ngOnInit(): void {
    this.loadSubmission();
  }

  loadSubmission(): void {
    this.loading = true;
    this.submissionReplyService
      .getSubmissionWithReplies$(this.data.submissionId)
      .pipe(
        finalize(() => (this.loading = false)),
        takeUntil(this.destroy$),
      )
      .subscribe({
        next: (submission) => {
          this.submission = submission;
        },
        error: (error) => {
          console.error('ì œì¶œ ë‚´ìš© ë¡œë“œ ì‹¤íŒ¨:', error);
          this.notificationService.error('ì œì¶œ ë‚´ìš©ì„ ë¶ˆëŸ¬ì˜¬ ìˆ˜ ì—†ìŠµë‹ˆë‹¤');
        },
      });
  }

  submitReply(): void {
    if (!this.replyContent.trim()) {
      this.notificationService.error('í”¼ë“œë°± ë‚´ìš©ì„ ì…ë ¥í•´ì£¼ì„¸ìš”');
      return;
    }

    this.saving = true;
    
    const replyData = {
      replyContent: this.replyContent,
      score: this.gradeScore ?? undefined,
    };

    this.submissionReplyService
      .createReply$(this.data.submissionId, replyData)
      .pipe(
        finalize(() => (this.saving = false)),
        takeUntil(this.destroy$)
      )
      .subscribe({
        next: (result) => {
          this.notificationService.success(
            this.gradeScore !== null ? 'ì±„ì ì´ ì™„ë£Œë˜ì—ˆìŠµë‹ˆë‹¤' : 'í”¼ë“œë°±ì´ ì €ì¥ë˜ì—ˆìŠµë‹ˆë‹¤'
          );
          this.replyContent = '';
          this.gradeScore = null;
          this.loadSubmission();
        },
        error: (error) => {
          console.error('ì €ì¥ ì‹¤íŒ¨:', error);
          this.notificationService.error('ì €ì¥ì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤');
        },
      });
  }

  onClose(): void {
    this.dialogRef.close();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
