import { Component, Inject, OnInit, OnDestroy } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { IUser } from '../../interfaces/auth.interface';
import { MissionApiService } from '../../services/mission-api.service';
import { SubmissionReplyApiService } from '../../services/submission-reply-api.service';
import { SubmissionReplyModalComponent } from '../submission-reply-modal/submission-reply-modal.component';
import { Subject, takeUntil } from 'rxjs';

export interface UserDetailDialogData {
  user: IUser;
  isMe: boolean;
}

@Component({
  selector: 'app-user-detail-dialog',
  templateUrl: './user-detail-dialog.component.html',
  styleUrls: ['./user-detail-dialog.component.scss'],
  standalone: false
})
export class UserDetailDialogComponent implements OnInit, OnDestroy {
  user: IUser;
  isMe: boolean;
  activeTab: 'info' | 'submissions' = 'info'; // íƒ­ ê´€ë¦¬
  
  // ë¯¸ì…˜ ì œì¶œ ë°ì´í„°
  submissions: any[] = [];
  loadingSubmissions = false;
  
  private destroy$ = new Subject<void>();

  constructor(
    public dialogRef: MatDialogRef<UserDetailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: UserDetailDialogData,
    private missionApiService: MissionApiService,
    private submissionReplyService: SubmissionReplyApiService,
    private dialog: MatDialog
  ) {
    this.user = data.user;
    this.isMe = data.isMe;
  }

  ngOnInit(): void {
    // ì´ˆê¸°í™” ì‹œ í•„ìš”í•œ ì‘ì—…
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  /**
   * ë‹¤ì´ì–¼ë¡œê·¸ ë‹«ê¸°
   */
  onClose(): void {
    this.dialogRef.close();
  }

  /**
   * ìˆ˜ì • ëª¨ë“œë¡œ ì „í™˜
   */
  onEdit(): void {
    this.dialogRef.close({ action: 'edit', user: this.user });
  }

  /**
   * ìƒíƒœ ë³€ê²½
   */
  onStatusChange(status: 'ACTIVE' | 'INACTIVE'): void {
    this.dialogRef.close({ action: 'status', status, user: this.user });
  }

  /**
   * ì‚­ì œ
   */
  onDelete(): void {
    this.dialogRef.close({ action: 'delete', user: this.user });
  }

  /**
   * ì—­í•  í‘œì‹œëª…
   */
  getRoleName(role: any): string {
    if (typeof role === 'object' && role !== null) {
      return role.roleName || role.name || String(role.roleId || role);
    }
    
    const roleMap: { [key: string]: string } = {
      'STUDENT': 'í•™ìƒ',
      'TEACHER': 'êµì‚¬',
      'ADMIN': 'ê´€ë¦¬ì',
      'SUPER_ADMIN': 'ìµœê³  ê´€ë¦¬ì',
      '100': 'í•™ìƒ',
      '200': 'êµì‚¬',
      '300': 'ê´€ë¦¬ì',
      '400': 'ìµœê³  ê´€ë¦¬ì'
    };
    
    return roleMap[String(role)] || String(role);
  }

  /**
   * ì—­í•  í´ë˜ìŠ¤
   */
  getRoleClass(role: any): string {
    let roleValue = role;
    
    if (typeof role === 'object' && role !== null) {
      roleValue = role.roleId || role.roleName || role.name || role.value;
    }
    
    const roleStr = String(roleValue).toUpperCase();
    
    if (roleStr.includes('STUDENT') || roleStr === '100') return 'role-student';
    if (roleStr.includes('TEACHER') || roleStr === '200') return 'role-teacher';
    if (roleStr.includes('ADMIN') || roleStr === '300' || roleStr === '400') return 'role-admin';
    
    return 'role-student';
  }

  /**
   * ìƒíƒœ í…ìŠ¤íŠ¸
   */
  getStatusText(status: string): string {
    const statusMap: { [key: string]: string } = {
      'ACTIVE': 'í™œì„±',
      'INACTIVE': 'ë¹„í™œì„±',
      'SUSPENDED': 'ì •ì§€',
      'DELETED': 'ì‚­ì œë¨'
    };
    return statusMap[status] || status;
  }

  /**
   * ìƒíƒœ í´ë˜ìŠ¤
   */
  getStatusClass(status: string): string {
    return `status-${status.toLowerCase()}`;
  }

  /**
   * ë‚ ì§œ í¬ë§·
   */
  formatDate(date: any): string {
    if (!date) return '-';
    
    try {
      const d = new Date(date);
      return d.toLocaleDateString('ko-KR', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit'
      });
    } catch {
      return String(date);
    }
  }

  /**
   * roleId ê°€ì ¸ì˜¤ê¸°
   */
  getRoleId(): number {
    if (typeof this.user.role === 'object' && this.user.role !== null) {
      return (this.user.role as any).roleId || 0;
    }
    if (typeof this.user.role === 'number') {
      return this.user.role;
    }
    return 0;
  }

  /**
   * íƒ­ ë³€ê²½
   */
  onTabChange(tab: 'info' | 'submissions'): void {
    this.activeTab = tab;
    
    // ë¯¸ì…˜ ì œì¶œ íƒ­ í´ë¦­ ì‹œ ë°ì´í„° ë¡œë“œ
    if (tab === 'submissions' && this.submissions.length === 0) {
      this.loadSubmissions();
    }
  }

  /**
   * í•™ìƒì˜ ë¯¸ì…˜ ì œì¶œ ëª©ë¡ ì¡°íšŒ
   */
  loadSubmissions(): void {
    if (this.user.role !== 'STUDENT') {
      return; // í•™ìƒì´ ì•„ë‹Œ ê²½ìš° return
    }
    
    this.loadingSubmissions = true;
    // ê¸°ì¡´ MissionApiService ì‚¬ìš© (ë°ì´í„°ê°€ ìˆëŠ” ê³³)
    this.missionApiService.getStudentSubmissions$(this.user.id)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (submissions) => {
          console.log('ğŸ“š í•™ìƒ ë¯¸ì…˜ ì œì¶œ ëª©ë¡:', submissions);
          this.submissions = submissions || [];
          this.loadingSubmissions = false;
        },
        error: (error) => {
          console.error('âŒ ë¯¸ì…˜ ì œì¶œ ëª©ë¡ ì¡°íšŒ ì‹¤íŒ¨:', error);
          this.submissions = [];
          this.loadingSubmissions = false;
        }
      });
  }

  /**
   * ë‹µì¥ ëª¨ë‹¬ ì—´ê¸°
   */
  openReplyModal(submission: any): void {
    const dialogRef = this.dialog.open(SubmissionReplyModalComponent, {
      width: '90%',
      maxWidth: '1000px',
      maxHeight: '90vh',
      data: {
        submissionId: submission.id,
        studentId: this.user.id,
        studentName: this.user.name,
      }
    });

    dialogRef.afterClosed().subscribe((result) => {
      if (result) {
        // ë‹µì¥ ì €ì¥ í›„ ëª©ë¡ ìƒˆë¡œê³ ì¹¨
        this.loadSubmissions();
      }
    });
  }
}
