  // AI ì‘ë‹µì„ íŒŒì‹±í•˜ì—¬ í¼ì— ì±„ìš°ê¸°
  private parseAndFillForm(generatedText: string): void {
    try {
      // JSON ë¶€ë¶„ë§Œ ì¶”ì¶œ (```json ... ``` ë˜ëŠ” { ... } í˜•ì‹)
      let jsonText = generatedText;
      
      // ë§ˆí¬ë‹¤ìš´ ì½”ë“œ ë¸”ë¡ ì œê±°
      const jsonMatch = generatedText.match(/```json\s*([\s\S]*?)\s*```/);
      if (jsonMatch) {
        jsonText = jsonMatch[1];
      } else {
        // ì¤‘ê´„í˜¸ ì‚¬ì´ì˜ JSON ì¶”ì¶œ
        const bracketMatch = generatedText.match(/\{[\s\S]*\}/);
        if (bracketMatch) {
          jsonText = bracketMatch[0];
        }
      }

      const brandData = JSON.parse(jsonText);

      // í¼ì— ë°ì´í„° ì±„ìš°ê¸°
      if (brandData.name) this.storeForm.patchValue({ name: brandData.name });
      if (brandData.description) this.storeForm.patchValue({ description: brandData.description });
      if (brandData.businessRegistrationNumber) this.storeForm.patchValue({ businessRegistrationNumber: brandData.businessRegistrationNumber });
      if (brandData.zipcode) this.storeForm.patchValue({ zipcode: brandData.zipcode });
      if (brandData.address) this.storeForm.patchValue({ address: brandData.address });
      if (brandData.addressDetail) this.storeForm.patchValue({ addressDetail: brandData.addressDetail });
      if (brandData.studentServiceNumber) this.storeForm.patchValue({ studentServiceNumber: brandData.studentServiceNumber });
      if (brandData.eCommerceRegistrationNumber) this.storeForm.patchValue({ eCommerceRegistrationNumber: brandData.eCommerceRegistrationNumber });
      if (brandData.email) this.storeForm.patchValue({ email: brandData.email });

      this.notification.success('AIê°€ ë¸Œëœë“œ ì •ë³´ë¥¼ ìƒì„±í–ˆìŠµë‹ˆë‹¤. í™•ì¸ í›„ ìˆ˜ì •í•´ì£¼ì„¸ìš”.');
    } catch (error) {
      this.notification.error('AI ì‘ë‹µì„ ì²˜ë¦¬í•˜ëŠ” ì¤‘ ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤.');
    }
  }

