        return 'ì‚¬ì—…ìë“±ë¡ë²ˆí˜¸ í˜•ì‹ì´ ì˜¬ë°”ë¥´ì§€ ì•ŠìŠµë‹ˆë‹¤. (ì˜ˆ: 123-45-67890)';
