  /**
   * í•™êµì˜ ê³¼ëª© ëª©ë¡ ë¡œë“œ
   */
  private loadSchoolCourses(): void {
    if (!this.schoolId) return;

    this.loadingProducts = true;
    
    // ìµœì†Œ ë¡œë”© ì‹œê°„ ë³´ì¥ (shimmer íš¨ê³¼ë¥¼ ë³´ì—¬ì£¼ê¸° ìœ„í•´)
    const minLoadingTime = 300; // 0.3ì´ˆ
    const startTime = Date.now();
    
    this.productApiService.getCoursesBySchool$(this.schoolId)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (products) => {
          const elapsedTime = Date.now() - startTime;
          const remainingTime = Math.max(0, minLoadingTime - elapsedTime);
          
          setTimeout(() => {
            // isVisibleì´ trueì¸ ìƒí’ˆë§Œ í•„í„°ë§
            let filteredProducts = products.filter(product => product.isVisible === true);
            
            // Dream Designì„ ë§¨ ìœ„ë¡œ ì •ë ¬
            filteredProducts = this.sortProductsWithDreamDesignFirst(filteredProducts);
            
            this.products = filteredProducts;
            this.loadingProducts = false;
            console.log(`í•™êµ ${this.school?.name}ì˜ ë…¸ì¶œ ê³¼ëª© ${this.products.length}ê°œ ë¡œë“œë¨ (ì „ì²´: ${products.length}ê°œ)`);
          }, remainingTime);
        },
        error: (error) => {
          const elapsedTime = Date.now() - startTime;
          const remainingTime = Math.max(0, minLoadingTime - elapsedTime);
          
          setTimeout(() => {
            this.products = [];
            this.loadingProducts = false;
          }, remainingTime);
        }
      });
  }

  /**
   * ì…êµ ìƒíƒœ í™•ì¸ - í•™ìƒì´ ì´ë¯¸ ì…êµí•œ í•™êµì¸ì§€ í™•ì¸
   */
  private checkEnrollmentStatus(): void {
    if (!this.schoolId) return;
    
    // í•™ìƒ ëª¨ë“œì´ê³  ë¡œê·¸ì¸í•œ ê²½ìš°ì—ë§Œ í™•ì¸
    const currentMode = this.authModeService.getCurrentMode();
    const isStudentAuthenticated = this.studentAuthService.isAuthenticated();
    
    if (currentMode !== 'STUDENT' || !isStudentAuthenticated) {
      this.isEnrolled = false;
      return;
    }

    // í•™ìƒì´ ì…êµí•œ í•™êµ ëª©ë¡ í™•ì¸
    this.enrollmentApiService.getMySchools$()
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (schools) => {
          // í˜„ì¬ í•™êµê°€ ì…êµí•œ í•™êµ ëª©ë¡ì— ìˆëŠ”ì§€ í™•ì¸
          this.isEnrolled = schools.some(school => school.id === this.schoolId);
          console.log(`í•™êµ ${this.school?.name} ì…êµ ì—¬ë¶€:`, this.isEnrolled);
        },
        error: (error) => {
          console.error('ì…êµ ìƒíƒœ í™•ì¸ ì‹¤íŒ¨:', error);
          this.isEnrolled = false;
        }
      });
  }

  /**
   * Dream Designì„ ë§¨ ìœ„ë¡œ ì •ë ¬
   */
  private sortProductsWithDreamDesignFirst(products: ICourse[]): ICourse[] {
    const dreamDesignIndex = products.findIndex(p => p.name === 'Dream Design');
    
    if (dreamDesignIndex === -1) {
      // Dream Designì´ ì—†ìœ¼ë©´ ì›ë˜ ìˆœì„œëŒ€ë¡œ ë°˜í™˜
      return products;
    }
    
    if (dreamDesignIndex === 0) {
      // ì´ë¯¸ ì²« ë²ˆì§¸ë©´ ê·¸ëŒ€ë¡œ ë°˜í™˜
      return products;
    }
    
    // Dream Designì„ ë¶„ë¦¬í•˜ê³  ë§¨ ì•ì— ë°°ì¹˜
    const dreamDesign = products[dreamDesignIndex];
    const others = products.filter((_, index) => index !== dreamDesignIndex);
    
    return [dreamDesign, ...others];
  }

