  /**
   * ê°€ê²© í¬ë§·íŒ…
   */
  formatPrice(price: number | undefined): string {
    if (!price) return 'ê°€ê²© ë¯¸ì •';
    return new Intl.NumberFormat('ko-KR', {
      style: 'currency',
      currency: 'KRW'
    }).format(price);
  }

  /**
   * ìƒí’ˆ ì„¤ëª… ì¶”ì¶œ (JSON íŒŒì‹±)
   */
  getProductDescription(product: ICourse): string {
    if (!product.detail) return 'ìƒí’ˆ ì„¤ëª…ì´ ì—†ìŠµë‹ˆë‹¤.';
    
    try {
      const data = JSON.parse(product.detail);
      
      // Text ìœ„ì ¯ì—ì„œ ì²« ë²ˆì§¸ í…ìŠ¤íŠ¸ ì½˜í…ì¸  ì¶”ì¶œ
      if (data.sections && Array.isArray(data.sections)) {
        for (const section of data.sections) {
          if (section.rows && Array.isArray(section.rows)) {
            for (const row of section.rows) {
              if (row.columns && Array.isArray(row.columns)) {
                for (const column of row.columns) {
                  if (column.widget && column.widget.widgetType === 'text') {
                    const content = column.widget.properties?.content || '';
                    // HTML íƒœê·¸ ì œê±°í•˜ê³  ì²« 100ìë§Œ ë°˜í™˜
                    const textOnly = content.replace(/<[^>]*>/g, '').trim();
                    return textOnly.substring(0, 100) + (textOnly.length > 100 ? '...' : '');
                  }
                }
              }
            }
          }
        }
      }
      
      return 'ìƒí’ˆ ì„¤ëª…ì´ ì—†ìŠµë‹ˆë‹¤.';
    } catch (e) {
      // JSON íŒŒì‹± ì‹¤íŒ¨ ì‹œ ì›ë³¸ ë¬¸ìì—´ì˜ ì²« 100ì ë°˜í™˜
      return product.detail.substring(0, 100) + '...';
    }
  }

  /**
   * í• ì¸ìœ¨ ê³„ì‚°
   */
  getDiscountRate(product: ICourse): number {
    if (!product) return 0;
    const discount = product.discount || 0;
    if (discount === 0) return 0;
    
    if (product.discountType === 'percent') {
      return discount;
    } else if (product.discountType === 'won' && product.price > 0) {
      return Math.round((discount / product.price) * 100);
    }
    return 0;
  }

  /**
   * ìµœì¢… ê°€ê²© ê³„ì‚° (í• ì¸ ì ìš©)
   */
  getFinalPrice(product: ICourse): number {
    if (!product) return 0;
    const discount = product.discount || 0;
    if (discount === 0) return product.price;
    
    if (product.discountType === 'won') {
      return Math.max(0, product.price - discount);
    } else if (product.discountType === 'percent') {
      return Math.max(0, product.price - (product.price * discount / 100));
    }
    return product.price;
  }

