import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../config/environment';
import {
  IIntroductionSurvey,
  ICreateIntroductionSurvey,
  IIntroductionSurveyResponse,
  ISurveyCompletionRate,
} from '../interfaces/introduction-survey.interface';

@Injectable({
  providedIn: 'root',
})
export class IntroductionSurveyService {
  private apiUrl = `${environment.apiUrl}/education/courses`;

  constructor(private http: HttpClient) {}

  /**
   * Get student's introduction survey
   * Returns existing survey or creates blank if doesn't exist
   */
  getStudentSurvey$(courseId: string): Observable<IIntroductionSurvey> {
    return this.http.get<IIntroductionSurvey>(
      `${this.apiUrl}/${courseId}/introduction-survey/me`
    );
  }

  /**
   * Submit completed survey
   */
  submitSurvey$(
    courseId: string,
    survey: ICreateIntroductionSurvey
  ): Observable<IIntroductionSurvey> {
    return this.http.post<IIntroductionSurvey>(
      `${this.apiUrl}/${courseId}/introduction-survey/me/submit`,
      survey
    );
  }

  /**
   * Auto-save survey draft (partial submission)
   */
  saveDraft$(
    courseId: string,
    survey: Partial<ICreateIntroductionSurvey>
  ): Observable<IIntroductionSurvey> {
    return this.http.put<IIntroductionSurvey>(
      `${this.apiUrl}/${courseId}/introduction-survey/me/draft`,
      survey
    );
  }

  /**
   * Check if student has completed the survey
   */
  checkSurveyStatus$(
    courseId: string,
    studentId?: string
  ): Observable<{ status: string; submittedAt?: Date }> {
    const params = studentId ? `?studentId=${studentId}` : '';
    return this.http.get<{ status: string; submittedAt?: Date }>(
      `${this.apiUrl}/${courseId}/introduction-survey/me/status${params}`
    );
  }

  /**
   * Get all survey responses for a course (teacher view)
   */
  getCourseSurveyResponses$(
    courseId: string
  ): Observable<IIntroductionSurveyResponse[]> {
    return this.http.get<IIntroductionSurveyResponse[]>(
      `${this.apiUrl}/${courseId}/introduction-survey/responses`
    );
  }

  /**
   * Get specific student's survey response (teacher view)
   */
  getStudentSurveyResponse$(
    courseId: string,
    studentId: string
  ): Observable<IIntroductionSurveyResponse> {
    return this.http.get<IIntroductionSurveyResponse>(
      `${this.apiUrl}/${courseId}/introduction-survey/responses/${studentId}`
    );
  }

  /**
   * Get survey completion statistics for course
   */
  getSurveyCompletionRate$(
    courseId: string
  ): Observable<ISurveyCompletionRate> {
    return this.http.get<ISurveyCompletionRate>(
      `${this.apiUrl}/${courseId}/introduction-survey/completion-rate`
    );
  }
}
