import { Component, OnInit, OnDestroy } from '@angular/core';
import { NotificationService } from '../../../../services/notification.service';
import { Router } from '@angular/router';
import { Subject, takeUntil } from 'rxjs';
import { ICart, ICartItem, ISchool } from '../../interfaces/models/school.interface';
import { CartService } from '../../services/cart.service';
import { StudentAuthService } from '../../services/student-auth.service';
import { environment } from '../../config/environment';
import { ConfirmationDialogService } from '../../../../shared/services/confirmation-dialog.service';

@Component({
  selector: 'app-shopping-cart',
  templateUrl: './shopping-cart.component.html',
  styleUrls: ['./shopping-cart.component.scss'],
  standalone: false
})
export class ShoppingCartComponent implements OnInit, OnDestroy {
  private destroy$ = new Subject<void>();

  // ë ˆì´ì•„ì›ƒ ê´€ë ¨
  showLeftPanel = false;
  isProcessing = false;

  // ì¥ë°”êµ¬ë‹ˆ ìƒíƒœ
  cart?: ICart;
  cartItems: ICartItem[] = [];
  selectedCartItems = new Set<string>(); // ì„ íƒëœ ì•„ì´í…œë“¤ì˜ IDë¥¼ ì €ì¥

  // ê²€ìƒ‰
  searchQuery = '';

  // ê³„ì‚°ëœ ì†ì„±ë“¤
  get totalItemCount(): number {
    return this.cartItems.reduce((total, item) => total + item.quantity, 0);
  }

  get selectedItemCount(): number {
    return Array.from(this.selectedCartItems).reduce((total, itemId) => {
      const item = this.cartItems.find(i => i.id === itemId);
      return total + (item ? item.quantity : 0);
    }, 0);
  }

  get totalPrice(): number {
    return this.cartItems.reduce((total, item) => {
      const discountedPrice = this.getDiscountedPrice(item);
      return total + (discountedPrice * item.quantity) + item.product.shippingFee;
    }, 0);
  }

  get selectedTotalPrice(): number {
    return Array.from(this.selectedCartItems).reduce((total, itemId) => {
      const item = this.cartItems.find(i => i.id === itemId);
      if (!item) return total;
      const discountedPrice = this.getDiscountedPrice(item);
      return total + (discountedPrice * item.quantity) + item.product.shippingFee;
    }, 0);
  }

  get selectedTotalOriginalPrice(): number {
    return Array.from(this.selectedCartItems).reduce((total, itemId) => {
      const item = this.cartItems.find(i => i.id === itemId);
      if (!item) return total;
      return total + (item.product.price * item.quantity);
    }, 0);
  }

  get selectedTotalShippingFee(): number {
    return Array.from(this.selectedCartItems).reduce((total, itemId) => {
      const item = this.cartItems.find(i => i.id === itemId);
      if (!item) return total;
      return total + item.product.shippingFee;
    }, 0);
  }

  get selectedTotalDiscountAmount(): number {
    return this.selectedTotalOriginalPrice - this.selectedTotalPrice + this.selectedTotalShippingFee;
  }

  // ì±„íŒ… ìœ„ì ¯ìš© organizationId
  chatOrganizationId = environment.chatOrganizationId;

  constructor(
    private router: Router,
    private cartService: CartService,
    private studentAuthService: StudentAuthService,
    private notification: NotificationService,
    private confirmationDialog: ConfirmationDialogService
  ) {}

  ngOnInit(): void {
    this.loadCart();
    this.observeCart();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  /**
   * ì¥ë°”êµ¬ë‹ˆ ë¡œë“œ
   */
  private loadCart(): void {
    this.studentAuthService.getUser$()
      .pipe(takeUntil(this.destroy$))
      .subscribe(user => {
        if (user?.id) {
          this.cartService.fetchCart(user.id);
        } else {
          this.notification.show('ë¡œê·¸ì¸ì´ í•„ìš”í•©ë‹ˆë‹¤.');
          this.router.navigate(['/education/login']);
        }
      });
  }

  /**
   * ì¥ë°”êµ¬ë‹ˆ ìƒíƒœ ê´€ì°°
   */
  private observeCart(): void {
    this.cartService.cart$
      .pipe(takeUntil(this.destroy$))
      .subscribe((cart) => {
        if (!cart) {
          this.cart = undefined;
          this.cartItems = [];
          return;
        }

        this.cart = cart;

        if (!cart.items || cart.items.length === 0) {
          this.cartItems = [];
          this.selectedCartItems.clear();
          return;
        }

        // ì•„ì´í…œ ë³µì‚¬ (URL ì²˜ë¦¬ëŠ” getImageUrl() ë©”ì„œë“œì—ì„œ ìˆ˜í–‰)
        this.cartItems = [...cart.items];

        // ê¸°ì¡´ ì„ íƒ ìƒíƒœ ìœ ì§€
        const previousSelectedItems = new Set(this.selectedCartItems);
        this.selectedCartItems.clear();

        this.cartItems.forEach((item) => {
          // ì´ì „ì— ì„ íƒë˜ì–´ ìˆë˜ ì•„ì´í…œì´ê±°ë‚˜, ì²˜ìŒ ë¡œë“œë˜ëŠ” ê²½ìš° ëª¨ë“  ì•„ì´í…œ ì„ íƒ
          if (previousSelectedItems.has(item.id) || previousSelectedItems.size === 0) {
            this.selectedCartItems.add(item.id);
          }
        });
      });
  }

  /* ---------- ì´ë²¤íŠ¸ í•¸ë“¤ëŸ¬ ---------- */

  onHomeClick(): void {
    this.router.navigate(['/education/home']);
  }

  onSearch(): void {
    const q = this.searchQuery.trim();
    if (!q) return;
    // TODO: ê²€ìƒ‰ ê²°ê³¼ í˜ì´ì§€ë¡œ ì´ë™
  }

  onCartClick(): void {
    // í˜„ì¬ í˜ì´ì§€ê°€ ì¥ë°”êµ¬ë‹ˆ
  }

  onUserClick(): void {
    this.router.navigate(['/education/login']);
  }

  onItemClick(item: ICartItem): void {
    this.router.navigate(['/education/course', item.product.id]);
  }

  onSchoolClick(school: ISchool): void {
    if (!school) return;
    this.router.navigate(['/education/school', school.id]);
  }

  /* ---------- ì²´í¬ë°•ìŠ¤ ê´€ë ¨ ---------- */

  isItemSelected(item: ICartItem): boolean {
    return this.selectedCartItems.has(item.id);
  }

  toggleItemSelection(item: ICartItem): void {
    if (this.selectedCartItems.has(item.id)) {
      this.selectedCartItems.delete(item.id);
    } else {
      this.selectedCartItems.add(item.id);
    }
  }

  toggleAllSelection(): void {
    if (this.isAllSelected()) {
      this.selectedCartItems.clear();
    } else {
      this.cartItems.forEach((item) => this.selectedCartItems.add(item.id));
    }
  }

  isAllSelected(): boolean {
    return this.cartItems.length > 0 && this.selectedCartItems.size === this.cartItems.length;
  }

  isIndeterminate(): boolean {
    return this.selectedCartItems.size > 0 && this.selectedCartItems.size < this.cartItems.length;
  }

  /* ---------- ê°€ê²© ê³„ì‚° ---------- */

  getDiscountedPrice(item: ICartItem): number {
    if (item.product.discount === 0) {
      return item.product.price;
    }

    if (item.product.discountType === 'won') {
      return Math.max(0, item.product.price - item.product.discount);
    } else {
      return Math.max(0, item.product.price * (1 - item.product.discount / 100));
    }
  }

  getDiscountAmount(item: ICartItem): number {
    return item.product.price - this.getDiscountedPrice(item);
  }

  hasDiscount(item: ICartItem): boolean {
    return item.product.discount > 0;
  }

  /* ---------- ìˆ˜ëŸ‰ ì¡°ì ˆ ---------- */

  increaseQuantity(item: ICartItem): void {
    this.cartService.queueUpdateItemQuantity({
      productId: item.product.id,
      quantity: item.quantity + 1
    });
  }

  decreaseQuantity(item: ICartItem): void {
    if (item.quantity > 1) {
      this.cartService.queueUpdateItemQuantity({
        productId: item.product.id,
        quantity: item.quantity - 1
      });
    }
  }

  updateQuantity(item: ICartItem, event: Event): void {
    const target = event.target as HTMLInputElement;
    const newQuantity = parseInt(target.value, 10);

    if (newQuantity && newQuantity >= 1) {
      this.cartService.queueUpdateItemQuantity({
        productId: item.product.id,
        quantity: newQuantity
      });
    } else {
      target.value = item.quantity.toString();
    }
  }

  /* ---------- ì•„ì´í…œ ì‚­ì œ ---------- */

  removeItem(item: ICartItem): void {
    this.confirmationDialog.confirmDelete(item.product.name).subscribe(result => {
      if (result) {
        this.studentAuthService.getUser$()
          .pipe(takeUntil(this.destroy$))
          .subscribe(user => {
            if (!user?.id) {
              this.notification.show('ë¡œê·¸ì¸ì´ í•„ìš”í•©ë‹ˆë‹¤.');
              return;
            }

            this.cartService.deleteItem(item.id, user.id).subscribe({
              next: (cart) => {
                if (cart) {
                  this.cartService.cart$.next(cart);
                }
              },
              error: (error) => {
                this.notification.show('ìƒí’ˆ ì‚­ì œì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.');
              }
            });
          });
      }
    });
  }

  removeSelectedItems(): void {
    if (this.selectedCartItems.size === 0) {
      this.notification.show('ì‚­ì œí•  ìƒí’ˆì„ ì„ íƒí•´ì£¼ì„¸ìš”.');
      return;
    }

    this.confirmationDialog.confirm('ì„ íƒí•œ ìƒí’ˆì„ ì‚­ì œí•˜ì‹œê² ìŠµë‹ˆê¹Œ?', 'ìƒí’ˆ ì‚­ì œ', 'ì‚­ì œ', 'ì·¨ì†Œ', 'warn')
      .subscribe(result => {
        if (result) {
          const selectedIds = Array.from(this.selectedCartItems);
          selectedIds.forEach((id) => {
            const item = this.cartItems.find((item) => item.id === id);
            if (item) {
              this.removeItem(item);
            }
          });
        }
      });
  }

  /* ---------- ê²°ì œ ì§„í–‰ ---------- */

  proceedToCheckout(): void {
    if (this.cartItems.length === 0) {
      this.notification.show('ì¥ë°”êµ¬ë‹ˆê°€ ë¹„ì–´ìˆìŠµë‹ˆë‹¤.');
      return;
    }

    if (this.selectedCartItems.size === 0) {
      this.notification.show('ê²°ì œí•  ìƒí’ˆì„ ì„ íƒí•´ì£¼ì„¸ìš”.');
      return;
    }

    if (!this.cart) {
      return;
    }

    // ì„ íƒëœ ì•„ì´í…œë“¤ë§Œ í•„í„°ë§
    const selectedItems = this.cart.items.filter((item) => this.selectedCartItems.has(item.id));
    const selectedCart: ICart = {
      ...this.cart,
      items: selectedItems
    };

    // ê²°ì œ í˜ì´ì§€ë¡œ ì´ë™ (ì„ íƒëœ ìƒí’ˆ ì •ë³´ë¥¼ stateë¡œ ì „ë‹¬)
    this.router.navigate(['/education/checkout'], {
      state: { cart: selectedCart, type: 'cart' }
    });
  }

  onContinueShopping(): void {
    this.router.navigate(['/education/home']);
  }

  /* ---------- ìœ í‹¸ë¦¬í‹° ---------- */

  formatPrice(price: number): string {
    return new Intl.NumberFormat('ko-KR').format(price);
  }

  getDefaultThumbnail(): string {
    return './assets/svg/icons/loworld-icon.svg';
  }

  /**
   * ì´ë¯¸ì§€ URLì„ ì ˆëŒ€ ê²½ë¡œë¡œ ë³€í™˜
   */
  getImageUrl(url?: string | null): string {
    if (!url) return this.getDefaultThumbnail();
    if (url.startsWith('http://') || url.startsWith('https://')) return url;
    return environment.baseSyncUrl + url;
  }

  /**
   * ì¢Œì¸¡ íŒ¨ë„ í† ê¸€
   */
  toggleLeftPanel(): void {
    this.showLeftPanel = !this.showLeftPanel;
  }

  /**
   * ì¢Œì¸¡ íŒ¨ë„ ë‹«ê¸°
   */
  closeLeftPanel(): void {
    this.showLeftPanel = false;
  }

  /**
   * ì¢Œì¸¡ ë©”ë‰´ ì•¡ì…˜ ì²˜ë¦¬
   */
  onStoreLeftAction(action: string): void {
    
    switch (action) {
      case 'home':
        this.router.navigate(['/store/home']);
        break;
      case 'cart':
        // ì´ë¯¸ ì¥ë°”êµ¬ë‹ˆ í˜ì´ì§€ì— ìˆìŒ
        break;
      case 'orders':
        // TODO: ì£¼ë¬¸ ë‚´ì—­ í˜ì´ì§€ë¡œ ì´ë™
        this.notification.show('ì£¼ë¬¸ ë‚´ì—­ í˜ì´ì§€ëŠ” ì¤€ë¹„ ì¤‘ì…ë‹ˆë‹¤.');
        break;
      case 'profile':
        // TODO: í”„ë¡œí•„ í˜ì´ì§€ë¡œ ì´ë™
        this.notification.show('í”„ë¡œí•„ í˜ì´ì§€ëŠ” ì¤€ë¹„ ì¤‘ì…ë‹ˆë‹¤.');
        break;
      default:
    }
    
    this.closeLeftPanel();
  }

  /**
   * ë©”ë‰´ ë³€ê²½ ì²˜ë¦¬ (í—¤ë” ë©”ë‰´ í´ë¦­)
   */
  onStoreMenuChange(menu: string): void {
    switch (menu) {
      case 'í™ˆ':
        this.router.navigate(['/store/home']);
        break;
      case 'ë¸Œëœë“œ ëª©ë¡':
        this.router.navigate(['/store/home']);
        break;
      case 'ì¥ë°”êµ¬ë‹ˆ':
        // ì´ë¯¸ ì¥ë°”êµ¬ë‹ˆ í˜ì´ì§€ì— ìˆìŒ
        break;
      default:
    }
  }

  /**
   * íˆ´ë°” ì•¡ì…˜ ì²˜ë¦¬
   */
  onStoreToolbarAction(action: any): void {
    switch (action.data?.action) {
      case 'refresh':
        this.loadCart();
        break;
      default:
    }
  }

  /**
   * ê²€ìƒ‰ ì²˜ë¦¬
   */
  onStoreSearch(query: string): void {
    if (query.trim()) {
      this.router.navigate(['/store/home'], { queryParams: { search: query } });
    }
  }
}
