import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule } from '@angular/forms';
import { MatButtonModule } from '@angular/material/button';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatInputModule } from '@angular/material/input';
import { MatSelectModule } from '@angular/material/select';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { MatIconModule } from '@angular/material/icon';
import { IntroductionSurveyService } from '../../services/introduction-survey.service';
import {
  IIntroductionSurvey,
  ExperienceLevel,
  IntroductionSurveyStatus,
} from '../../interfaces/introduction-survey.interface';

@Component({
  selector: 'app-introduction-survey',
  templateUrl: './introduction-survey.component.html',
  styleUrls: ['./introduction-survey.component.scss'],
  standalone: true,
  imports: [
    CommonModule,
    ReactiveFormsModule,
    MatButtonModule,
    MatFormFieldModule,
    MatInputModule,
    MatSelectModule,
    MatProgressSpinnerModule,
    MatIconModule,
  ],
})
export class IntroductionSurveyComponent implements OnInit {
  surveyForm: FormGroup;
  loading = false;
  submitting = false;
  survey: IIntroductionSurvey | null = null;
  experienceLevels = Object.values(ExperienceLevel);
  motivationScale = [1, 2, 3, 4, 5];

  constructor(
    private fb: FormBuilder,
    private surveyService: IntroductionSurveyService,
    public dialogRef: MatDialogRef<IntroductionSurveyComponent>,
    @Inject(MAT_DIALOG_DATA) public data: { courseId: string }
  ) {
    this.surveyForm = this.fb.group({
      dreamDescription: ['', [Validators.required, Validators.minLength(10)]],
      wantToMake: ['', [Validators.required, Validators.minLength(10)]],
      selfIntroduction: ['', [Validators.required, Validators.minLength(20)]],
      motivationScore: ['', Validators.required],
      experienceLevel: ['', Validators.required],
      expectation: ['', [Validators.required, Validators.minLength(10)]],
      specialRequest: [''],
    });
  }

  ngOnInit(): void {
    this.loadSurvey();
  }

  /**
   * Load existing survey or create blank
   */
  loadSurvey(): void {
    this.loading = true;
    this.surveyService.getStudentSurvey$(this.data.courseId).subscribe({
      next: (survey) => {
        this.survey = survey;
        if (
          survey.status !== IntroductionSurveyStatus.PENDING ||
          survey.dreamDescription
        ) {
          this.surveyForm.patchValue({
            dreamDescription: survey.dreamDescription || '',
            wantToMake: survey.wantToMake || '',
            selfIntroduction: survey.selfIntroduction || '',
            motivationScore: survey.motivationScore || '',
            experienceLevel: survey.experienceLevel || '',
            expectation: survey.expectation || '',
            specialRequest: survey.specialRequest || '',
          });
        }
        this.loading = false;
      },
      error: (err) => {
        console.error('Failed to load survey:', err);
        this.loading = false;
      },
    });
  }

  /**
   * Auto-save draft when user stops typing
   */
  autosaveDraft(): void {
    if (this.surveyForm.valid) {
      this.surveyService
        .saveDraft$(this.data.courseId, this.surveyForm.value)
        .subscribe({
          next: () => {
            console.log('Draft auto-saved');
          },
          error: (err) => {
            console.error('Failed to save draft:', err);
          },
        });
    }
  }

  /**
   * Submit completed survey
   */
  submitSurvey(): void {
    if (!this.surveyForm.valid) {
      alert('ëª¨ë“  í•„ë“œë¥¼ ì˜¬ë°”ë¥´ê²Œ ì…ë ¥í•´ì£¼ì„¸ìš”.');
      return;
    }

    this.submitting = true;
    this.surveyService
      .submitSurvey$(this.data.courseId, this.surveyForm.value)
      .subscribe({
        next: (survey) => {
          this.submitting = false;
          alert('ì„¤ë¬¸ì„ ì œì¶œí–ˆìŠµë‹ˆë‹¤!');
          this.dialogRef.close(survey);
        },
        error: (err) => {
          this.submitting = false;
          console.error('Failed to submit survey:', err);
          alert('ì„¤ë¬¸ ì œì¶œ ì¤‘ ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤.');
        },
      });
  }

  /**
   * Close dialog without submitting
   */
  onCancel(): void {
    this.dialogRef.close();
  }

  /**
   * Get error message for form field
   */
  getErrorMessage(fieldName: string): string {
    const control = this.surveyForm.get(fieldName);
    if (!control || !control.errors) {
      return '';
    }

    if (control.errors['required']) {
      return `${this.getFieldLabel(fieldName)}ëŠ”(ì€) í•„ìˆ˜ í•­ëª©ì…ë‹ˆë‹¤.`;
    }
    if (control.errors['minlength']) {
      const minLength = control.errors['minlength'].requiredLength;
      return `ìµœì†Œ ${minLength}ì ì´ìƒ ì…ë ¥í•´ì£¼ì„¸ìš”.`;
    }
    return 'ì…ë ¥ì´ ì˜¬ë°”ë¥´ì§€ ì•ŠìŠµë‹ˆë‹¤.';
  }

  /**
   * Get user-friendly field label
   */
  getFieldLabel(fieldName: string): string {
    const labels: { [key: string]: string } = {
      dreamDescription: 'ê¿ˆê³¼ ëª©í‘œ',
      wantToMake: 'ë§Œë“¤ê³  ì‹¶ì€ ê²ƒ',
      selfIntroduction: 'ìê¸°ì†Œê°œ',
      motivationScore: 'ë™ê¸° ìˆ˜ì¤€',
      experienceLevel: 'ê²½í—˜ ìˆ˜ì¤€',
      expectation: 'ê¸°ëŒ€ì‚¬í•­',
      specialRequest: 'íŠ¹ë³„ ìš”ì²­ì‚¬í•­',
    };
    return labels[fieldName] || fieldName;
  }

  /**
   * Calculate progress percentage
   */
  getProgressPercentage(): number {
    const fields = [
      'dreamDescription',
      'wantToMake',
      'selfIntroduction',
      'motivationScore',
      'experienceLevel',
      'expectation',
    ];
    const completedFields = fields.filter(
      (field) => this.surveyForm.get(field)?.value
    ).length;
    return Math.round((completedFields / fields.length) * 100);
  }

  /**
   * Get number of completed required fields
   */
  getCompletedFields(): number {
    const fields = [
      'dreamDescription',
      'wantToMake',
      'selfIntroduction',
      'motivationScore',
      'experienceLevel',
      'expectation',
    ];
    return fields.filter((field) => this.surveyForm.get(field)?.value).length;
  }
}
