import { Injectable } from '@angular/core';
import { BehaviorSubject, catchError, EMPTY, filter, map, Observable, of, tap } from 'rxjs';
import { ITeacher, LoginDto } from '../interfaces/models/membership.interface';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { environment } from '../config/environment';

@Injectable({ providedIn: 'root' })
export class TeacherMembershipService {
  user$ = new BehaviorSubject<ITeacher | null>(null);
  private readonly storageTokenKey = 'edu_teacher_token';
  private readonly storageUserKey = 'edu_teacher_user';
  private initialized = false;
  private readonly baseUrl = environment.apiUrl + '/teachers';

  constructor(
    private http: HttpClient,
    private router: Router
  ) {
    this.initUser();
  }

  getUser$(): Observable<ITeacher> {
    return this.user$.asObservable().pipe(
      filter((user): user is ITeacher => user !== null)
    );
  }

  setUser(user: ITeacher) {
    this.user$.next(user);
    localStorage.setItem(this.storageUserKey, JSON.stringify(user));
  }

  getNullableUser$(): Observable<ITeacher | null> {
    return this.user$.asObservable();
  }

  get currentUser(): ITeacher | null {
    return this.user$.value;
  }

  get isLoggedIn(): boolean {
    return this.user$.value !== null;
  }

  initUser() {
    if (this.initialized) return;
    this.initialized = true;

    const token = localStorage.getItem(this.storageTokenKey);
    const userJson = localStorage.getItem(this.storageUserKey);

    if (token && userJson) {
      try {
        const user = JSON.parse(userJson) as ITeacher;
        this.user$.next(user);
      } catch (error) {
        this.logout();
      }
    }
  }

  login$(loginDto: LoginDto): Observable<ITeacher> {
    return this.http.post<{ user: ITeacher; token: string }>(`${this.baseUrl}/login`, loginDto).pipe(
      tap(response => {
        localStorage.setItem(this.storageTokenKey, response.token);
        this.setUser(response.user);
      }),
      map(response => response.user),
      catchError(error => {
        return EMPTY;
      })
    );
  }

  register$(teacherData: any): Observable<ITeacher> {
    return this.http.post<{ user: ITeacher; token: string }>(`${this.baseUrl}/register`, teacherData).pipe(
      tap(response => {
        localStorage.setItem(this.storageTokenKey, response.token);
        this.setUser(response.user);
      }),
      map(response => response.user),
      catchError(error => {
        return EMPTY;
      })
    );
  }

  logout() {
    localStorage.removeItem(this.storageTokenKey);
    localStorage.removeItem(this.storageUserKey);
    this.user$.next(null);
    this.router.navigate(['/education/teacher/login']);
  }

  checkAuthStatus(): Observable<boolean> {
    const token = localStorage.getItem(this.storageTokenKey);
    if (!token) {
      return of(false);
    }

    return this.http.get<{ user: ITeacher }>(`${this.baseUrl}/profile`).pipe(
      tap(response => this.setUser(response.user)),
      map(() => true),
      catchError(() => {
        this.logout();
        return of(false);
      })
    );
  }
}