import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, catchError, throwError } from 'rxjs';
import { environment } from '../config/environment';

export interface SurveyQuestionOption {
  value: string;
  label: string;
}

export interface SurveyQuestion {
  id: string;
  type: 'TEXT' | 'TEXTAREA' | 'SINGLE_CHOICE' | 'MULTIPLE_CHOICE' | 'RATING' | 'SCALE' | 'DATE';
  question: string;
  required: boolean;
  options?: SurveyQuestionOption[];
  min?: number;
  max?: number;
  minLabel?: string;
  maxLabel?: string;
  placeholder?: string;
  maxLength?: number;
  rows?: number;
}

export interface WeeklySurvey {
  weekNumber: number;
  title: string;
  description: string;
  questions: SurveyQuestion[];
}

export interface SurveyAnswer {
  questionId: string;
  questionText: string;
  value: any;
  displayValue?: string;
}

export interface SurveySubmitDto {
  courseCode: string;
  courseId?: string;
  schoolId: string;
  studentId: string;
  weekNumber: number;
  answers: SurveyAnswer[];
}

export interface SurveyResponse {
  id: string;
  courseCode: string;
  schoolId: string;
  studentId: string;
  weekNumber: number;
  surveyTitle: string;
  answers: SurveyAnswer[];
  submittedAt: Date;
}

export interface SurveyStats {
  courseCode: string;
  weekNumber: number;
  surveyTitle: string;
  totalResponses: number;
  questionStats: any[];
}

