import { Component, Input, OnInit, ViewChild, ElementRef } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatIconModule } from '@angular/material/icon';

@Component({
  selector: 'app-video-player',
  standalone: true,
  imports: [CommonModule, MatIconModule],
  template: `
    <div class="video-container" (click)="$event.stopPropagation()">
      <video 
        #videoElement
        autoplay 
        [muted]="isMuted"
        loop 
        playsinline 
        class="video-player"
        [class.show]="isVisible"
        (click)="$event.stopPropagation()">
        <source [src]="videoSrc" type="video/mp4">
      </video>
      <button class="mute-toggle" (click)="toggleMute(); $event.stopPropagation()" [attr.aria-label]="isMuted ? 'ìŒì†Œê±° í•´ì œ' : 'ìŒì†Œê±°'">
        <mat-icon>{{ isMuted ? 'volume_off' : 'volume_up' }}</mat-icon>
      </button>
    </div>
  `,
  styles: [`
    .video-container {
      position: relative;
      width: 100%;
      height: 100%;
    }

    .video-player {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      display: block;
    }

    .video-player.show {
      animation: fadeIn 0.3s ease-in;
    }

    @keyframes fadeIn {
      from {
        opacity: 0;
      }
      to {
        opacity: 1;
      }
    }

    .mute-toggle {
      position: absolute;
      top: 12px;
      right: 12px;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      border: none;
      background: rgba(255, 255, 255, 0.3);
      backdrop-filter: blur(10px);
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      transition: all 0.3s ease;
      z-index: 10;
    }

    .mute-toggle:hover {
      background: rgba(255, 255, 255, 0.5);
    }

    .mute-toggle mat-icon {
      font-size: 20px;
      width: 20px;
      height: 20px;
    }
  `]
})
export class VideoPlayerComponent implements OnInit {
  @Input() videoSrc: string = '';
  @ViewChild('videoElement') videoElement!: ElementRef<HTMLVideoElement>;
  
  isVisible = false;
  isMuted = true;

  ngOnInit(): void {
    this.isVisible = true;
  }

  ngAfterViewInit(): void {
    if (this.videoElement) {
      const video = this.videoElement.nativeElement;
      video.play().catch(err => {
        console.warn('ë¹„ë””ì˜¤ ì¬ìƒ ì‹¤íŒ¨:', err);
      });
    }
  }

  play(): void {
    if (this.videoElement) {
      this.videoElement.nativeElement.play().catch(err => {
        console.warn('ë¹„ë””ì˜¤ ì¬ìƒ ì‹¤íŒ¨:', err);
      });
    }
  }

  pause(): void {
    if (this.videoElement) {
      this.videoElement.nativeElement.pause();
    }
  }

  stop(): void {
    if (this.videoElement) {
      this.videoElement.nativeElement.currentTime = 0;
      this.videoElement.nativeElement.pause();
    }
  }

  toggleMute(): void {
    this.isMuted = !this.isMuted;
  }
}
