import { Component, Input, Output, EventEmitter, OnInit, Inject } from '@angular/core';
import { IWysiwyg, ISection, IRow, IWidget, WidgetFactory, WidgetType } from '../../interfaces/models/widget.interface';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { SchoolApiService } from '../../services/school-api.service';
import { environment } from '../../config/environment';

@Component({
  selector: 'app-course-detail-editor',
  templateUrl: './course-detail-editor.component.html',
  styleUrls: ['./course-detail-editor.component.scss'],
  standalone: false
})
export class CourseDetailEditorComponent implements OnInit {
  @Input() initialContent?: string; // JSON string
  @Input() userId?: string; // ì´ë¯¸ì§€ ì—…ë¡œë“œìš© ì‚¬ìš©ì ID
  @Output() contentChange = new EventEmitter<string>();

  wysiwyg: IWysiwyg = { sections: [] };
  
  // í˜„ì¬ í¸ì§‘ ì¤‘ì¸ ìœ„ì ¯
  editingWidget: { sectionIdx: number; rowIdx: number; colIdx: number; widget: IWidget } | null = null;
  
  // ìœ„ì ¯ íƒ€ì… ì„ íƒ UI
  showWidgetSelector = false;
  widgetSelectorPosition = { sectionIdx: -1, rowIdx: -1 };

  // ì´ë¯¸ì§€ ì—…ë¡œë“œ ê´€ë ¨
  isDraggingImage = false;
  uploadingImage = false;

  // ì¸ë¼ì¸ í¸ì§‘ ê´€ë ¨
  inlineEditingWidget: { sectionIdx: number; rowIdx: number; colIdx: number } | null = null;

  constructor(private storeApiService: SchoolApiService) {}

  ngOnInit(): void {
    // ì´ˆê¸° ì„¹ì…˜ì´ ì—†ìœ¼ë©´ í•˜ë‚˜ ì¶”ê°€ (ExpressionChangedAfterItHasBeenCheckedError ë°©ì§€ë¥¼ ìœ„í•´ setTimeout ì‚¬ìš©)
    if (this.wysiwyg.sections.length === 0) {
      setTimeout(() => {
        this.addSection();
      }, 0);
    }
  }

  // ===== ì„¹ì…˜ ê´€ë¦¬ =====
  addSection(): void {
    this.wysiwyg.sections.push(WidgetFactory.createEmptySection());
    this.emitChange();
  }

  removeSection(sectionIdx: number): void {
    if (confirm('ì´ ì„¹ì…˜ì„ ì‚­ì œí•˜ì‹œê² ìŠµë‹ˆê¹Œ?')) {
      this.wysiwyg.sections.splice(sectionIdx, 1);
      this.emitChange();
    }
  }

  // ===== Row ê´€ë¦¬ =====
  addRow(sectionIdx: number): void {
    this.wysiwyg.sections[sectionIdx].rows.push(WidgetFactory.createEmptyRow());
    this.emitChange();
  }

  removeRow(sectionIdx: number, rowIdx: number): void {
    if (confirm('ì´ í–‰ì„ ì‚­ì œí•˜ì‹œê² ìŠµë‹ˆê¹Œ?')) {
      this.wysiwyg.sections[sectionIdx].rows.splice(rowIdx, 1);
      this.emitChange();
    }
  }

  dropRow(event: CdkDragDrop<IRow[]>, sectionIdx: number): void {
    moveItemInArray(
      this.wysiwyg.sections[sectionIdx].rows,
      event.previousIndex,
      event.currentIndex
    );
    this.emitChange();
  }

  // ===== ìœ„ì ¯ ì¶”ê°€ =====
  openWidgetSelector(sectionIdx: number, rowIdx: number): void {
    this.showWidgetSelector = true;
    this.widgetSelectorPosition = { sectionIdx, rowIdx };
  }

  closeWidgetSelector(): void {
    this.showWidgetSelector = false;
    this.widgetSelectorPosition = { sectionIdx: -1, rowIdx: -1 };
  }

  addWidget(widgetType: WidgetType): void {
    const { sectionIdx, rowIdx } = this.widgetSelectorPosition;
    
    if (sectionIdx < 0 || rowIdx < 0) return;

    let widget: IWidget;
    
    switch (widgetType) {
      case 'text':
        widget = WidgetFactory.createTextWidget();
        break;
      case 'image':
        widget = WidgetFactory.createImageWidget();
        break;
      case 'embed':
        widget = WidgetFactory.createEmbedWidget();
        break;
      default:
        return;
    }

    const column = WidgetFactory.createColumnWithWidget(widget);
    this.wysiwyg.sections[sectionIdx].rows[rowIdx].columns.push(column);
    
    this.closeWidgetSelector();
    this.emitChange();
  }

  // ===== ìœ„ì ¯ í¸ì§‘ =====
  editWidget(sectionIdx: number, rowIdx: number, colIdx: number): void {
    const widget = this.wysiwyg.sections[sectionIdx].rows[rowIdx].columns[colIdx].widget;
    if (widget) {
      this.editingWidget = { sectionIdx, rowIdx, colIdx, widget: JSON.parse(JSON.stringify(widget)) };
    }
  }

  saveWidget(): void {
    if (!this.editingWidget) return;

    const { sectionIdx, rowIdx, colIdx, widget } = this.editingWidget;
    this.wysiwyg.sections[sectionIdx].rows[rowIdx].columns[colIdx].widget = widget;
    
    this.editingWidget = null;
    this.emitChange();
  }

  cancelEdit(): void {
    this.editingWidget = null;
  }

  removeWidget(sectionIdx: number, rowIdx: number, colIdx: number): void {
    if (confirm('ì´ ìœ„ì ¯ì„ ì‚­ì œí•˜ì‹œê² ìŠµë‹ˆê¹Œ?')) {
      this.wysiwyg.sections[sectionIdx].rows[rowIdx].columns.splice(colIdx, 1);
      this.emitChange();
    }
  }

  // ===== ìœ„ì ¯ ì´ë™ =====
  moveWidgetUp(sectionIdx: number, rowIdx: number, colIdx: number): void {
    const columns = this.wysiwyg.sections[sectionIdx].rows[rowIdx].columns;
    if (colIdx > 0) {
      [columns[colIdx], columns[colIdx - 1]] = [columns[colIdx - 1], columns[colIdx]];
      this.emitChange();
    }
  }

  moveWidgetDown(sectionIdx: number, rowIdx: number, colIdx: number): void {
    const columns = this.wysiwyg.sections[sectionIdx].rows[rowIdx].columns;
    if (colIdx < columns.length - 1) {
      [columns[colIdx], columns[colIdx + 1]] = [columns[colIdx + 1], columns[colIdx]];
      this.emitChange();
    }
  }

  // ===== ë³€ê²½ ì´ë²¤íŠ¸ =====
  emitChange(): void {
    const jsonString = JSON.stringify(this.wysiwyg);
    this.contentChange.emit(jsonString);
  }

  // ===== ë¯¸ë¦¬ë³´ê¸° =====
  getWidgetPreview(widget: IWidget): string {
    switch (widget.widgetType) {
      case 'text':
        const textContent = widget.properties.content || 'í…ìŠ¤íŠ¸';
        return textContent.length > 50 ? textContent.substring(0, 50) + '...' : textContent;
      case 'image':
        return widget.properties.source?.alt || 'ì´ë¯¸ì§€';
      case 'embed':
        return widget.properties.title || 'Embed';
      default:
        return widget.widgetType;
    }
  }

  getWidgetIcon(widgetType: WidgetType): string {
    const icons: Record<WidgetType, string> = {
      text: 'text_fields',
      image: 'image',
      embed: 'code',
      video: 'videocam',
      button: 'smart_button',
      spacer: 'space_bar'
    };
    return icons[widgetType] || 'widgets';
  }

  // ===== ì´ë¯¸ì§€ ë“œë˜ê·¸&ë“œë¡­ ì—…ë¡œë“œ =====
  onImageDragOver(event: DragEvent): void {
    event.preventDefault();
    event.stopPropagation();
    this.isDraggingImage = true;
  }

  onImageDragLeave(event: DragEvent): void {
    event.preventDefault();
    event.stopPropagation();
    this.isDraggingImage = false;
  }

  onImageDrop(event: DragEvent): void {
    event.preventDefault();
    event.stopPropagation();
    this.isDraggingImage = false;

    const files = event.dataTransfer?.files;
    if (files && files.length > 0) {
      const file = files[0];
      if (file.type.startsWith('image/')) {
        this.uploadImageFile(file);
      } else {
        alert('ì´ë¯¸ì§€ íŒŒì¼ë§Œ ì—…ë¡œë“œ ê°€ëŠ¥í•©ë‹ˆë‹¤.');
      }
    }
  }

  onImageFileSelect(event: Event): void {
    const input = event.target as HTMLInputElement;
    const file = input.files?.[0];
    if (file) {
      this.uploadImageFile(file);
    }
  }

  uploadImageFile(file: File): void {
    if (!this.userId) {
      alert('ì‚¬ìš©ì ì •ë³´ê°€ ì—†ìŠµë‹ˆë‹¤. ë‹¤ì‹œ ë¡œê·¸ì¸í•´ì£¼ì„¸ìš”.');
      return;
    }

    this.uploadingImage = true;

    this.storeApiService.uploadImage$(file, this.userId).subscribe({
      next: (result) => {
        
        if (this.editingWidget) {
          // ìƒëŒ€ ê²½ë¡œë¥¼ ì ˆëŒ€ ê²½ë¡œë¡œ ë³€í™˜
          const imageUrl = result.url.startsWith('http') 
            ? result.url 
            : `${environment.baseSyncUrl}${result.url}`;

          this.editingWidget.widget.properties.source = {
            src: imageUrl,
            alt: file.name
          };
        }

        this.uploadingImage = false;
      },
      error: (error) => {
        alert('ì´ë¯¸ì§€ ì—…ë¡œë“œì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.');
        this.uploadingImage = false;
      }
    });
  }

  removeEditingImage(): void {
    if (this.editingWidget) {
      this.editingWidget.widget.properties.source = {
        src: '',
        alt: ''
      };
    }
  }

  // ===== ì¸ë¼ì¸ í…ìŠ¤íŠ¸ í¸ì§‘ =====
  
  /**
   * ìœ„ì ¯ì´ ì¸ë¼ì¸ í¸ì§‘ ëª¨ë“œì¸ì§€ í™•ì¸
   */
  isWidgetInEditMode(sectionIdx: number, rowIdx: number, colIdx: number): boolean {
    if (!this.inlineEditingWidget) return false;
    return this.inlineEditingWidget.sectionIdx === sectionIdx &&
           this.inlineEditingWidget.rowIdx === rowIdx &&
           this.inlineEditingWidget.colIdx === colIdx;
  }

  /**
   * ìœ„ì ¯ ë”ë¸”í´ë¦­ - í…ìŠ¤íŠ¸ ìœ„ì ¯ë§Œ ì¸ë¼ì¸ í¸ì§‘ ëª¨ë“œë¡œ ì „í™˜
   */
  onWidgetDoubleClick(sectionIdx: number, rowIdx: number, colIdx: number, widget: IWidget): void {
    // í…ìŠ¤íŠ¸ ìœ„ì ¯ë§Œ ì¸ë¼ì¸ í¸ì§‘ ì§€ì›
    if (widget.widgetType !== 'text') {
      return;
    }

    // ì¸ë¼ì¸ í¸ì§‘ ëª¨ë“œ í™œì„±í™”
    this.inlineEditingWidget = { sectionIdx, rowIdx, colIdx };

    // textareaì— í¬ì»¤ìŠ¤ë¥¼ ì£¼ê¸° ìœ„í•´ ë‹¤ìŒ ë Œë”ë§ ì‚¬ì´í´ì—ì„œ ì‹¤í–‰
    setTimeout(() => {
      const textarea = document.querySelector('.text-inline-textarea') as HTMLTextAreaElement;
      if (textarea) {
        textarea.focus();
        textarea.select(); // ì „ì²´ í…ìŠ¤íŠ¸ ì„ íƒ
      }
    }, 0);
  }

  /**
   * í…ìŠ¤íŠ¸ ì¸ë¼ì¸ í¸ì§‘ ì €ì¥ (blur ì´ë²¤íŠ¸)
   */
  saveInlineTextEdit(sectionIdx: number, rowIdx: number, colIdx: number, event: Event): void {
    const textarea = event.target as HTMLTextAreaElement;
    const newContent = textarea.value.trim();

    // ìœ„ì ¯ ë‚´ìš© ì—…ë°ì´íŠ¸
    const widget = this.wysiwyg.sections[sectionIdx].rows[rowIdx].columns[colIdx].widget;
    if (widget && widget.widgetType === 'text') {
      widget.properties.content = newContent;
      this.emitChange();
    }

    // ì¸ë¼ì¸ í¸ì§‘ ëª¨ë“œ ì¢…ë£Œ
    this.inlineEditingWidget = null;
  }

  /**
   * ì¸ë¼ì¸ í¸ì§‘ ì·¨ì†Œ (ESC í‚¤)
   */
  cancelInlineEdit(): void {
    this.inlineEditingWidget = null;
  }
}
