import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';
import { AdminAuthService } from '../../services/admin-auth.service';
import { AuthModeService } from '../../services/auth-mode.service';
import { ThemeService } from '../../../../services/theme.service';

@Component({
  selector: 'app-admin-login',
  templateUrl: './admin-login.component.html',
  styleUrls: ['./admin-login.component.scss'],
  standalone: false
})
export class AdminLoginComponent implements OnInit, OnDestroy {
  loginForm: FormGroup;
  isLoading = false;
  errorMessage = '';
  hidePassword = true;
  isDarkMode = false;
  private subscriptions = new Subscription();

  constructor(
    private fb: FormBuilder,
    private adminAuth: AdminAuthService,
    private authModeService: AuthModeService,
    private router: Router,
    private route: ActivatedRoute,
    public themeService: ThemeService
  ) {
    this.loginForm = this.fb.group({
      id: ['', [Validators.required, Validators.minLength(3)]],
      password: ['', [Validators.required, Validators.minLength(6)]]
    });
  }

  ngOnInit(): void {
    // ë¡œê·¸ì¸ í˜ì´ì§€ì´ë¯€ë¡œ ëª¨ë“œë§Œ ì„¤ì • (ë¡œê·¸ì•„ì›ƒì€ í•˜ì§€ ì•ŠìŒ)
    const currentMode = this.authModeService.getCurrentMode();
    if (currentMode !== 'ADMIN') {
      // ì§ì ‘ ëª¨ë“œ ì„¤ì • (switchModeëŠ” ë¡œê·¸ì•„ì›ƒì„ ìˆ˜í–‰í•˜ë¯€ë¡œ ì‚¬ìš©í•˜ì§€ ì•ŠìŒ)
      this.authModeService.currentMode$.next('ADMIN');
      localStorage.setItem('ez_edu_auth_mode', 'ADMIN');
    }

    // í…Œë§ˆ êµ¬ë…
    this.subscriptions.add(
      this.themeService.currentTheme$.subscribe(theme => {
        this.isDarkMode = theme === 'dark';
      })
    );

    // ì´ë¯¸ ë¡œê·¸ì¸ëœ ê²½ìš° ë¦¬ë‹¤ì´ë ‰íŠ¸
    if (this.adminAuth.isAuthenticated()) {
      this.redirectToReturnUrl();
    }
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  onSubmit(): void {
    if (this.loginForm.valid && !this.isLoading) {
      this.isLoading = true;
      this.errorMessage = '';

      const { id, password } = this.loginForm.value;

      this.subscriptions.add(
        this.adminAuth.login({ id, password }).subscribe({
          next: (user) => {
            
            // ëª¨ë“œë¥¼ ADMINìœ¼ë¡œ í™•ì‹¤í•˜ê²Œ ì„¤ì •
            this.authModeService.currentMode$.next('ADMIN');
            localStorage.setItem('ez_edu_auth_mode', 'ADMIN');
            
            this.redirectToReturnUrl();
          },
          error: (error) => {
            this.errorMessage = this.getErrorMessage(error);
            this.isLoading = false;
          }
        })
      );
    } else {
      this.markFormGroupTouched();
    }
  }

  onModeSwitch(mode: 'STUDENT' | 'TEACHER'): void {
    this.authModeService.switchMode(mode);
    if (mode === 'STUDENT') {
      this.router.navigate(['/education/login']);
    } else {
      this.router.navigate(['/education/teacher/login']);
    }
  }

  private redirectToReturnUrl(): void {
    // ê´€ë¦¬ìëŠ” ê¸°ë³¸ì ìœ¼ë¡œ êµìœ¡ íšŒì› ê´€ë¦¬ í˜ì´ì§€ë¡œ ì´ë™
    const returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/education/admin/users';
    this.router.navigate([returnUrl]);
  }
  private markFormGroupTouched(): void {
    Object.keys(this.loginForm.controls).forEach(key => {
      const control = this.loginForm.get(key);
      if (control) {
        control.markAsTouched();
      }
    });
  }

  getFieldError(fieldName: string): string {
    const field = this.loginForm.get(fieldName);
    if (field && field.errors && field.touched) {
      if (field.errors['required']) {
        return `${this.getFieldDisplayName(fieldName)}ì„(ë¥¼) ì…ë ¥í•´ì£¼ì„¸ìš”.`;
      }
      if (field.errors['minlength']) {
        const minLength = field.errors['minlength'].requiredLength;
        return `${this.getFieldDisplayName(fieldName)}ì€(ëŠ”) ìµœì†Œ ${minLength}ì ì´ìƒì´ì–´ì•¼ í•©ë‹ˆë‹¤.`;
      }
    }
    return '';
  }

  private getFieldDisplayName(fieldName: string): string {
    const displayNames: { [key: string]: string } = {
      'id': 'ê´€ë¦¬ì ì•„ì´ë””',
      'password': 'ë¹„ë°€ë²ˆí˜¸'
    };
    return displayNames[fieldName] || fieldName;
  }

  isFieldInvalid(fieldName: string): boolean {
    const field = this.loginForm.get(fieldName);
    return !!(field && field.errors && field.touched);
  }

  private getErrorMessage(error: any): string {
    // ì¤‘ì²©ëœ ë©”ì‹œì§€ ê°ì²´ ì²˜ë¦¬
    let message = error.error?.message;
    
    if (message) {
      // messageê°€ ê°ì²´ì¸ ê²½ìš° ë‚´ë¶€ì˜ message í•„ë“œ ì¶”ì¶œ
      if (typeof message === 'object' && message !== null) {
        message = message.message || message.error || JSON.stringify(message);
      }
      return message;
    }
    
    if (error.status === 401) {
      return 'ê´€ë¦¬ì ì•„ì´ë”” ë˜ëŠ” ë¹„ë°€ë²ˆí˜¸ê°€ ì˜ëª»ë˜ì—ˆìŠµë‹ˆë‹¤.';
    }
    
    if (error.status === 403) {
      return 'ê´€ë¦¬ì ê¶Œí•œì´ ì—†ìŠµë‹ˆë‹¤. ì‹œìŠ¤í…œ ê´€ë¦¬ìì—ê²Œ ë¬¸ì˜í•´ì£¼ì„¸ìš”.';
    }
    
    if (error.status === 0) {
      return 'ì„œë²„ì— ì—°ê²°í•  ìˆ˜ ì—†ìŠµë‹ˆë‹¤. ë„¤íŠ¸ì›Œí¬ ì—°ê²°ì„ í™•ì¸í•´ì£¼ì„¸ìš”.';
    }
    
    return 'ë¡œê·¸ì¸ì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤. ì ì‹œ í›„ ë‹¤ì‹œ ì‹œë„í•´ì£¼ì„¸ìš”.';
  }
}