/**
 * ì´ë¯¸ì§€ Fallback ìœ í‹¸ë¦¬í‹°
 * ì´ë¯¸ì§€ê°€ ì—†ì„ ë•Œ ìƒ‰ìƒ ë°°ê²½ê³¼ í…ìŠ¤íŠ¸ë¡œ SVGë¥¼ ìƒì„±í•©ë‹ˆë‹¤.
 * ë°±ì—”ë“œì—ì„œ ì œê³µí•˜ëŠ” fallback ë°ì´í„° ë˜ëŠ” í”„ë¡ íŠ¸ì—”ë“œì—ì„œ ìƒì„±í•©ë‹ˆë‹¤.
 */

import { IUploadImage } from '../interfaces/models/shared.interface';

const COLORS = [
  '#FF6B6B', '#4ECDC4', '#45B7D1', '#FFA07A', 
  '#98D8C8', '#F7DC6F', '#BB8FCE', '#85C1E2',
  '#F8B739', '#52B788', '#E85D75', '#6A4C93'
];

/**
 * ì´ë¦„ì— ë”°ë¼ ì¼ê´€ëœ ìƒ‰ìƒì„ ìƒì„±í•˜ëŠ” í—¬í¼ í•¨ìˆ˜
 */
export function getColorByName(name: string): string {
  const hash = name.split('').reduce((acc, char) => acc + char.charCodeAt(0), 0);
  return COLORS[Math.abs(hash) % COLORS.length];
}

/**
 * ìƒ‰ìƒ ë°°ê²½ê³¼ í…ìŠ¤íŠ¸ë¡œ SVG ì´ë¯¸ì§€ ìƒì„±
 * @param backgroundColor ë°°ê²½ ìƒ‰ìƒ
 * @param text í‘œì‹œí•  í…ìŠ¤íŠ¸
 * @param size SVG í¬ê¸° (ê¸°ë³¸: 200)
 * @param fontSize í°íŠ¸ í¬ê¸° (ê¸°ë³¸: sizeì˜ 24%)
 */
export function createFallbackSvg(
  backgroundColor: string, 
  text: string, 
  size: number = 200,
  fontSize?: number
): string {
  const actualFontSize = fontSize || Math.round(size * 0.24);
  const centerY = size / 2 + actualFontSize / 3;
  
  const svg = `
    <svg width="${size}" height="${size}" xmlns="http://www.w3.org/2000/svg">
      <rect width="${size}" height="${size}" fill="${backgroundColor}" />
      <text x="${size / 2}" y="${centerY}" 
            font-family="Arial, sans-serif" 
            font-size="${actualFontSize}" 
            font-weight="bold" 
            text-anchor="middle" 
            dominant-baseline="middle" 
            fill="white">
        ${text}
      </text>
    </svg>
  `;
  
  return `data:image/svg+xml;base64,${btoa(unescape(encodeURIComponent(svg)))}`;
}

/**
 * ì´ë¦„ì—ì„œ fallback ì´ë¯¸ì§€ ìƒì„±
 * @param name ìƒì  ë˜ëŠ” ìƒí’ˆ ì´ë¦„
 * @param size SVG í¬ê¸° (ê¸°ë³¸: 200)
 */
export function createFallbackImageFromName(name: string, size: number = 200): string {
  if (!name || name.length === 0) {
    return createFallbackSvg('#CCCCCC', '?', size);
  }
  
  const backgroundColor = getColorByName(name);
  
  // ì´ë¦„ì´ ì§§ìœ¼ë©´(4ê¸€ì ì´í•˜) ì „ì²´ í‘œì‹œ, ê¸¸ë©´ ì• 2ê¸€ìë§Œ í‘œì‹œ
  let text = name;
  let fontSize = Math.round(size * 0.16); // ê¸°ë³¸ í°íŠ¸ í¬ê¸° (4ê¸€ì ê¸°ì¤€)

  if (name.length > 4) {
    text = name.substring(0, 2);
    fontSize = Math.round(size * 0.24); // 2ê¸€ìì¼ ë•ŒëŠ” í¬ê²Œ
  } else if (name.length <= 2) {
    fontSize = Math.round(size * 0.24); // 2ê¸€ì ì´í•˜ëŠ” í¬ê²Œ
  } else if (name.length === 3) {
    fontSize = Math.round(size * 0.20); // 3ê¸€ìëŠ” ì¤‘ê°„ í¬ê¸°
  }
  
  return createFallbackSvg(backgroundColor, text, size, fontSize);
}

/**
 * IUploadImage ê°ì²´ ë˜ëŠ” URLì—ì„œ ì´ë¯¸ì§€ URLì„ ê°€ì ¸ì˜¤ê±°ë‚˜ fallback ìƒì„±
 * @param imageData IUploadImage ê°ì²´ ë˜ëŠ” URL ë¬¸ìì—´
 * @param fallbackName fallback ìƒì„±ì— ì‚¬ìš©í•  ì´ë¦„
 * @param defaultImage ê¸°ë³¸ ì´ë¯¸ì§€ ê²½ë¡œ (undefinedë©´ fallback ìƒì„±)
 * @param size SVG í¬ê¸°
 */
export function getImageOrFallback(
  imageData: IUploadImage | string | null | undefined,
  fallbackName: string,
  defaultImage?: string,
  size: number = 200
): string {
  // ë¬¸ìì—´ URLì¸ ê²½ìš°
  if (typeof imageData === 'string') {
    return imageData;
  }
  
  // IUploadImage ê°ì²´ì¸ ê²½ìš°
  if (imageData && typeof imageData === 'object') {
    // URLì´ ìˆìœ¼ë©´ ì‚¬ìš©
    if (imageData.url) {
      return imageData.url;
    }
    
    // ë°±ì—”ë“œì—ì„œ ì œê³µí•œ fallback ë°ì´í„°ê°€ ìˆìœ¼ë©´ ì‚¬ìš©
    if (imageData.backgroundColor && imageData.text) {
      return createFallbackSvg(imageData.backgroundColor, imageData.text, size);
    }
  }
  
  // fallback ì´ë¦„ì´ ìˆìœ¼ë©´ í”„ë¡ íŠ¸ì—”ë“œì—ì„œ ìƒì„±
  if (fallbackName) {
    return createFallbackImageFromName(fallbackName, size);
  }
  
  // ê¸°ë³¸ ì´ë¯¸ì§€ê°€ ì œê³µë˜ì—ˆìœ¼ë©´ ì‚¬ìš©, ì•„ë‹ˆë©´ ê¸°ë³¸ fallback ìƒì„±
  return defaultImage || createFallbackImageFromName('N/A', size);
}

