  /**
   * ì£¼ë¬¸ í•„í„°ë§
   */
  filterOrders(): void {
    let filtered = this.allOrders;
    
    // ê²°ì œ ë°©ì‹ í•„í„°
    if (this.selectedPaymentMethod !== 'ALL') {
      // vbank í•„í„°ëŠ” vbankì™€ bankTransfer ë‘˜ ë‹¤ í¬í•¨
      if (this.selectedPaymentMethod === 'vbank') {
        filtered = filtered.filter(order => 
          order.paymentMethod === 'vbank' || order.paymentMethod === 'bankTransfer'
        );
      } else {
        filtered = filtered.filter(order => order.paymentMethod === this.selectedPaymentMethod);
      }
    }
    
    // ì£¼ë¬¸ ìƒíƒœ í•„í„°
    if (this.selectedOrderStatus !== 'ALL') {
      filtered = filtered.filter(order => order.status === this.selectedOrderStatus);
    }
    
    this.displayedOrders = filtered;
  }

