import { Component, Input, OnInit, OnChanges, SimpleChanges, ViewChild, ElementRef, OnDestroy } from '@angular/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';

@Component({
  selector: 'app-widget-embed',
  templateUrl: './widget-embed.component.html',
  styleUrls: ['./widget-embed.component.scss'],
  standalone: false
})
export class WidgetEmbedComponent implements OnInit, OnChanges, OnDestroy {
  @Input() properties!: any;

  @ViewChild('embedIframe') embedIframe!: ElementRef<HTMLIFrameElement>;

  private messageListener?: (event: MessageEvent) => void;

  safeSrc: SafeResourceUrl | null = null;
  style: any = {};

  constructor(private sanitizer: DomSanitizer) {}

  ngOnInit(): void {
    this.updateSafeSrc();
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.updateSafeSrc();
    this.style = this.getStyle();
  }

  ngOnDestroy(): void {
    this.cleanupListeners();
  }

  /**
   * iframe ë¡œë“œ ì™„ë£Œ ì‹œ í˜¸ì¶œ
   * postMessageë¡œ ë†’ì´ ì •ë³´ë¥¼ ë°›ì„ ì¤€ë¹„
   */
  onIframeLoad(): void {
    if (!this.embedIframe?.nativeElement) return;

    this.setupMessageListener();
  }

  /**
   * postMessage ì´ë²¤íŠ¸ ë¦¬ìŠ¤ë„ˆ ì„¤ì •
   * ì„ë² ë”©ëœ í˜ì´ì§€ì—ì„œ ìë™ìœ¼ë¡œ ë³´ë‚´ëŠ” ë†’ì´ ì •ë³´ë¥¼ ìˆ˜ì‹ 
   */
  private setupMessageListener(): void {
    this.messageListener = (event: MessageEvent) => {
      // iframeì—ì„œ ì˜¨ ë©”ì‹œì§€ì¸ì§€ í™•ì¸
      if (this.embedIframe?.nativeElement &&
          event.source === this.embedIframe.nativeElement.contentWindow) {
        this.handleIframeMessage(event.data);
      }
    };

    window.addEventListener('message', this.messageListener);
  }

  /**
   * iframeì—ì„œ ë°›ì€ ë©”ì‹œì§€ ì²˜ë¦¬
   */
  private handleIframeMessage(data: any): void {
    if (!data || typeof data !== 'object') return;


    // ë†’ì´ ì •ë³´ ì²˜ë¦¬
    if ((data.type === 'resize' || data.type === 'height') &&
        data.height && this.embedIframe?.nativeElement) {
      const newHeight = parseInt(data.height, 10);
      const minHeight = 100;
      const finalHeight = Math.max(newHeight, minHeight);

      if (finalHeight > 0) {
        this.embedIframe.nativeElement.style.height = finalHeight + 'px';

        // ë¶€ëª¨ ì»¨í…Œì´ë„ˆë„ ë†’ì´ ì¡°ì •
        const container = this.embedIframe.nativeElement.parentElement;
        if (container) {
          container.style.height = 'auto';
          container.style.minHeight = finalHeight + 'px';
        }
      }
    }
  }

  /**
   * ì´ë²¤íŠ¸ ë¦¬ìŠ¤ë„ˆ ì •ë¦¬
   */
  private cleanupListeners(): void {
    if (this.messageListener) {
      window.removeEventListener('message', this.messageListener);
      this.messageListener = undefined;
    }
  }

  /**
   * srcë¥¼ ì•ˆì „í•œ URLë¡œ ë³€í™˜
   */
  private updateSafeSrc(): void {
    if (this.properties && this.properties.src) {
      this.safeSrc = this.sanitizer.bypassSecurityTrustResourceUrl(this.properties.src);
    } else {
      this.safeSrc = null;
    }
  }

  /**
   * properties.style ê°ì²´ë¥¼ ì‹¤ì œ CSS ìŠ¤íƒ€ì¼ë¡œ ë³€í™˜
   */
  getStyle() {
    const styles: { [key: string]: string | number } = {};

    if (!this.properties?.style) return styles;

    for (const key in this.properties.style) {
      const item = this.properties.style[key];
      if (!item.enabled) continue;

      styles[item.cssProperty] = item.unit
        ? `${item.value}${item.unit}`
        : item.value;
    }

    return styles;
  }
}
