import { Component, OnInit, OnDestroy, ViewChild, ElementRef, HostBinding } from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { ThemeService } from '../../services/theme.service';
import { environment } from '../../../environments/environment';

@Component({
  selector: 'app-education',
  templateUrl: './education.component.html',
  styleUrls: ['./education.component.scss'],
  standalone: false
})
export class EducationComponent implements OnInit, OnDestroy {
  @ViewChild('scrollContainer', { static: true }) scrollContainer!: ElementRef<HTMLElement>;

  private destroy$ = new Subject<void>();

  // í™˜ê²½ ì„¤ì •
  get showGlobalNavigation(): boolean {
    return environment.showGlobalNavigation === true;
  }

  constructor(private themeService: ThemeService) {}

  ngOnInit() {
    // í…Œë§ˆ ë³€ê²½ ê°ì§€í•˜ì—¬ ì»¨í…Œì´ë„ˆì— í´ë˜ìŠ¤ ì¶”ê°€
    this.themeService.currentTheme$
      .pipe(takeUntil(this.destroy$))
      .subscribe(theme => {
        if (this.scrollContainer?.nativeElement) {
          const container = this.scrollContainer.nativeElement;
          container.classList.remove('dark-theme', 'light-theme');
          container.classList.add(`${theme}-theme`);
        }
      });
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }
}