import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatInputModule } from '@angular/material/input';
import { MatButtonModule } from '@angular/material/button';
import { MatIconModule } from '@angular/material/icon';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatTooltipModule } from '@angular/material/tooltip';
import { ISubmissionReply } from '../../interfaces/submission-reply.interface';

@Component({
  selector: 'app-submission-reply-editor',
  templateUrl: './submission-reply-editor.component.html',
  styleUrls: ['./submission-reply-editor.component.scss'],
  standalone: true,
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    MatFormFieldModule,
    MatInputModule,
    MatButtonModule,
    MatIconModule,
    MatCheckboxModule,
    MatTooltipModule,
  ],
})
export class SubmissionReplyEditorComponent implements OnInit {
  @Input() editingReply?: ISubmissionReply;
  @Output() save = new EventEmitter<any>();
  @Output() cancel = new EventEmitter<void>();

  form!: FormGroup;
  isSaving = false;
  selectedFiles: File[] = [];

  constructor(private fb: FormBuilder) {}

  ngOnInit(): void {
    this.initializeForm();
  }

  private initializeForm(): void {
    this.form = this.fb.group({
      replyContent: [
        this.editingReply?.replyContent || '',
        [Validators.required, Validators.minLength(10), Validators.maxLength(50000)],
      ],
      isRequiringAction: [this.editingReply?.isRequiringAction || false],
      requiredScore: [
        this.editingReply?.requiredScore || null,
        [Validators.min(0), Validators.max(100)],
      ],
    });
  }

  onFileSelected(event: any): void {
    const files = event.target.files;
    if (files) {
      this.selectedFiles = Array.from(files);
    }
  }

  removeFile(index: number): void {
    this.selectedFiles.splice(index, 1);
  }

  onSave(): void {
    if (this.form.invalid) {
      return;
    }

    this.isSaving = true;
    this.save.emit(this.form.value);
  }

  onCancel(): void {
    this.cancel.emit();
  }

  insertMarkdown(syntax: string): void {
    const content = this.form.get('replyContent')?.value;
    this.form.patchValue({
      replyContent: content + syntax,
    });
  }

  getErrorMessage(field: string): string {
    const control = this.form.get(field);
    if (!control?.errors || !control?.touched) {
      return '';
    }

    if (control.errors['required']) {
      return 'í•„ìˆ˜ í•­ëª©ì…ë‹ˆë‹¤';
    }
    if (control.errors['minlength']) {
      return `ìµœì†Œ ${control.errors['minlength'].requiredLength}ì ì´ìƒì´ì–´ì•¼ í•©ë‹ˆë‹¤`;
    }
    if (control.errors['maxlength']) {
      return `ìµœëŒ€ ${control.errors['maxlength'].requiredLength}ì ì´í•˜ì—¬ì•¼ í•©ë‹ˆë‹¤`;
    }
    if (control.errors['min']) {
      return `0 ì´ìƒì´ì–´ì•¼ í•©ë‹ˆë‹¤`;
    }
    if (control.errors['max']) {
      return `100 ì´í•˜ì—¬ì•¼ í•©ë‹ˆë‹¤`;
    }

    return 'ì˜ëª»ëœ ì…ë ¥ì…ë‹ˆë‹¤';
  }
}
