import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { JwtHelperService } from '@auth0/angular-jwt';
import { Router } from '@angular/router';
import { Observable, map } from 'rxjs';
import { BaseAuthService } from './base-auth.service';
import { IStudent, AuthResponse } from '../interfaces/auth.interface';
import { environment } from '../config/environment';

export interface UpdateStudentDto {
  name: string;
  email: string;
  phone: string;
}

export interface StudentSignupDto {
  id: string;
  password: string;
  name: string;
  email: string;
  phone: string;
}

@Injectable({
  providedIn: 'root'
})
export class StudentAuthService extends BaseAuthService<IStudent> {
  protected storageTokenKey = 'ez_edu_student_token';
  protected loginEndpoint = environment.baseSyncUrl + '/education/students/login';
  protected loginRedirectPath = '/education/login';

  constructor(
    protected override http: HttpClient,
    protected override jwtHelper: JwtHelperService,
    protected override router: Router
  ) {
    super(http, jwtHelper, router);
    this.initUser();
  }

  register(signupData: StudentSignupDto): Observable<IStudent> {
    const signupEndpoint = environment.baseSyncUrl + '/education/students/register';
    return this.http.post<any>(signupEndpoint, signupData).pipe(
      map((response) => {
        console.log('[StudentAuthService] ğŸ” íšŒì›ê°€ì… ì‘ë‹µ:', response);
        
        let user: any;
        let accessToken: string = '';

        // íŒ¨í„´ 1: { user: {...}, accessToken: '...' }
        if (response.user && response.accessToken) {
          user = response.user;
          accessToken = response.accessToken;
        } 
        // íŒ¨í„´ 2: { accessToken: '...', ...userData }
        else if (response.accessToken) {
          accessToken = response.accessToken;
          const { accessToken: _, ...userData } = response;
          user = userData;
        }
        // íŒ¨í„´ 3: ì‘ë‹µ ìì²´ê°€ user ê°ì²´ì¸ ê²½ìš° (ë°±ì—”ë“œ ìˆ˜ì • ì „ í˜¸í™˜ì„±)
        else {
          user = response;
          console.warn('[StudentAuthService] íšŒì›ê°€ì… ì‘ë‹µì— í† í°ì´ ì—†ìŠµë‹ˆë‹¤. ìë™ ë¡œê·¸ì¸ì´ ë˜ì§€ ì•Šì„ ìˆ˜ ìˆìŠµë‹ˆë‹¤.');
        }

        if (accessToken) {
          this.setToken(accessToken);
        }
        
        return this.mapUser(user);
      })
    );
  }

  updateProfile(updateData: UpdateStudentDto): Observable<IStudent> {
    // idëŠ” ë¬¸ìì—´ userId (ì˜ˆ: "teacher1"), userIdxëŠ” ìˆ«ì ì¸ë±ìŠ¤
    const studentId = this.user$.value?.id;
    if (!studentId) {
      throw new Error('ì‚¬ìš©ì ì •ë³´ê°€ ì—†ìŠµë‹ˆë‹¤. ë‹¤ì‹œ ë¡œê·¸ì¸ í•´ì£¼ì„¸ìš”.');
    }


    // SyncAnaì™€ ë™ì¼í•œ ì—”ë“œí¬ì¸íŠ¸: PATCH /student/profile/{studentId}
    const updateEndpoint = environment.baseSyncUrl + `/student/profile/${studentId}`;
    return this.http.patch<{ user: IStudent; accessToken: string }>(updateEndpoint, updateData).pipe(
      map(({ user, accessToken }) => {
        // ì•¡ì„¸ìŠ¤ í† í° ì—…ë°ì´íŠ¸
        this.setToken(accessToken);
        const mappedUser = this.mapUser(user);
        this.setUser(mappedUser);
        return mappedUser;
      })
    );
  }
}