  /**
   * ì´ë¯¸ ìˆ˜ê°•ì‹ ì²­í•œ ê³¼ëª© ë¡œë“œ
   */
  private loadEnrolledCourses(): void {
    console.log('ğŸ”„ ìˆ˜ê°•ì‹ ì²­ ê³¼ëª© ì¡°íšŒ ì¤‘...');
    
    // ë°±ì—”ë“œ APIì—ì„œ ìˆ˜ê°•ì‹ ì²­ ëª©ë¡ ì¡°íšŒ (í”„ë¡œí•„ í˜ì´ì§€ì™€ ë™ì¼)
    this.orderApiService.getMyEnrollments$()
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (enrollments) => {
          console.log('ğŸ“š API ì‘ë‹µ - ìˆ˜ê°•ì‹ ì²­ ê³¼ëª©:', enrollments);
          
          if (!enrollments || enrollments.length === 0) {
            console.log('âš ï¸ ìˆ˜ê°•ì‹ ì²­ëœ ê³¼ëª©ì´ ì—†ìŠµë‹ˆë‹¤');
            this.enrolledCourseIds = new Set();
            return;
          }
          
          // enrollment ê°ì²´ì—ì„œ courseId ì¶”ì¶œ
          const courseIds: string[] = [];
          
          enrollments.forEach((enrollment: any) => {
            let courseId = null;
            
            // 1. enrollment.courseIdë¥¼ ë¨¼ì € í™•ì¸ (ì‹¤ì œ ê°•ì˜ ID)
            if (enrollment.courseId) {
              courseId = enrollment.courseId;
            }
            // 2. enrollment.course?.id í™•ì¸ (ì´ê²ƒì€ school IDì¼ ìˆ˜ ìˆìŒ)
            else if (enrollment.course?.id) {
              courseId = enrollment.course.id;
            }
            
            if (courseId) {
              courseIds.push(courseId);
              console.log(`âœ… ìˆ˜ê°•ì‹ ì²­ ê³¼ëª© ID: ${courseId} (${enrollment.course?.name || 'N/A'})`);
            }
          });
          
          this.enrolledCourseIds = new Set(courseIds);
          console.log('ğŸ“ ìµœì¢… ìˆ˜ê°•ì‹ ì²­ ê³¼ëª©:', Array.from(this.enrolledCourseIds));
        },
        error: (error) => {
          console.error('âŒ ìˆ˜ê°•ì‹ ì²­ ê³¼ëª© ì¡°íšŒ ì‹¤íŒ¨:', error);
          this.enrolledCourseIds = new Set();
        }
      });
  }

