  /**
   * ìˆ˜ê°• ì‹ ì²­ ì§„í–‰
   */
  onProceedPayment(): void {
    if (!this.checkoutForm.valid) {
      this.notification.show('ì‹ ì²­ì ì •ë³´ë¥¼ ì…ë ¥í•´ì£¼ì„¸ìš”.');
      return;
    }

    if (!this.cart || !this.cart.items || this.cart.items.length === 0) {
      this.notification.show('ì‹ ì²­í•  ê³¼ëª©ì´ ì—†ìŠµë‹ˆë‹¤.');
      return;
    }

    const user = this.studentAuthService.getCurrentUser();
    if (!user) {
      this.notification.show('ë¡œê·¸ì¸ì´ í•„ìš”í•©ë‹ˆë‹¤.');
      this.router.navigate(['/education/login']);
      return;
    }

    this.isLoading = true;

    // ìˆ˜ê°• ì‹ ì²­ API í˜¸ì¶œ (ì—¬ëŸ¬ ê³¼ëª© ì‹ ì²­)
    const enrollmentRequests = this.cart.items.map(item => {
      const enrollDto = {
        courseId: item.product.id
      };
      return this.orderApiService.enrollCourse$(enrollDto);
    });

    // ëª¨ë“  ìˆ˜ê°• ì‹ ì²­ ì™„ë£Œ ëŒ€ê¸°
    forkJoin(enrollmentRequests)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (results) => {
          this.isLoading = false;
          
          this.notification.show('ìˆ˜ê°• ì‹ ì²­ì´ ì™„ë£Œë˜ì—ˆìŠµë‹ˆë‹¤.');
          console.log('âœ… ìˆ˜ê°•ì‹ ì²­ ì™„ë£Œ!');
          
          // 500ms í›„ ìˆ˜ê°•ì‹ ì²­ ëª©ë¡ì„ ë‹¤ì‹œ ë¡œë“œ
          setTimeout(() => {
            console.log('ğŸ”„ ìˆ˜ê°•ì‹ ì²­ ëª©ë¡ ì¬ë¡œë“œ ì¤‘...');
            this.loadEnrolledCourses();
            
            // ì¶”ê°€ë¡œ 1ì´ˆ í›„ í˜ì´ì§€ ìƒˆë¡œê³ ì¹¨ (ì™„ì „ ë™ê¸°í™”)
            setTimeout(() => {
              console.log('ğŸ”„ í˜ì´ì§€ ìƒˆë¡œê³ ì¹¨ ì¤‘...');
              window.location.reload();
            }, 1000);
          }, 500);
        },
        error: (error) => {
          this.isLoading = false;
          let errorMessage = 'ìˆ˜ê°• ì‹ ì²­ì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.';
          if (error.error?.data?.message) {
            errorMessage = error.error.data.message;
          } else if (error.error?.message) {
            errorMessage = error.error.message;
          }
          this.notification.show(errorMessage);
        }
      });
  }

