    if (!this.cart?.items?.length) return false;
    
    console.log('ğŸ” ìˆ˜ê°•ì‹ ì²­ ìƒíƒœ í™•ì¸:');
    console.log('  - enrolledCourseIds:', Array.from(this.enrolledCourseIds));
    console.log('  - ì¥ë°”êµ¬ë‹ˆ ê³¼ëª©:', this.cart.items.map(item => item.product.id));
    
    const allEnrolled = this.cart.items.every(item => this.enrolledCourseIds.has(item.product.id));
    console.log('  - ê²°ê³¼:', allEnrolled ? 'âœ… ëª¨ë‘ ìˆ˜ê°•ì‹ ì²­ë¨' : 'âŒ ë¯¸ì‹ ì²­ ìˆìŒ');
    
