export type WidgetType = 'text' | 'image' | 'embed' | 'video' | 'button' | 'spacer';

export interface IWidget {
  widgetType: WidgetType;
  properties: any;
}

export interface IColumn {
  widget?: IWidget;
}

export interface IRow {
  columns: IColumn[];
}

export interface ISection {
  rows: IRow[];
}

export interface IWysiwyg {
  sections: ISection[];
}

// ===== Text Widget =====
export interface ITextWidgetProperties {
  content: string;
  style?: {
    fontSize?: { enabled: boolean; value: number; unit: string; cssProperty: string };
    fontWeight?: { enabled: boolean; value: string; cssProperty: string };
    color?: { enabled: boolean; value: string; cssProperty: string };
    textAlign?: { enabled: boolean; value: string; cssProperty: string };
    lineHeight?: { enabled: boolean; value: number; unit: string; cssProperty: string };
  };
}

// ===== Image Widget =====
export interface IImageWidgetProperties {
  source?: {
    src: string;
    alt: string;
  };
  description?: string;
  style?: {
    width?: { enabled: boolean; value: number; unit: string; cssProperty: string };
    height?: { enabled: boolean; value: number; unit: string; cssProperty: string };
    rounded?: { enabled: boolean; value: number; unit: string; cssProperty: string };
    circled?: { enabled: boolean; value: string; cssProperty: string };
    blackWhite?: { enabled: boolean; value: string; cssProperty: string };
  };
  action?: {
    action: 'link' | 'modal';
    value: string;
  };
}

// ===== Embed Widget =====
export interface IEmbedWidgetProperties {
  src?: string;
  title?: string;
  style?: {
    width?: { enabled: boolean; value: number; unit: string; cssProperty: string };
    height?: { enabled: boolean; value: number; unit: string; cssProperty: string };
  };
}

// ===== Widget Factory =====
export class WidgetFactory {
  static createTextWidget(content: string = 'í…ìŠ¤íŠ¸ë¥¼ ì…ë ¥í•˜ì„¸ìš”'): IWidget {
    return {
      widgetType: 'text',
      properties: {
        content,
        style: {
          fontSize: { enabled: false, value: 16, unit: 'px', cssProperty: 'font-size' },
          fontWeight: { enabled: false, value: 'normal', cssProperty: 'font-weight' },
          color: { enabled: false, value: '#000000', cssProperty: 'color' },
          textAlign: { enabled: false, value: 'left', cssProperty: 'text-align' },
          lineHeight: { enabled: false, value: 1.5, unit: '', cssProperty: 'line-height' }
        }
      } as ITextWidgetProperties
    };
  }

  static createImageWidget(src: string = '', alt: string = 'ì´ë¯¸ì§€'): IWidget {
    return {
      widgetType: 'image',
      properties: {
        source: { src, alt },
        description: '',
        style: {
          width: { enabled: true, value: 100, unit: '%', cssProperty: 'width' },
          height: { enabled: false, value: 400, unit: 'px', cssProperty: 'height' },
          rounded: { enabled: false, value: 0, unit: 'px', cssProperty: 'border-radius' },
          circled: { enabled: false, value: '50%', cssProperty: 'border-radius' },
          blackWhite: { enabled: false, value: 'grayscale(100%)', cssProperty: 'filter' }
        }
      } as IImageWidgetProperties
    };
  }

  static createEmbedWidget(src: string = '', title: string = 'Embedded Content'): IWidget {
    return {
      widgetType: 'embed',
      properties: {
        src,
        title,
        style: {
          width: { enabled: true, value: 100, unit: '%', cssProperty: 'width' },
          height: { enabled: true, value: 400, unit: 'px', cssProperty: 'height' }
        }
      } as IEmbedWidgetProperties
    };
  }

  static createEmptySection(): ISection {
    return {
      rows: []
    };
  }

  static createEmptyRow(): IRow {
    return {
      columns: []
    };
  }

  static createColumnWithWidget(widget: IWidget): IColumn {
    return { widget };
  }
}
