import { Injectable } from '@angular/core';
import { HttpInterceptor, HttpRequest, HttpHandler, HttpEvent, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { StudentAuthService } from '../services/student-auth.service';
import { TeacherAuthService } from '../services/teacher-auth.service';
import { AdminAuthService } from '../services/admin-auth.service';
import { AuthModeService } from '../services/auth-mode.service';

/**
 * êµìœ¡ ëª¨ë“ˆ HTTP ì¸í„°ì…‰í„°
 * - ëª¨ë“  ìš”ì²­ì— í† í° ì¶”ê°€
 * - 401/403 ì—ëŸ¬ ì‹œ ë¡œê·¸ì•„ì›ƒ ì²˜ë¦¬
 */
@Injectable()
export class EducationAuthInterceptor implements HttpInterceptor {
  constructor(
    private studentAuth: StudentAuthService,
    private teacherAuth: TeacherAuthService,
    private adminAuth: AdminAuthService,
    private authMode: AuthModeService
  ) {}

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    // ì¸ì¦ í† í°ì´ í•„ìš” ì—†ëŠ” ì—”ë“œí¬ì¸íŠ¸ë“¤
    const excludeUrls = [
      '/education/students/login',
      '/education/students/register',
      '/education/teachers/login',
      '/education/teachers/register',
      '/education/admins/login',
      '/student/login',
      '/teacher/login',
      '/admin/login'
    ];

    const isExcludedUrl = excludeUrls.some(url => req.url.includes(url));

    // í† í°ì„ ìš”ì²­ì— ì¶”ê°€
    let authReq = req;
    if (!isExcludedUrl) {
      const currentMode = this.authMode.getCurrentMode();
      let token: string | null = null;

      switch (currentMode) {
        case 'STUDENT':
          token = this.studentAuth.getToken();
          break;
        case 'TEACHER':
          token = this.teacherAuth.getToken();
          break;
        case 'ADMIN':
          token = this.adminAuth.getToken();
          break;
      }

      if (token) {
        authReq = req.clone({
          setHeaders: {
            Authorization: `Bearer ${token}`
          }
        });
      }
    }

    return next.handle(authReq).pipe(
      catchError((error: HttpErrorResponse) => {
        // 401 (Unauthorized) ë˜ëŠ” 403 (Forbidden) ì—ëŸ¬ ë°œìƒ ì‹œ
        if ((error.status === 401 || error.status === 403) && !isExcludedUrl) {
          console.warn(
            `[EducationAuthInterceptor] ${error.status} ì—ëŸ¬ ë°œìƒ - ë¡œê·¸ì•„ì›ƒ ì²˜ë¦¬`,
            error.url
          );

          // í˜„ì¬ ëª¨ë“œì— ë”°ë¼ ë¡œê·¸ì•„ì›ƒ
          const currentMode = this.authMode.getCurrentMode();
          switch (currentMode) {
            case 'STUDENT':
              this.studentAuth.logout();
              break;
            case 'TEACHER':
              this.teacherAuth.logout();
              break;
            case 'ADMIN':
              this.adminAuth.logout();
              break;
          }
        }

        return throwError(() => error);
      })
    );
  }
}
