  submitSurvey(): void {
    console.log('Submit clicked');
    console.log('Form valid:', this.surveyForm.valid);
    console.log('Form value:', this.surveyForm.value);
    console.log('Selected mission:', this.selectedMission);
    
    if (!this.selectedMission?.surveys?.[0]) {
      console.error('ì„¤ë¬¸ì´ ì—†ìŠµë‹ˆë‹¤');
      return;
    }

    // í¼ ë°ì´í„°ì—ì„œ ë¹ˆ ê°’ ì²´í¬
    const answers = Object.keys(this.surveyForm.value).map(key => ({
      questionId: key,
      value: this.surveyForm.value[key]
    }));

    // í•„ìˆ˜ í•„ë“œ í™•ì¸ (required: trueì¸ í•„ë“œë§Œ ê²€ì¦)
    const survey = this.selectedMission.surveys[0];
    const requiredQuestions = survey.questions.filter(q => q.required === true);
    const unansweredRequired = requiredQuestions.filter(q => 
      !answers.find(ans => ans.questionId === q.id && ans.value)
    );

    if (unansweredRequired.length > 0) {
      console.error('í•„ìˆ˜ í•„ë“œê°€ ì…ë ¥ë˜ì§€ ì•Šì•˜ìŠµë‹ˆë‹¤:', unansweredRequired);
      this.snackBar.open('ëª¨ë“  í•„ìˆ˜ í•­ëª©ì„ ì…ë ¥í•´ì£¼ì„¸ìš”.', 'ë‹«ê¸°', { duration: 3000 });
      return;
    }

    this.submitting = true;
    console.log('Submitting answers:', answers);
    console.log('Current studentId:', this.currentStudentId);

    this.missionApiService.submitSurveyResponse$(survey.id, answers, this.currentStudentId || undefined)
      .subscribe({
        next: () => {
          console.log('ì„¤ë¬¸ ì œì¶œ ì„±ê³µ');
          this.snackBar.open('ì„¤ë¬¸ì„ ì„±ê³µì ìœ¼ë¡œ ì œì¶œí–ˆìŠµë‹ˆë‹¤!', 'ë‹«ê¸°', { duration: 3000 });
          this.submitting = false;
          
          // ë¯¸ì…˜ ìƒíƒœë¥¼ COMPLETEDë¡œ ì—…ë°ì´íŠ¸
          if (this.selectedMission) {
            this.selectedMission.status = 'COMPLETED';
            this.selectedMission.isSubmitted = true;  // ì œì¶œë¨ í”Œë˜ê·¸ ì¶”ê°€
            // ë¯¸ì…˜ ëª©ë¡ì—ì„œë„ ìƒíƒœ ì—…ë°ì´íŠ¸
            const missionIndex = this.missions.findIndex(m => m.id === this.selectedMission?.id);
            if (missionIndex >= 0) {
              this.missions[missionIndex].status = 'COMPLETED';
              this.missions[missionIndex].isSubmitted = true;  // ì œì¶œë¨ í”Œë˜ê·¸ ì¶”ê°€
            }
          }
          
          // ì œì¶œ ê²°ê³¼ ì €ì¥ (ì„¤ë¬¸ëª…, ì œì¶œ ì‹œê°„, ë‹µë³€)
          this.submissionResult = {
            surveyTitle: survey.title,
            surveyDescription: survey.description,
            submittedAt: new Date().toLocaleString('ko-KR'),
            answers: answers.map(ans => {
              const question = survey.questions.find(q => q.id === ans.questionId);
              return {
                question: question?.question || 'ì§ˆë¬¸',
                value: ans.value,
                type: question?.type
              };
            })
          };
          
          // ì œì¶œ ê²°ê³¼ í˜ì´ì§€ í‘œì‹œ
          this.showResultPage = true;
          this.showSubmissionListPage = false;
        },
        error: (err) => {
          console.error('ì„¤ë¬¸ ì œì¶œ ì‹¤íŒ¨:', err);
          const errorMessage = err?.error?.message || err?.message || 'ì œì¶œ ì¤‘ ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤.';
          this.snackBar.open(errorMessage, 'ë‹«ê¸°', { duration: 5000 });
          this.submitting = false;
        }
      });
  }

