  hasCompletedAllMissions(): boolean {
    if (this.missions.length === 0) return false;
    
    // ì ê¸ˆì´ ì•ˆ ë˜ê³  ì™„ë£Œë˜ì§€ ì•Šì€ ë¯¸ì…˜ì´ í•˜ë‚˜ë¼ë„ ìˆìœ¼ë©´ false
    const availableMissions = this.missions.filter(m => 
      m.status !== 'COMPLETED' && 
      !m.isSubmitted && 
      !m.isLocked
    );
    
    return availableMissions.length === 0;
  }

