import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { JwtHelperService } from '@auth0/angular-jwt';
import { Router } from '@angular/router';
import { Observable, map } from 'rxjs';
import { BaseAuthService } from './base-auth.service';
import { IAdmin, AuthResponse } from '../interfaces/auth.interface';
import { environment } from '../config/environment';

export interface AdminSignupDto {
  id: string;
  password: string;
  name: string;
  email: string;
  phone: string;
  role?: 'ADMIN' | 'SUPER_ADMIN';
}

@Injectable({
  providedIn: 'root'
})
export class AdminAuthService extends BaseAuthService<IAdmin> {
  protected storageTokenKey = 'ez_edu_admin_token';
  // Admin = User(ê´€ë¦¬ì) - admin/login ì—”ë“œí¬ì¸íŠ¸ ì‚¬ìš© (roleId >= 300 ì²´í¬)
  protected loginEndpoint = environment.baseSyncUrl + '/users/admin/login';
  protected loginRedirectPath = '/education/admin/login';

  constructor(
    protected override http: HttpClient,
    protected override jwtHelper: JwtHelperService,
    protected override router: Router
  ) {
    super(http, jwtHelper, router);
    this.initUser();
  }

  register(signupData: AdminSignupDto): Observable<IAdmin> {
    const signupEndpoint = environment.baseSyncUrl + '/admin/signup';
    return this.http.post<AuthResponse<IAdmin>>(signupEndpoint, signupData).pipe(
      map(({ user, accessToken }) => {
        this.setToken(accessToken);
        return this.mapUser(user);
      })
    );
  }

  hasPermission(permission: string): boolean {
    const user = this.getCurrentUser();
    if (!user) return false;
    
    // ìŠˆí¼ ê´€ë¦¬ìëŠ” ëª¨ë“  ê¶Œí•œ ë³´ìœ 
    if (user.role === 'SUPER_ADMIN') return true;
    
    // ì¼ë°˜ ê´€ë¦¬ìëŠ” permissions ë°°ì—´ í™•ì¸
    return user.permissions?.includes(permission) ?? false;
  }

  isSuperAdmin(): boolean {
    const user = this.getCurrentUser();
    return user?.role === 'SUPER_ADMIN' || false;
  }

  getAdminList(): Observable<IAdmin[]> {
    const endpoint = environment.baseSyncUrl + '/admin/list';
    return this.http.get<{ admins: IAdmin[] }>(endpoint).pipe(
      map(({ admins }) => admins)
    );
  }

  updateAdminPermissions(adminId: string, permissions: string[]): Observable<IAdmin> {
    const endpoint = environment.baseSyncUrl + `/admin/${adminId}/permissions`;
    return this.http.put<{ user: IAdmin }>(endpoint, { permissions }).pipe(
      map(({ user }) => this.mapUser(user))
    );
  }

  deleteAdmin(adminId: string): Observable<void> {
    const endpoint = environment.baseSyncUrl + `/admin/${adminId}`;
    return this.http.delete<void>(endpoint);
  }
}