  /**
   * êµìœ¡ ëª¨ë“ˆ ë°±ì—”ë“œ ì‘ë‹µ ë§¤í•‘
   */
  private mapEducationSchoolResponse(school: any): ISchool {
    return {
      id: school.id,
      name: school.name,
      description: school.description || school.shortDescription,
      ownerId: school.ownerId,
      businessRegistrationNumber: school.businessRegistrationNumber || '',
      zipcode: '',
      address: school.address || '',
      addressDetail: '',
      studentServiceNumber: school.contactPhone || school.phone || '',
      eCommerceRegistrationNumber: '',
      email: school.contactEmail || school.email || '',
      productCount: school.courseCount || 0,
      logoImageMeta: (school.logoImageId || school.logoId) ? { 
        id: school.logoImageId || school.logoId, 
        url: `${environment.baseSyncUrl}/uploads/${school.logoImageId || school.logoId}` 
      } : (school.logoImage ? {
        id: school.logoImage.id,
        url: `${environment.baseSyncUrl}/uploads/${school.logoImage.id}`
      } : undefined),
      bannerImageMeta: (school.bannerImageId || school.bannerId) ? {
        id: school.bannerImageId || school.bannerId,
        url: `${environment.baseSyncUrl}/uploads/${school.bannerImageId || school.bannerId}`
      } : (school.bannerImage ? {
        id: school.bannerImage.id,
        url: `${environment.baseSyncUrl}/uploads/${school.bannerImage.id}`
      } : undefined),
      createdAt: new Date(school.createdAt),
      // êµìœ¡ ëª¨ë“ˆ ì „ìš© í•„ë“œ
      type: school.type,
      status: school.status,
      isRecommended: school.isRecommended,
      recommendOrder: school.recommendOrder,
      isVerified: school.isVerified,
      studentCount: school.studentCount,
      courseCount: school.courseCount || school.productCount || 0,
      rating: school.rating,
      reviewCount: school.reviewCount,
      categories: school.categories,
      website: school.website,
    } as ISchool;
  }

