  /**
   * ë‹¨ì¼ ì´ë¯¸ì§€ ì—…ë¡œë“œ
   */
  uploadImage$(file: File, userId: string): Observable<{ id: string; url: string }> {
    this.debugLogger.info('ì´ë¯¸ì§€ì—…ë¡œë“œ', `ğŸ“¤ ì´ë¯¸ì§€ ì—…ë¡œë“œ: ${file.name} (${(file.size / 1024).toFixed(2)}KB)`);
    
    const formData = new FormData();
    formData.append('file', file);
    
    return this.http.post<{ id: string; url: string }>(
      environment.baseSyncUrl + '/upload/image',
      formData,
      {
        headers: {
          'x-user-id': userId
        }
      }
    ).pipe(
      tap(result => {
        this.debugLogger.success('ì´ë¯¸ì§€ì—…ë¡œë“œ', `âœ… ì—…ë¡œë“œ ì„±ê³µ: ${result.url}`);
      })
    );
  }

  /**
   * ë‹¤ì¤‘ ì´ë¯¸ì§€ ì—…ë¡œë“œ
   */
  uploadImages$(files: File[], userId: string): Observable<Array<{ id: string; url: string }>> {
    const formData = new FormData();
    files.forEach(file => {
      formData.append('files', file);
    });
    
    return this.http.post<Array<{ id: string; url: string }>>(
      environment.baseSyncUrl + '/upload/images',
      formData,
      {
        headers: {
          'x-user-id': userId
        }
      }
    );
  }

  // Map backend store response to UI model
  private mapStoreResponse(store: StoreResponseDto): ISchool {
    return {
      ...store,
      logoImageMeta: store.logoImageMeta ? { 
        ...store.logoImageMeta, 
        url: environment.baseSyncUrl + store.logoImageMeta.url 
      } : undefined,
      productCount: store.productCount,
      createdAt: new Date(store.createdAt),
    } as ISchool;
  }

